import Vue from 'vue'

// 小组件 loading
Vue.directive('load-sync', {
  // 当被绑定的元素插入到 DOM 中时……
  inserted: function (el) {
    el.className = el.className + ' load-sync-box'
    const span = document.createElement('span')
    const text = document.createElement('span')
    const icon = document.createElement('i')
    text.innerHTML = '数据加载中'
    span.className = 'load-sync-icon'
    icon.className = 'el-icon-loading'
    span.style.display = 'none'
    span.appendChild(text)
    span.appendChild(icon)
    el.appendChild(span)

  },
  update: function (el, {
    value
  }) {
    const span = el.querySelector('.load-sync-icon')
    if (value) {
      span.style.display = 'block'
    } else {
      span.style.display = 'none'
    }
  }
})