<template>
  <div
    ref='panel'
    v-loading='loading'
    class="dynamic-container"
    :class='{...commonClassName}'
    :style='{...componentStyle, ...panelStyle, whiteSpace}'
  >
    <layout-placeholder
      :elementId="elementId"
      :elementType="elementType"
      v-if="
        (!children.length || children.every((child) => getElementPropsById(child.elementId).isVisible === false)) &&
          isEditMode
      "
    ></layout-placeholder>
    <component
      v-if='(isEditMode || isStatic) && children[0]'
      :is='getElement(children[0].elementType, children[0].elementVersion)'
      :ref="children[0].elementId"
      :data-zone="`${children[0].elementId}_${children[0].elementType}`"
      v-bind="children[0]"
      :id="children[0].elementId"
    ></component>
    <template v-if='dataList.length && !isEditMode'>
      <component
        v-for="(child, index) in visibleChildren"
        :key="child.elementId"
        :is='getElement(child.elementType, child.elementVersion)'
        :ref="child.elementId"
        :data-zone="`${child.elementId}_${child.elementType}`"
        v-bind="{...child, slotProps: dataList[index]}"
        :id="child.elementId"
        @click.native.capture="ROW_CLICK(index, $event)"
      ></component>
    </template>
  </div>
</template>

<script>
  import common from '@/template/mixins/common'
  import {
    UPDATE_DYNAMIC_PROPS,
    DELETE_LAYOUT_DATA,
    UPDATE_LAYOUT_DATA,
    UPDATE_EVENTS_DATA,
    DATA_LOAD_SUCCESS,
    UPDATE_FUNCTIONS_DATA,
    UPDATE_JS_DATA,
  } from '@/config/storeKey'
  import { RESET, ROW_CLICK } from '@/config/eventName'
  import request from '@/request/request'
  import _ from 'lodash'
  import { isValidJsonString } from '@/utils/validateUtils'
  import { getNodeByElementId, treeUtil, getRandomHexadecimal } from '@/utils/tree-utils'
  import { prefixZero } from '@/utils'
  import { FUNCTION_FLAG, JS_FLAG } from '@/config'
  import { globalEmit } from '@/renderer/tools/eventBus'
  import { EXECUTE_VALUE, TRIGGER } from '@/config/eventKey'
  import trigger from '@/renderer/tools/trigger'
  import { pageInject } from '@/renderer/tools/pageStore'
  export default {
    mixins: [common],
    variables: {
      $dataList(n) {
        this.dataList = n || []
        this.handleGenerateChildren()
      },
    },
    inject: ['dndOptions', ...pageInject],
    data() {
      return {
        panelStyle: undefined,
        panelRect: undefined,
        currentRow: {},
        loading: false,
        firstChild: null
      }
    },
    computed: {
      isStatic() {
        return !this.staticData && !this.apiRequest && !this.sqlRequest
      }
    },
    created() {
      this.dataList = this.isEditMode ? [{}] : []
      if (this.staticData && !this.isEditMode) {
        setTimeout(() => {
          this.handleSuccess(this.staticData)
        })
      }
      if (this.autoFetch && (this.sqlRequest || this.apiRequest)) {
        this[RESET]()
      }
    },
    methods: {
      handleFetch({ logId }) {

        const log = {
          pid: logId,
          id: trigger.getUnionKey(),
          tag: '数据集',
          target: this,
          children: []
        }

        if (this.apiRequest) {
          const params = this.getApiRequestParams(this.apiRequest)
          if (!params.url) {
            return
          }
          // 记录日志
          const unionId = trigger.getUnionKey()
          log.children.push(trigger.formatLinkMsg({
            id: unionId,
            type: '调用API',
            tagList: {
              method: params.method,
              url: params.url,
              params: params.params,
              data: params.data,
              headers: params.headers,
            },
          }))
          trigger.recordLog(log)
          // 记录日志

          this.loading = true
          request({
            method: params.method,
            url: params.url,
            params: params.params,
            data: params.data,
            headers: params.headers,
          })
            .then((res) => {
              this.handleSuccess(res.data)
              // 记录日志
              trigger.recordLog({ pid: unionId, children: [trigger.formatLinkMsg({ successText: '成功回调', tagList: { response: res } })] })
            })
            .catch((err) => {
              this.loading = false
              // 记录日志
              trigger.recordLog({ pid: unionId, children: [trigger.formatLinkMsg({ errorText: '失败回调', tagList: { err } })] })
            })
          return
        }
        if (this.sqlRequest) {
          const params = this.getSqlParams(this.sqlRequest)
          // 记录日志
          const unionId = trigger.getUnionKey()
          log.children.push(trigger.formatLinkMsg({
            id: unionId,
            type: '调用SQL',
            tagList: {
              method: params.method,
              url: params.url,
              params: params.params,
              data: params.data,
              headers: params.headers,
            },
          }))
          trigger.recordLog(log)
          // 记录日志
          this.loading = true
          this.$http({
            url: params.url,
            data: trigger.formatSQLParams(params.data, unionId),
          })
            .then((res) => {
              this.handleSuccess(res)
              // 记录日志
              trigger.recordSqlResponse(unionId)
              trigger.recordLog({ pid: unionId, children: [trigger.formatLinkMsg({ successText: '成功回调', tagList: { response: res } })] })
            })
            .catch((err) => {
              this.loading = false
              // 记录日志
              trigger.recordSqlResponse(unionId)
              trigger.recordLog({ pid: unionId, children: [trigger.formatLinkMsg({ errorText: '失败回调', tagList: { err } })] })
            })
          return
        }
      },
      // 请求成功
      handleSuccess(res) {
        let response = {
          response: res
        }
        const dataListPath = this.dataVariables.find(item => item.key === 'dataList').path
        if (!dataListPath.length) {
          response = res
        }
        let dataList = dataListPath.length ? _.get(response, dataListPath) : response
        if (!Array.isArray(dataList)) {
          if (typeof dataList === 'string' && isValidJsonString(dataList)) {
            try {
              const res = JSON.parse(dataList.trim())
              dataList = Array.isArray(res) ? res : [res]
            } catch (e) {
              dataList = []
            }
          } else {
            dataList = [dataList]
          }
        }
        // if (!_.isObject(dataList[0])) {
        //   return
        // }
        this.dataList = dataList
        this.handleGenerateChildren()
      },
      async handleGenerateChildren() {
        if (!this.firstChild) {
          this.firstChild = _.cloneDeep(this.children[0])
        }
        const firstChild = this.firstChild
        const eventsData = this.getStore().eventsData
        const functionsData = this.getStore().functionsData
        const jsData = this.getStore().jsData
        const dataListPath = this.dataVariables.find(item => item.key === 'dataList').path
        const dynamicPropsList = []
        const eventsDataList = []
        const newNodeList = []
        const newFunctionsData = []
        const newJsData = []
        this.dataList.forEach((item, index) => {
          const newEventsList = []
          const newFunctionList = []
          const newJsList = []
          const diffIds = []
          const diffFuncIds = []
          const diffJsIds = []
          const currentNode = getNodeByElementId(this.getStore().layoutData, firstChild.elementId)
          const newNode = _.cloneDeep(currentNode)
          treeUtil.forEach([newNode], (child) => {
            const newProps = {}
            const newEvents = {}
            const oldId = child.elementId
            const subId = prefixZero(index + 1, 6) // 保证已存在的组件不被重新替换
            const newId = `${oldId}-${subId}`
            const {children, ...childProps} = this.getElementPropsById(oldId)
            child.elementId = newId
            newProps[newId] = _.cloneDeep({
              ...childProps,
              elementId: newId,
              isVisible: oldId === firstChild.elementId ? true : childProps.isVisible,
              layout: childProps.layout ? {...childProps.layout, i: newId} : undefined
            })
            newEvents[newId] = _.cloneDeep(eventsData[oldId])
            const currentVariables = this.widgetVariables.filter(v => {
              const elementId = v.key.split('.$')[0]
              return elementId === oldId
            })
            currentVariables.forEach(v => {
              const { path, key } = v
              const deepPath = path.slice(dataListPath.length + 1)
              const value = _.get(item, deepPath.length ? deepPath : _.last(path))
              const elementId = key.split('.$')[0]
              const variableKey = key.replace(`${elementId}.$`, '')
              _.set(newProps[newId], variableKey, value)
            })
            dynamicPropsList.push(newProps)
            newEventsList.push(newEvents)
            diffIds.push({newId, oldId})

            // 处理函数和js脚本和高级变量
            const eventsDataString = JSON.stringify(eventsData[oldId] || {})
            const dynamicPropsString = JSON.stringify(childProps || {})
            const functionExp = new RegExp(`${FUNCTION_FLAG}.{12}`, 'g')
            const jsExp = new RegExp(`${JS_FLAG}.{12}`, 'g')
            const usedFunctionIds =
              eventsDataString.concat(dynamicPropsString).match(functionExp) || []
            const usedFunctionList = functionsData.filter((item) =>
              usedFunctionIds.includes(`${FUNCTION_FLAG}${item.id}`)
            )
            const functionsDataString = JSON.stringify(usedFunctionList)
            const usedJsIds = functionsDataString.match(jsExp) || []
            const usedJsList = jsData.filter((item) =>
              usedJsIds.includes(`${JS_FLAG}${item.id}`)
            )

            usedFunctionList.forEach(func => {
              const currentFunction = functionsData.find(item => item.id === func.id)
              const oid = func.id
              const nid = getRandomHexadecimal(12)
              const oldFuncId = `${FUNCTION_FLAG}${func.id}`
              const newFuncId = `${FUNCTION_FLAG}${nid}`
              newFunctionList.push({..._.cloneDeep(currentFunction), id: nid})
              diffFuncIds.push({newFuncId, oldFuncId, nid, oid})
            })

            usedJsList.forEach(js => {
              const currentJs = jsData.find(item => item.id === js.id)
              const nid = getRandomHexadecimal(12)
              const oldJsId = `${JS_FLAG}${js.id}`
              const newJsId = `${JS_FLAG}${nid}`
              newJsList.push({...currentJs, id: nid})
              diffJsIds.push({newJsId, oldJsId})
            })
          })
          newEventsList.forEach(events => {
            Object.keys(events).forEach(eId => {
              let preEventString = JSON.stringify(events[eId])
              if (preEventString) {
                diffIds.forEach(({newId, oldId}) => {
                  preEventString = preEventString.replaceAll(oldId, newId)
                })
                diffFuncIds.forEach(({newFuncId, oldFuncId, nid, oid}) => {
                  preEventString = preEventString.replaceAll(oldFuncId, newFuncId)
                  preEventString = preEventString.replaceAll(oid, nid)
                })
                events[eId] = JSON.parse(preEventString)
              }
            })
          })

          dynamicPropsList.forEach(props => {
            Object.keys(props).forEach(pId => {
              let prePropString = JSON.stringify(props[pId])
              if (prePropString) {
                diffIds.forEach(({newId, oldId}) => {
                  prePropString = prePropString.replaceAll(oldId, newId)
                })
                diffFuncIds.forEach(({newFuncId, oldFuncId}) => {
                  prePropString = prePropString.replaceAll(oldFuncId, newFuncId)
                })
                props[pId] = JSON.parse(prePropString)
              }
            })
          })

          newFunctionList.forEach(func => {
            let paramsDataString = JSON.stringify(func.paramsData)
            if (paramsDataString) {
              diffIds.forEach(({newId, oldId}) => {
                paramsDataString = paramsDataString.replaceAll(oldId, newId)
              })
              diffJsIds.forEach(({newJsId, oldJsId}) => {
                paramsDataString = paramsDataString.replaceAll(oldJsId, newJsId)
              })
            }
            func.paramsData = JSON.parse(paramsDataString)
            newFunctionsData.push(func)
          })

          newJsList.forEach(js => {
            let jsString = JSON.stringify(js)
            if (jsString) {
              diffIds.forEach(({newId, oldId}) => {
                jsString = jsString.replaceAll(oldId, newId)
              })
            }
            newJsData.push(JSON.parse(jsString))
          })

          const diffEvents = newEventsList.filter(eventData => Object.keys(eventData).length)
          eventsDataList.push(...diffEvents)
          newNodeList.push(newNode)
        })
        const newIdList = newNodeList.map(item => item.elementId)
        const oldIdList = this.children.map(item => item.elementId)
        let addNodeList = []
        if (newNodeList.length < (this.children.length - 1)) {
          const deleteIds = oldIdList.filter(elementId => elementId !== firstChild.elementId && !newIdList.includes(elementId))
          await this._storeDispatch(DELETE_LAYOUT_DATA, { elementIdList: deleteIds })
          this.children = this.children.filter(item => !deleteIds.includes(item.elementId))
        } else if (newNodeList.length > (this.children.length - 1)) {
          addNodeList = newNodeList.filter(item => !oldIdList.includes(item.elementId))
        }

        const firstProps = {}
        firstProps[firstChild.elementId] = {
          elementId: firstChild.elementId,
          isVisible: false
        }
        dynamicPropsList.push(firstProps)
        this._storeCommit(UPDATE_DYNAMIC_PROPS, { dynamicPropsList })
        this._storeCommit(UPDATE_EVENTS_DATA, { eventsDataList })
        this._storeCommit(UPDATE_FUNCTIONS_DATA, { functionsData: [...functionsData, ...newFunctionsData] })
        this._storeCommit(UPDATE_JS_DATA, { jsData: [...jsData, ...newJsData] })
        if (addNodeList.length) {
          this.children = [...this.children, ...addNodeList]
          this._storeCommit(UPDATE_LAYOUT_DATA, {elementId: this.elementId, layout: {children: this.children}})
        }
        if (!this.isEditMode) {
          this.updateChildrenStaticStyle()
        }
        this.$nextTick(() => {
          this.showIds = this.children.slice(1).map(item => item.elementId)
          dynamicPropsList.forEach(props => {
            Object.keys(props).forEach(elementId => {
              const currentNode = getNodeByElementId(this.children, elementId)
              const showIds = currentNode.children.filter(item => this.getElementPropsById(item.elementId).isVisible !== false).map(item => item.elementId)
              globalEmit.call(this, TRIGGER, { elementId, data: {type: EXECUTE_VALUE, data: {...props[elementId], showIds }} })
            })
          })
          this.loading = false
          this._emitEvent(DATA_LOAD_SUCCESS)
        })
      },
      [RESET]({ logId } = {}) {
        if (this.isEditMode) {
          return
        }
        this.handleFetch({ logId })
      },
      [ROW_CLICK](index, e) {
        this.currentRow = this.dataList[index]
        this._emitEvent(ROW_CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      }
    },
  }
</script>

<style lang="less" scoped>
  .dynamic-container::v-deep {
    box-sizing: border-box;
    .el-loading-spinner {
      margin-top: 0!important;
    }
    &.is-edit-mode {
      outline: 1px dashed #eeeeee;
      padding: 1px;
    }
  }
</style>
