<template>
  <div
    v-loading="!chartInstance && fetchLoading"
    ref="chart"
    class="chart-content"
    :class="commonClassName"
  ></div>
</template>

<script>
import { CLICK, RESET, DATA_ZOOM } from '@/config/eventName'
import common from '@/template/mixins/common'
import chart from '@/template/mixins/chart'

import { debounce } from '@/utils'

export default {
  mixins: [common, chart],
  variables: {
    $chartData(newV) {
      this.staticData = newV
      this[RESET](false)
    },
  },
  computed: {},
  watch: {},
  data() {
    return {}
  },
  mounted() {},
  created() {},
  methods: {
    getOption() {
      const option = this.getBaseOption()
      // 如果数据线第一项是数值的话, x坐标类型变数字, 为适应四象限的模式
      try {
        const {
          dimensions: [xKey],
          source: [firstDataItem],
        } = this.dataset
        if (typeof firstDataItem[xKey] === 'number') {
          option.xAxis.type = 'value'
        }
      } catch (err) {
        console.log(err)
      }
      // 简洁模式要坐标轴
      if(this.cleanMode) {
        option.xAxis.axisLine = { show: true }
        option.yAxis.axisLine = { show: true }
      }
      return option
    },
    // 初始化监听事件
    initEvents() {
      //点击
      this.chartInstance.on('click', (e) => {
        this.dotData = e.data
        this._emitEvent(CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.event.event.stopPropagation()
        }
      })
      // 数据缩放
      this.chartInstance.on(
        'dataZoom',
        debounce((e) => {
          const { start, end } = e
          const { xAxisData: data } = this
          const result = this.getDataZoomRange({ start, end, data })
          this.dataZoomRange = result
          this._emitEvent(DATA_ZOOM)
        })
      )
    },
    // 获取散点图类型
    getTypeSetting(item, index, symbolSetting) {
      const {
        dimensions: [xKey],
        source,
      } = this.dataset

      const { type } = this
      const settting = {}
      // 气泡图模式时
      if (type === 'bubble') {
        const presetSize = symbolSetting.symbolSize || 5
        const key = item.value || item.key
        // 平均值
        const average =
          this.dataset.source.reduce((total, item) => {
            return (total += Math.abs(item[key]))
          }, 0) / this.dataset.source.length
        settting.symbolSize = function ([, val]) {
          // 动态增加的直径为偏离平均值的百分比
          const extraSize = ((Math.abs(val) - average) / average) * presetSize
          // 防止点太小, 最小值是一半尺寸
          return 2 * presetSize + extraSize
        }
      }
      // 图例名
      settting.name = item.value || item.key
      // 数据
      settting.data = source.map(opt => [opt[xKey], opt[item.key]])
      return settting
    },
  },
}
</script>

<style lang="less" scoped>
@import '~@/styles/index.less';

.chart-content {
  width: 100%;
  height: 400px;
}
</style>
