<template>
  <div
    class="page-container-widget"
    :class="commonClassName"
    :style="componentStyle"
  >
    <div v-if="isEditMode" class="page-container-mask"></div>
    <template>
      <el-tag v-if="!pageId" type="danger">请选择页面</el-tag>
      <el-tag v-else-if="isEditMode && isMini" type="success">{{
        componentName
      }}</el-tag>
      <template v-else>
        <div v-if="componentLoading">
          页面加载中 <i class="el-icon-loading"></i>
        </div>
        <el-tag v-else-if="!componentLoading && componentError" type="danger"
          >{{ componentErrorMsg || componentName + '异常' }}</el-tag
        >
        <component
          v-if="compoentRender && currentElement"
          :is="currentElement"
          :ref="storeData.layoutData.elementId"
          :key="storeData.layoutData.elementId"
          v-bind="storeData.layoutData"
          :id="storeData.layoutData.elementId"
        ></component>
      </template>
    </template>
  </div>
</template>

<script>
import _ from 'lodash'
import { RECEIVE_PROPS, RECEIVE_VAR } from '@/config/eventKey'
import trigger from '@/renderer/tools/trigger'
import pageStore from '@/renderer/tools/pageStore'
import { UPDATE_PAGE_CONFIG } from '@/config/storeKey'
import common from '@/template/mixins/common'
import { getComponent } from '@/renderer/tools/config'
import { getSharePageParams } from '@/utils'
export default {
  inject: ['isPageShare'],
  mixins: [common, pageStore],
  variables: {},
  data() {
    return {
      componentLoading: false,
      componentError: false,
      componentErrorMsg: '',
      compoentRender: false
    }
  },
  computed: {
    currentElement() {
      const { layoutData } = this.storeData
      if (!layoutData?.elementType) {
        return ''
      }
      const { elementType, elementVersion } = layoutData
      return getComponent(elementType, elementVersion)
    },
    componentName() {
      return `页面容器: ${this.pageInfo?.elementName}`
    },
    pageInfo() {
      return this.$store.state.CODE.lowCodeStore.pageData.find(item => item.elementId === this.pageId)
    }
  },
  mounted() {},
  methods: {
    renderPage() {
      const { pageId, componentName, isEditMode, isMini } = this
      if (isEditMode && isMini) {
        return
      }
      this.componentErrorMsg = ''
      // 配置不完整报错
      if (!pageId) {
        this.componentError = true
        trigger.recordBug({
          message: this.componentName + '配置不完整',
          target: this,
        })
      } else if(this.findParentsById(this, this.pageId)) {
        this.componentErrorMsg = this.componentName + '已存在, 不能无限嵌套'
        this.componentError = true
        this.componentLoading = false
        trigger.recordBug({
          message: this.componentErrorMsg,
          target: this,
        })
      } else {
        this.componentLoading = true
        let params
        // 页面分享
        if(this.isPageShare) {
          const { appId } = getSharePageParams()
          params = {
            elementId: this.pageId,
            cache: false, // 预览模式配置经常改,不走缓存
            instanceId: appId,
            isPageShare: true,
          }
        } else {
          params = {
            pageDesignId: this.pageInfo.pageDesignId,
            appDesignId: this.$route.query.vid,
          }
        }
        this._storeDispatch(UPDATE_PAGE_CONFIG, params)
          .then(() => {
            // 初始值变量赋值
            try {
              for(let key in this.props) {
                this.storeData.dynamicProps[this.storeData.layoutData.elementId].dynamicVariables[key].value = _.cloneDeep(this.props[key])
              }
            } catch(e) {
              console.warn(e)
            }
            this.compoentRender = true
            // 让组件渲染一段时间再停止loading
            this.$nextTick(() => {
              this.componentLoading = false
              this.componentError = false
            })
          })
          .catch((err) => {
            this.compoentRender = false
            this.componentError = true
            this.componentLoading = false
            // 资源加载错误报错
            trigger.recordBug({
              message: `“${componentName}” 加载异常`,
              error: err,
              target: this,
            })
          })
      }
    },
    // 寻找是否存在id相同的祖先节点
    findParentsById(node, elementId) {
      if(node.$parent) {
        if(node.$parent.elementId === elementId) {
          return true
        }
        return this.findParentsById(node.$parent, elementId)
      } else {
        false
      }
    },
    _receiveData({ data, type }) {
      /* 在保存配置后JSON.stringify,把值为 undefined的key给过滤掉,所以不能配置的 props 整个
        覆盖组件里的props, 只能每个键以覆盖形式追加上去
      */
      for(const key in data.props) {
        this.props[key] = _.cloneDeep(data.props[key])
      }
      // 配置更新,重新渲染
      if ([RECEIVE_PROPS].includes(type)) {
        this.renderPage()
      }
      // 变量赋值, 往所属的页面去更新页面变量
      else if(type === RECEIVE_VAR) {
        const result = {}
        for(let key in data) {
          if(key.startsWith('props.')) {
            result[`dynamicVariables.${key.slice(6)}.value`] = data[key]
          }
        }
        // 传$children是因为让事件链里面的vueThis.getStore() 拿到的是它所在级的正确store
        if(this.$children[0]) {
          trigger.setCompVariable({ result, elementId: this.storeData.layoutData.elementId, vueThis: this.$children[0] })
        }
      }
    },
    // 覆盖common.js的
    recordBug(err) {
      trigger.recordBug({
        message: `${this.componentName}发生错误: ${err}`,
        error: err,
        target: this,
        panel: 'prop',
      })
    },
  },
  created() {
    this.renderPage()
  },
}
</script>

<style lang="less" scoped>
@import '~@/styles/index.less';

.page-container-widget {
  position: relative;
  .page-container-mask {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: transparent;
    z-index: 1;
  }
}
</style>
