<template>
  <div
    class="component-dialog component-drawer-edit"
    :class="commonClassName"
    v-if="isEditMode"
    :id="elementId"
    :style="drawerStyle"
  >
    <div v-if="withHeader" class="component-drawer__header">
      <div class="dialog-title">{{ title }}</div>
      <div class="dialog-close-btn">
        <i class="iconfont icon-liuchengtu-guanbi" v-if="showClose"></i>
      </div>
    </div>
    <component
      class="component-drawer__content"
      :is="body.slotElement"
      :ref="body.elementId"
      :data-zone="`${body.elementId}_${body.elementType}`"
      v-bind="body"
      :id="body.elementId"
    ></component>
    <component
      v-if="showFooter !== false"
      class="component-drawer__footer"
      :showLayoutType="false"
      :is="footer.slotElement"
      :ref="footer.elementId"
      :data-zone="`${footer.elementId}_${footer.elementType}`"
      v-bind="footer"
      :id="footer.elementId"
    ></component>
  </div>
  <el-drawer
    v-else
    class="component-drawer"
    :withHeader="withHeader"
    :wrapperClosable="wrapperClosable"
    :modal-append-to-body="true"
    :append-to-body="true"
    :title="title"
    :size="size"
    :direction="direction"
    :visible.sync="dialogProvide.visible"
    :modal="modal"
    :show-close="showClose"
    :before-close="handleClose"
  >
    <div class="component-drawer__content">
      <component
        class="component-drawer__body"
        :is="body.slotElement"
        :ref="body.elementId"
        :data-zone="`${body.elementId}_${body.elementType}`"
        v-bind="body"
        :id="body.elementId"
      ></component>
      <component
        v-if="showFooter !== false"
        class="component-drawer__footer"
        slot="footer"
        :is="footer.slotElement"
        :ref="footer.elementId"
        :data-zone="`${footer.elementId}_${footer.elementType}`"
        v-bind="footer"
        :id="footer.elementId"
      ></component>
    </div>
  </el-drawer>
</template>

<script>
import { CLOSE, OPEN } from '@/config/eventName'
import common from '@/template/mixins/common'

export default {
  components: {
  },
  mixins: [common],
  variables: {},
  data() {
    return {
      dialogProvide: {
        visible: false,
      }
    }
  },
  computed: {
    body() {
      return this.getSlotByName('body')
    },
    footer() {
      return this.getSlotByName('footer')
    },
    drawerStyle() {
      const result = {
        ltr: {
          left: 0,
          height: '100%',
          maxHeight: 'unset',
        },
        rtl: {
          right: 0,
          height: '100%',
          maxHeight: 'unset',
        },
        ttb: {
          top: 0,
          width: '100%',
          maxWidth: 'unset',
        },
        btt: {
          bottom: 0,
          width: '100%',
          maxWidth: 'unset',
        },
      }[this.direction]
      return result
    },
  },
  provide() {
    return {
      dialogProvide: this.dialogProvide,
    }
  },
  methods: {
    handleClose(done) {
      this.dialogProvide.visible = false
      done()
      this._emitEvent(CLOSE)
    },
    async [OPEN]() {
      this.dialogProvide.visible = true
      await this.$nextTick()
      this._emitEvent(OPEN)
    },
    async [CLOSE]() {
      this.dialogProvide.visible = false
      await this.$nextTick()
      this._emitEvent(CLOSE)
    },
  },
  mounted() {
  },
}
</script>

