<template>
  <n-form-item
    v-show="state != 'hidden'"
    class="input-number-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    :rules="rules"
    @click.native="handleClickFormItem"
  >
    <div class="input-container">
      <el-input-number
        :style="{ width: inputWidth }"
        :disabled="state == 'disabled' || state == 'readonly'"
        :readonly="state == 'readonly'"
        :size="size"
        :step="step"
        :min="minValue"
        :max="maxValue"
        :precision="precision"
        :controls-position="controlsPosition"
        :placeholder="placeholder"
        @change="handleChange"
        v-model="value"
      >
      </el-input-number>
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
  import { CHANGE } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import form from '@/template/mixins/form'

  export default {
    mixins: [common, form],
    variables: {
      $value(newV) {
        if (this.precision !== undefined) {
          newV = Number(newV).toFixed(this.precision)
        }
        this.value = newV
      },
    },
    data() {
      return {
        passwordIcon: 'icon-kai',
      }
    },
    computed: {
      rules() {
        const { required, regExp, label } = this
        const res = []
        if (required) {
          res.push(this.getTrueRule({
            required: true,
            message: `请输入${label}`,
            trigger: 'change',
          }))
        }
        return regExp?.reduce((prev, cur) => {
          return prev.concat({
            pattern: cur.reg,
            message: cur.errorTips,
            transform(value) {
              return String(value)
            },
          })
        }, res)
      },
    },
    methods: {
      handleChange(value) {
        this._emitEvent(CHANGE)
      },
      handleIconClick() {
        if (this.passwordIcon === 'icon-guan') {
          this.type = 'text'
          this.passwordIcon = 'icon-kai'
        } else {
          this.type = 'password'
          this.passwordIcon = 'icon-guan'
        }
      },
    },
    mounted() {
      if (!this.valueInSlot && this.value === undefined) {
        this.value = this.defaultValue
      }
    },
  }
</script>

<style lang="less" scoped>
  .input-number-widget::v-deep {

    .input-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }

    .bottom-tips {
      color: #aaa;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
    }
  }
</style>
