<template>
  <n-form-item
    v-show="state !== 'hidden'"
    ref="ElFormItem"
    class="rate-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :rules="rules"
    :label-width="labelWidth"
    @click.native="handleClickFormItem"
  >
    <div class="input-container">
      <el-rate
        v-model="value"
        :disabled="state === 'disabled'"
        :max="maxValue"
        :show-text="textOptions.length"
        :allow-half="allowHalf"
        :texts="textOptions"
        @change="handleChange"
      />
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
  import { CHANGE } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import form from '@/template/mixins/form'
  import { requiredNumber } from '@/utils/validateUtils'

  export default {
    mixins: [common, form],
    variables: {},
    data() {
      return {
      }
    },
    computed: {
      rules() {
        const { required, regExp, label } = this
        const res = []
        if (required) {
          res.push(this.getTrueRule({
            required: true,
            message: `请选择${label}`,
            trigger: 'change',
            validator(rule, value, callback) {
              requiredNumber(value) ? callback(new Error()) : callback()
            }
          }))
        }
        return regExp?.reduce((prev, cur) => {
          return prev.concat({
            pattern: cur.reg,
            message: cur.errorTips,
          })
        }, res)
      },
    },
    mounted() {
      if(!this.valueInSlot) {
        this.value = this.defaultValue > this.maxValue ? this.maxValue : this.defaultValue
      }
    },
    methods: {
      async handleChange(value) {
        await this.$nextTick()
        // 触发formItem的change校验
        this.dispatchFormItem(this.$refs.ElFormItem, 'el.form.change', value)
        this._emitEvent(CHANGE)
      },
    },
  }
</script>

<style lang="less" scoped>
  .rate-widget::v-deep {
    .input-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      min-height: 32px;
    }
    .bottom-tips {
      color: #aaa;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
    }
  }
</style>
