export const VERSION_INFO = 'VERSION_INFO'
export const VERSION_JUDGE = 'VERSION_JUDGE'

export const mutationPath = 'CODE/baseInfo/'
const allMutationKey = {
  VERSION_INFO,
  VERSION_JUDGE
}

Object.keys(allMutationKey).forEach((key) => {
  allMutationKey[key] = mutationPath + allMutationKey[key]
})
export default allMutationKey