<template>
  <div
    v-if="isEditMode"
    class="iframe-widget"
    :class='commonClassName'
    :style='componentStyle'
    @click="handleStopPropagation"
    >
    <iframe
      class="ifr"
      :style='componentStyle'
      :src="src"
      scrolling="no"
      seamless
    ></iframe>
  </div>
  <iframe
    v-else
    class="iframe-widget"
    :class='commonClassName'
    :style='componentStyle'
    :src="src"
    scrolling="no"
    seamless
  ></iframe>
</template>

<script>
  import common from '@/template/mixins/common'

  export default {
    mixins: [common],
    variables: {},
    methods: {
      handleStopPropagation(e) {
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      }
    },
  }
</script>

<style lang="less" scoped>
  .iframe-widget::v-deep {
    width: 100%;
    height: 100%;
    border: unset;
    &.is-edit-mode {
      border: 1px dashed #dddddd;
      > .ifr {
        pointer-events: none;
        border: unset;
      }
    }
  }
</style>
