// event-bus.js
import Vue from 'vue'
const EventBus = new Vue()

import {
  UPDATE_DYNAMIC_PROPS
} from '@/config/storeKey'
import {
  update
} from './history'
import { BIND_EVENTS, BIND_PROPS, BIND_STYLES, PROPS, STYLES } from '@/config/eventKey'
import { EDIT } from '@/config'
import { isEditMode } from '@/utils'
import _ from 'lodash'

/**
 *
 * @param {*} key
 * @param {*} data
 */
export function globalEmit(key, params) {
  const {
    elementId,
    data: oldData,
    pushHistory = true,
    extra,
    callback
  } = params
  const data = _.cloneDeep(oldData)
  const emitKey = key + elementId
  const isConfigChange = isEditMode() && [PROPS, STYLES].includes(key)

  // 更改组件和全局配置的数据
  const commonCallback = data => {
    EventBus.$emit(emitKey, data)
    if(isConfigChange) {
      this._storeCommit(UPDATE_DYNAMIC_PROPS, {
        dynamicProps: {
          [elementId]: data
        }
      })
    }
  }

  if(isConfigChange && pushHistory) {
    // 存进历史栈
    const oldVal = this.getStore().dynamicProps[elementId]
    update({
      elementId,
      oldVal,
      val: data,
      extra,
      callback: data => {
        commonCallback(data);
        [BIND_PROPS, BIND_STYLES, BIND_EVENTS].forEach(item => EventBus.$emit(item))
        if(callback instanceof Function) {
          callback(data)
        }
      }
    })
  }
  commonCallback(data)
}

/**
 * @param {*} key
 * @param {*} elementId
 * @returns
 */
export const globalOn = (key, elementId, callback) => {
  return EventBus.$on(key + elementId, data => callback(data))
}


/**
 * 取消监听
 * @param {*} key 事件名称
 * @returns
 */
export const globalOff = (key, elementId) => {
  return EventBus.$off(key + elementId)
}

export default EventBus