<template>
  <div
    v-loading="!chartInstance && fetchLoading"
    ref="chart"
    class="chart-content"
    :class="commonClassName"
  ></div>
</template>

<script>
import { CLICK, RESET, DATA_ZOOM } from '@/config/eventName'
import common from '@/template/mixins/common'
import chart from '@/template/mixins/chart'

import { debounce } from '@/utils'
import _ from 'lodash'

export default {
  mixins: [common, chart],
  variables: {
    $chartData(newV) {
      this.staticData = newV
      this[RESET](false)
    },
  },
  computed: {},
  watch: {},
  data() {
    return {}
  },
  mounted() {},
  created() {},
  methods: {
    getOption() {
      const option = this.getBaseOption()
      if (this.type === 'horizontal') {
        option.xAxis.type = 'value'
        option.yAxis.type = 'category'
      }
      return option
    },
    // 初始化监听事件
    initEvents() {
      //点击
      this.chartInstance.on('click', (e) => {
        this.dotData = e.data
        this._emitEvent(CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.event.event.stopPropagation()
        }
      })
      // 数据缩放
      this.chartInstance.on(
        'dataZoom',
        debounce((e) => {
          const { start, end } = e
          const { xAxisData: data } = this
          this.dataZoomRange = this.getDataZoomRange({ start, end, data })
          this._emitEvent(DATA_ZOOM)
        })
      )
    },
    getTypeSetting() {
      const extra = {
        stack: this.stack && 'name',
        // 不同系列的柱间距离，为百分比（如 '30%'，表示柱子宽度的 30%）。
        barGap: this.getBarGap(this.barGap?.barGap),
        // 同一系列的柱间距离，默认为类目间距的20%，可设固定值
        barCategoryGap: this.getBarGap(this.barGap?.barCategoryGap),
      }
      return extra
    },
    getBarGap(obj) {
      if (!obj) {
        return undefined
      }
      const { value, unit } = obj
      if(unit === '') {
        return Number(value)
      }
      return `${value}${unit}`
    },
  },
}
</script>

<style lang="less" scoped>
@import '~@/styles/index.less';

.chart-content {
  width: 100%;
  height: 400px;
}
</style>
