import { PAGE_EXPORT_FUNCTION } from '@/config'
import {
  versionData
} from '@/template'
import _ from 'lodash'
// props的缓存
const propsConfigCache = {}

export function getConfig(elementType, elementVersion) {
  if (!elementType || !elementVersion) {
    return {
      default: {},
      fns: [],
      events: []
    }
  }
  const { events, fns, ...others } = require(`@/template/components/${elementType}/${elementVersion}/watch-props.js`)
  return {
    ...others,
    events: events || [],
    fns: fns || []
  }
}

/**
 * 获取对应类型和版本的组件的配置
 * @param {string} elementType 组件类型
 * @param {string} elementVersion 组件版本
 * @returns {array} 配置数组
 */
export function getPropsConfig({
  elementType,
  elementVersion
}) {
  if (!elementType || !elementVersion) {
    return []
  }
  const key = `${elementType}-${elementVersion}`
  // 已有配置, 不再重新遍历
  if (propsConfigCache[key]) return propsConfigCache[key]

  const watchProps = getConfig(elementType, elementVersion).default
  let result = Object.keys(watchProps).map((key) => {
    const attrObj = {}
    const ATTR = 'attr'
    const keyObj = watchProps[key]
    attrObj.default = typeof keyObj.default === 'function' ? keyObj.default() : keyObj.default
    Object.keys(keyObj).forEach((k) => {
      const v = keyObj[k]
      if (k.includes(ATTR)) {
        let nk = k.split(ATTR)[1]
        nk = nk ? nk.toLowerCase() : ''
        attrObj[nk] = v
      }
      attrObj.propKey = key
    })
    return attrObj
  })

  propsConfigCache[key] = result

  return result
}

/**
 * 获取属性配置的默认值
 * @param {string} elementType 组件类型
 * @param {string} elementVersion 组件版本
 * @returns {object} 包含默认值的键值对对象
 */
export function getPropsValues({
  elementType,
  elementVersion,
  elementId
}) {
  let props = {}

  if (isCustomAttributes({ elementId, elementType })) {
    const {
      propsData
    } = getCustomConfig.call(this, elementId)
    propsData.forEach(item => {
      const {
        type,
        value
      } = item.defaultValue
      let val = value
      try {
        if(type === 'function') {
          val = new Function(value)()
        }
      } catch (error) {
        val = value
        console.warn(error)
      }
      props[item.name] = type === 'function' ? val : value
    })
  }

  const result = getPropsConfig({
    elementType,
    elementVersion
  }).filter(item => !item.protected).reduce((obj, item) => {
    if(item.propKey === 'props' && isCustomAttributes({ elementId, elementType })) {
      obj[item.propKey] = props
    } else {
      obj[item.propKey] = item.default
    }
    return obj
  }, {})

  return result
}

/**
 * 通过key去获取特点项的字段
 * @param {*} props
 * @param {*} key
 * @returns {object} 字段配置
 */
export function getPropByKey(props, key) {
  return props.find(item => item.propKey === key)
}

/**
 * 获取组件的配置,
 * @param {string} elementType 组件类型
 * @param {string} elementVersion 组件版本
 * @param {string} elementId 组件id 非必填
 * @returns {object} 组件配置
 */
export function getConfigByKey({
  elementType,
  elementVersion,
  elementId
}) {
  let {
    events = [], fns = [],
      default: props = {}
  } = getConfig(elementType, elementVersion)
  // 业务组件事件是用户可动态修改的
  if (isCustomAttributes({ elementId, elementType })) {
    const {
      eventsData = []
    } = getCustomConfig.call(this, elementId)
    events = events.concat(eventsData.map(item => ({
      ...item,
      eventName: item.emitKey,
      params: item.emitParams
    })))
  }

  // 格式化参数key
  events = events.map(item => {
    if (item.params && item.params instanceof Array) {
      item.params = item.params.map((param, index) => ({
        ...param,
        key: `param${index + 1}`
      }))
    } else {
      item.params = []
    }
    return item
  })
  return {
    events: _.cloneDeep(events),
    fns,
    props
  }
}

/**
 * 根据类型和版本获取组件的暴露变量
 * @param {string} elementType 组件类型
 * @param {string} elementVersion 组件版本
 * @returns {object}
 */
export function getVariableByVersion({
  elementType,
  elementVersion,
  elementId
}) {
  const watchProps = getConfig(elementType, elementVersion).default
  const result = Object.keys(watchProps).reduce((obj, key) => {
    const newKey = '$' + key
    const isFn = watchProps[key].type === Function
    obj[newKey] = obj[newKey] || {}
    obj[newKey].type = isFn ? watchProps[key].type.name : watchProps[key].type
    obj[newKey].desc = watchProps[key].desc || ''
    obj[newKey][key] = newKey
    obj[newKey].auth = watchProps[key].auth
    obj[newKey].attrVue = watchProps[key].attrVue
    return obj
  }, {})

  if (isCustomAttributes({ elementId, elementType })) {
    const {
      propsData
    } = getCustomConfig.call(this, elementId)
    propsData.forEach(item => {
      const {
        name,
        desc,
      } = item
      const newKey = `$props.${name}`
      result[newKey] = {
        desc: `${desc || ''} （组件props: ${name}）`,
        type: '',
        [newKey]: newKey
      }
    })
  }

  return result
}

export function getComponent(elementType, elementVersion) {
  return versionData[elementVersion][elementType] || {}
}

/**
 * 解析vue组件的props
 * @param {*} vueInstance vue组件对象
 * @returns {array}
 */
export async function resolveProps(value) {
  const vueInstance = await resolveComponent(value)
  let data = []
  const {
    props = {}
  } = vueInstance
  // 是一个数组,例如: props: ['id', 'row']
  if (props instanceof Array) {
    data = props.map((prop) => ({
      name: prop,
      type: type2string()
    }))
  } else if (Object.prototype.toString.call(props) === '[object Object]') {
    /**
        props: {
          // 基础的类型检查 (`null` 和 `undefined` 会通过任何类型验证)
          propA: Number,
          // 多个可能的类型
          propB: [String, Number],
          // 必填的字符串
          propC: {
            type: String,
            required: true
          },
          // 带有默认值的数字
          propD: {
            type: Number,
            default: 100
          },
          // 带有默认值的对象
          propE: {
            type: Object,
            // 对象或数组默认值必须从一个工厂函数获取
            default: function () {
              return { message: 'hello' }
            }
          },
          // 自定义验证函数
          propF: {
            validator: function (value) {
              // 这个值必须匹配下列字符串中的一个
              return ['success', 'warning', 'danger'].indexOf(value) !== -1
            }
          }
        }
      */

    data = Object.entries(props).map(([key, setting]) => {
      // 值是数组, 所以是其他配置
      if (Object.prototype.toString.call(setting) === '[object Object]') {
        return {
          name: key,
          ...setting,
          type: type2string(setting.type)
        }
      }
      // 值不是数组, 那就是类型了
      else {
        return {
          name: key,
          type: type2string(setting)
        }
      }
    })
  }
  // 增加默认值配置
  data = data.map(item => ({
    ...item,
    // 避免保存时JSON.stringify时被过滤掉
    default: typeof item.default === 'function' ? item.default.toString() : item.default,
    desc: item.name,
    defaultValue: {
      value: undefined,
      type: ''
    }
  }))
  return data
}

/**
 * 解析vue组件包含的emit名字和参数数量
 * @param {*} vueInstance vue组件对象
 * @returns {array}
 */
export async function resolveEvents(value) {
  const vueInstance = await resolveComponent(value)
  const mapKey = [
    'beforeCreate',
    'created',
    'beforeMount',
    'mounted',
    'beforeUpdate',
    'updated',
    'activated',
    'deactivated',
    'beforeDestroy',
    'destroyed',
    'errorCaptured',
    'watch',
    'methods',
    'computed',
    'render',
  ]
  // 转化方法的对象
  const searchTarget = {}
  Object.keys(vueInstance).forEach((key) => {
    if (mapKey.includes(key)) {
      searchTarget[key] = vueInstance[key]
    }
  })
  const result = obj2String(searchTarget)

  const paramsData = result.map((item) => {
    const emitKey = item.replace(
      /\$emit\((('|")([^"']*)('|"))[\s\S]*?\)/g,
      '$3'
    )
    const commaMatch = item.match(/,/g)
    const paramsNumber = commaMatch ? commaMatch.length : 0
    const emitParams = []
    for (let i = 0; i < paramsNumber; i++) {
      emitParams.push({
        name: `第${i + 1}个参数`
      })
    }
    return {
      name: emitKey,
      emitKey,
      emitParams,
    }
  })
  // 去重
  const target = []
  paramsData.forEach(item => {
    const index = target.findIndex(opt => opt.emitKey === item.emitKey)
    if (index !== -1) {
      if (item.emitParams.length > target[index].emitParams.length) {
        target[index] = item
      }
    } else {
      target.push(item)
    }
  })
  return target

}

// 把对象解析为vue组件
function resolveComponent(data) {
  return new Promise((resolve) => {
    // 方法
    if (data instanceof Function) {
      const result = data()
      if (result instanceof Promise) {
        result.then(res => {
          resolve(res.default)
        })
        return
      }
    } else if (Object.prototype.toString.call(data) === '[object Object]') {
      resolve(data)
      return
    }
    resolve(data || {})
  })
}

// 把类型转为字符串
function type2string(type) {
  function getString(value) {
    const stringKey = {
      'String': String,
      'Number': Number,
      'Boolean': Boolean,
      'Array': Array,
      'Object': Object,
      'Date': Date,
      'Function': Function,
      'Symbol': Symbol,
    }
    for (let key in stringKey) {
      if (stringKey[key] === value) {
        return key
      }
    }
  }
  if (type instanceof Array) {
    return type.map((item) => getString(item) || '*')
  }
  return getString(type) || '*'
}

// 把对象的function转为字符串
function obj2String(target) {
  let result = []

  switch (Object.prototype.toString.call(target)) {
    // 对象
    case '[object Object]': {
      for (let key in target) {
        result = result.concat(obj2String(target[key]))
      }
      break
    }
    //方法
    case '[object Function]':
      return getEmitFn(target.toString())
      // 数组
    case '[object Array]': {
      for (let item of target) {
        result = result.concat(obj2String(item))
      }
      break
    }
  }
  return result
}

/* 没有$emit就去掉,有的把emit()括号里面的括号成对去掉, 只留下逗号
 这里注意的是, 要过滤掉 $emit("update:xxx") 这样的和.sync修饰符搭配的非接收事件
*/
function getEmitFn(str) {
  const [prefix] = str.match(/\$emit\((\"|')(?!update:).*?(\"|')/) || ['']
  // 有结果, 开始计算文本
  if (prefix) {
    // 匹配到的字符串当前索引
    const index = str.indexOf(prefix)
    // 除去$emit开始的文本
    const include = str.slice(index + prefix.length)
    // 栈
    const record = []
    // 闭合符号对应的关系
    const deleteMap = {
      '(': ')',
      '[': ']',
      '{': '}'
    }
    // 累加的字符串
    let total = prefix
    // 闭合符号左右的符号
    let leftKey, rightKey
    for (let i = 0; i < include.length; i++) {
      // 是否刚被清空的
      let isJustEmpty = false
      const item = include[i]
      /* 以最先遇到的一个闭合符号为基准符号, 比如 ({"a": "234"}),结果就是 => leftKey = "(" rightKey = ")"
        就只会把 "(" ")" 以及中间的内容统统删掉
      */
      if (deleteMap[item] && !leftKey && !rightKey) {
        leftKey = item
        rightKey = deleteMap[item]
      }
      // 有一个左符号, 例如 "(", 进栈
      if (item === leftKey) {
        record.push(i)
      }
      // 有一个右符号, 例如 ")", 出栈
      else if (item === rightKey) {
        if (record.length) {
          record.pop()
          // 如果全都抵消了, 就重新定义闭合符
          if (!record.length) {
            // 置空,以便上面重新分配闭合符
            leftKey = ''
            rightKey = ''
            // 刚置空
            isJustEmpty = true
          }
        }
      } else {
        // 栈为空时(没有未闭合的符号, 或者已被抵消) => 继续累加
        if (!record.length) {
          total += item
        }
      }
      // 匹配到 $emit(".*".*) 后面的这个 ")"时, 证明已经匹配到一个完成的方法, 保存当前格式化后的字符串的并递归后面的字符串
      if (item === ')' && !record.length && !isJustEmpty) {
        return [total].concat(getEmitFn(include.slice(i)))
      }
    }
    // 没有在for 循环中return, 证明没有正常闭合, 可能是一个不规则的字符串, 不予理会=
    return []
  } else {
    return []
  }
}

function getCustomConfig(elementId) {
  const data = _.cloneDeep(this.getStore() ? this.getStore().dynamicProps[elementId] : {}) || {}
  data.eventsData = data.eventsData || []
  data.propsData = data.propsData || []
  return data
}
// 获取回调参数变量的正则
export function getEmitParamsRegx(elementId) {
  return new RegExp(`^(${elementId}\\.\\$emitEvent\\.args)(\\.\\[[0-9]+\\])*(\\..*)*$`)
}
// 是否有自定义属性
export function isCustomAttributes({ elementId, elementType }) {
  return ['business', 'page', 'page-container', 'antv-chart'].includes(elementType) && elementId
}