// 获取token
export const getToken = () => {
  let token
  try {
    token = JSON.parse(localStorage.getItem('loginKey'))['snc-token']
  } catch (e) {
    console.error('获取token出错 :', e)
    token = ''
  }
  return token
}

export function getCommonHeaders() {
  return {
    'snc-token': getToken()
  }
}

/** 根据图片名字获取可显示图片的src
 * @param {string} name 图片名字
 * return {string} 可访问图片src
 */
export const getImageSrc = (name) => {
  return new Promise((resolve, reject) => {
    let token
    try {
      token = getToken()
    } catch (err) {
      console.log('获取token时错误', err)
      reject()
      return
    }

    axios({
        url: API.appIcon + name,
        method: 'get',
        responseType: 'blob',
        headers: {
          'snc-token': token,
        },
      })
      .then((res) => {
        const src = URL.createObjectURL(res.data)
        // 获取到src之后进行垃圾回收
        setTimeout(() => {
          URL.revokeObjectURL(src)
        }, 0)
        resolve(src)
      })
      .catch(() => {
        console.error(`“${name}” 图片加载失败`)
        reject()
      })
  })
}

export const exportFile = ({
  url,
  method = 'post',
  params,
  data,
  headers = {
    'snc-token': getToken(),
  },
  fileName
}) => {
  axios({
      url,
      method,
      responseType: 'blob',
      params,
      data,
      headers
    })
    .then((res) => {
      let name
      try {
        name = decodeURIComponent(res.headers['content-disposition']
          .split(';')
          .find((item) => item.includes('filename='))
          .split('filename=')[1])
      } catch (err) {
        console.log(err)
        name = new Date().getTime()
      }
      if (fileName) {
        name = fileName
      }
      const url = URL.createObjectURL(res.data)
      const aLink = document.createElement('a')
      aLink.download = name
      aLink.href = url
      aLink.dispatchEvent(new MouseEvent('click', {}))
      Message({
        message: '导出成功!文件格式为.zip',
        type: 'success',
      })
    })
    .catch(() => {
      Message({
        message: '导出失败!',
        type: 'error',
      })
    })
}

export const getImageUrl = (name, placeholder = true, appId ) => {
  if (!name) {
    return placeholder ? getImgPrefix('/img/img-error.png') : ''
  }
  // 兼容传进来是一个http地址的情况
  if (name.includes('http')) {
    return name
  }
  const {appId: instanceId} = getSharePageParams()
  const search = window.location.search
  const { appId: queryAppId, id } = qs.parse(search.replace('?', ''))
  const sourcePath = name.indexOf('app_') === 0 ? `apps/${appId || queryAppId || id || instanceId}` : 'default'
  const path = `${store.state.CODE.lowCodeStore.appSourcePrefix}${sourcePath}/images/` + name
  return path
}

export const copyText = (text, message = '复制成功') => {
  const textarea = document.createElement('textarea')
  textarea.value = text
  document.body.appendChild(textarea)
  textarea.select()
  try {
    document.execCommand('Copy')
    Message({
      message: message,
      type: 'success',
    })
  } catch (error) {
    Message({
      message: '复制失败！',
      type: 'error',
    })
  } finally {
    document.body.removeChild(textarea)
  }
}
/**
 * 把对象过滤掉undefined并转换成 a=1&b=2&c=3的格式
 * @param {object} query
 * @returns {string}
 */
export const formatQuery = (query) =>
  qs.stringify(JSON.parse(JSON.stringify(query)))

export function formatTag(value, length = 20) {
  if (typeof value === 'string') {
    if (!value) {
      return '空文本'
    }
    const res = value.slice(0, length)
    return value.length >= length ? res + '...' : res
  }
  if (typeof value === 'number') {
    const res = `${value}`
    return res.length >= length ? res.slice(0, length) + '...' : res
  }
  if (typeof value === 'object') {
    const res = JSON.stringify(value)
    return res.length >= length ? res.slice(0, length) + '...' : res
  }
  if (typeof value === 'boolean') {
    return `${value}`
  }
  return value
}

export function formatTip(value, length = 400) {
  if (typeof value === 'string' && !value) {
    return '空文本'
  }
  if (typeof value === 'object') {
    return JSON.stringify(value).slice(0, length)
  }
  if (typeof value === 'boolean') {
    return `${value}`
  }
  return `${value}`.slice(0, length)
}

export const sleep = (time = 0) =>
  new Promise((resolve) => setTimeout(resolve, time))

/**
 * 全局的异常捕获
 * @param {Function} func 传入的方法
 * @param  {...any} args 方法调用的参数
 * @returns {Array} [err, res]
 */
export async function asyncFunc(func, ...args) {
  try {
    const res = await func(...args)
    return [null, res]
  } catch (e) {
    return [e, null]
  }
}


/**
 * 使一个dom元素全屏展示
 * @param {HTMLElement} el 要全屏展示的元素
 */
export function fullScreen(element = document.documentElement) {
  if (isFullscreen()) {
    if (document.exitFullscreen) {
      document.exitFullscreen()
    } else if (document.webkitCancelFullScreen) {
      document.webkitCancelFullScreen()
    } else if (document.mozCancelFullScreen) {
      document.mozCancelFullScreen()
    } else if (document.msExitFullscreen) {
      document.msExitFullscreen()
    }
  } else {
    if (element.requestFullscreen) {
      element.requestFullscreen()
    } else if (element.webkitRequestFullScreen) {
      element.webkitRequestFullScreen()
    } else if (element.mozRequestFullScreen) {
      element.mozRequestFullScreen()
    } else if (element.msRequestFullscreen) {
      // IE11
      element.msRequestFullscreen()
    }
  }

  function isFullscreen() {
    return document.fullscreenElement ||
      document.msFullscreenElement ||
      document.mozFullScreenElement ||
      document.webkitFullscreenElement || false
  }
}
export const trimAll = (str) => str.replace(/[\n\r\t\f]/g, '').replace(/ /g, '')

/**
 * 可指定返回值的防抖函数
 * @param {function} callback 回调
 * @param {number} time 延迟时间
 * @returns {function} 防抖方法返回的值
 */
export const debounce = (callback, {
  time = 300,
  returnValue = undefined
} = {}) => {
  let timer = null
  return function () {
    const args = Array.from(arguments)
    clearTimeout(timer)
    timer = setTimeout(() => {
      callback.apply(this, args)
    }, time)
    return returnValue
  }
}

// 获取格式化类型
export const getMapType = value => {
  const type = Object.prototype.toString.call(value).slice(8, -1)
  switch (type) {
    case 'String':
    case 'Number':
    case 'Boolean':
    case 'Object':
    case 'Array':
      return type
    case 'Null':
    case 'Undefined':
      return 'Null'
  }
}

/**
 *
 * @param {any} data 要导出的数据
 * @param {string} fileName 导出的文件名
 */
export const downLoadFiles = (data, fileName) => {
  if (typeof navigator !== 'undefined' && navigator.msSaveOrOpenBlob) {
    //兼容ie10
    var blob = new Blob([JSON.stringify(data)], {
      type: 'data:application/json;charset=utf-8'
    });
    navigator.msSaveOrOpenBlob(blob, fileName);
  } else {
    const a = document.createElement('a');
    a.style.visibility = 'hidden';
    document.body.appendChild(a);
    var blobs = new Blob([JSON.stringify(data)], {
      type: 'data:application/json;charset=utf-8'
    });
    var objurl = URL.createObjectURL(blobs);
    a.href = objurl;
    // a.href = "data:application/json;charset=utf-8" + JSON.stringify(data, null, 2);  // 使用这种方式会导出文件失败net error，原因在于协议不同无法跨域
    a.download = fileName;
    a.click();
    document.body.removeChild(a);
  }
}

export function getCookies() {
  var pairs = document.cookie.split(';')
  var cookies = {}
  for (var i = 0; i < pairs.length; i++) {
    var pair = pairs[i].split('=')
    cookies[(pair[0] + '').trim()] = unescape(pair[1])
  }
  return cookies
}

// 补零
export function prefixZero(num, n) {
  return (Array(n).join(0) + num).slice(-n);
}



/**
 * 将CSS形式的字符串转换为JSON
 *
 * @param {css字符串} css
 */
export function css2json(css) {
  // 是json,直接返回
  if (Object.prototype.toString.call(css) === '[object Object]') {
    return css
  }
  // 不是字符串, 返回空json
  else if (Object.prototype.toString.call(css) !== '[object String]') {
    return {}
  }

  let open, close
  // 移除CSS所有注释
  while ((open = css.indexOf('/*')) !== -1 &&
    (close = css.indexOf('*/')) !== -1) {
    css = css.substring(0, open) + css.substring(close + 2);
  }

  // 第一步：将声明转换为Object，如：
  // `font: 'Times New Roman' 1em; color: #ff0000; margin-top: 1em;`
  //  ==>
  // `{"font": "'Times New Roman' 1em", "color": "#ff0000", "margin-top": "1em"}`

  // 辅助方法：将array转为object

  // 切割声明块并移除空白符，然后放入数组中
  let declarations = css.split(';')
    .map(e => e.trim())
    .filter(e => e.length > 0); // 移除所有""空值

  // 转为Object对象
  const json = toObject(declarations);

  function toObject(array) {
    let ret = {};
    array.forEach(e => {
      const index = e.indexOf(':');
      const property = e.substring(0, index).trim();
      const value = e.substring(index + 1).trim();
      ret[property] = value;
    });
    return ret;
  }

  // 返回JSON形式的结果串
  return json;
}

// 正则判断字符串是否属于组件变量

export function isValidateVariable(key) {
  const str = typeof key === 'string' ? key : false
  if (!str) {
    return false
  }
  return /^([0-9a-zA-Z]+(\-[0-9a-zA-Z]+)+|app)\.\$[0-9a-zA-Z]/.test(str)
}

// 正则判断字符串是否是应用变量并返回对应值
export function getTrueAppVariables(str) {
  if(typeof str !== 'string') {
    return false
  }
  const valid = /^([0-9a-zA-Z]+(\-[0-9a-zA-Z]+)+|app)\.\$appVariables\..+\.value/.test(str)
  const path = str.split('.$appVariables.')[1]
  return valid && _.get(getAppVariables(), path)
}

// 正则验证字符串是否是参数
export function isValidateParams(key, codeList) {
  const str = typeof key === 'string' ? key : false
  if (!str) {
    return false
  }
  return [...codeList, 'global', 'macroVariables'].some(code => code && str.indexOf(`$${code}`) === 0)
}

