export function loadCustomIconfont(force = true) {
  const customIconfont = document.getElementById('customIconfont')
  if (customIconfont) {
    if (force) {
      customIconfont.remove()
    } else {
      return
    }
  }
  const search = window.location.search
  const { appId, id } = qs.parse(search.replace('?', ''))
  const link = document.createElement('link')
  link.id = 'customIconfont'
  link.type = 'text/css'
  link.rel = 'stylesheet'
  link.href = `${store.state.CODE.lowCodeStore.appSourcePrefix}apps/${appId || id}/iconfont/iconfont.css`
  const head = document.getElementsByTagName('head')[0]
  head.appendChild(link)
}

// 暂时判断dom中有没有打开弹窗的, 阻止快捷键操作
export function dialogOpend() {
  const { $ } = window
  const hasPopup = Array.from($('.el-popover')).some(item => $(item).css('display') !== 'none')
  const hasDialog = Array.from($('.el-dialog__wrapper')).some(item => $(item).css('display') !== 'none')
  const hasMessage = Array.from($('.el-message-box__wrapper')).some(item => $(item).css('display') !== 'none')
  return hasPopup || hasDialog || hasMessage
}

// 过滤可能存在的xss危险内容
const whiteList = XSS.getDefaultWhiteList()
const attrs = ['class', 'style']
whiteList.strike = []
Object.keys(whiteList).forEach(key => {
  whiteList[key].push(...attrs)
})
export function filterXss(html, options = { whiteList, css: false, onIgnoreTagAttr: function(tag, name, value, isWhiteAttr) {
  if (name.substr(0, 5) === 'data-') {
    // 通过内置的escapeAttrValue函数来对属性值进行转义
    return name + '="' + XSS.escapeAttrValue(value) + '"';
  }
}}) {
  return XSS(html, options)
}

// 创建webworker
export function createWorker(f) {
  var blob = new Blob(['(' + f.toString() +')()']);
  var url = window.URL.createObjectURL(blob);
  var worker = new Worker(url);
  return worker;
}

// 为了兼容旧数据，暂时改为new Function的形式，因为旧数据已经在代码中调用了rootApp等全局对象的方法
export function runCodeInWorker(args, code, values) {
  return new Promise((resolve, reject) => {
    try {
      const func = new Function(...args, code)
      const res = func(...values)
      resolve(res)
    } catch (e) {
      reject(e)
    }
  })
}

