// 获取分享页的code
export function getSharePageParams() {
  const pathname = window.location.pathname

  try {
      // 不带基座头部和左侧菜单的链接
    if(pathname.includes(previewRoutePath)) {
      const { id, code } = qs.parse(location.search.slice(1))
      return { appId: id, code }
    } else {
      const lastPath = _.last(pathname.split('/').filter(p => p))
      const hasInstancePrefix = lastPath.indexOf(instancePathPrefix) === 0
      if (hasInstancePrefix) {
        const appId = +lastPath.replace(instancePathPrefix, '').split('_')[0]
        return {
          appId,
          code: lastPath.split('_')[1]
        }
      }
    }
  } catch (error) {
    console.log(error)
  }
  return {}
}

export function getImgPrefix(path) {
  return `${STATIC_PREFIX}/static${path}`
}

export function addCssByStyle(id, cssString) {
  if (!cssString) {
    return
  }
  const beforeStyle = document.getElementById(id)
  beforeStyle?.remove()
  let style = document.createElement('style')
  style.setAttribute('type', 'text/css')
  style.setAttribute('id', id)
  if( style.styleSheet ) {
    style.styleSheet.cssText = cssString
  } else {
    const cssText = document.createTextNode(cssString)
    style.appendChild(cssText)
  }
  const heads = document.getElementsByTagName('head')
  if( heads.length ) {
    heads[0].appendChild(style)
  } else {
    document.documentElement.appendChild(style)
  }
}

export function isEditMode() {
  return !(location.href.includes(instancePathPrefix) || location.href.includes(previewRoutePath))
}

// 获取当前应用变量
export function getAppVariables() {
  const allAppVariables = store.state.CODE.lowCodeStore.appVariables || {}
  return allAppVariables[getUnionAppKey()] || null
}

// 设置当前应用变量
export function setAppVariables(appVariables) {
  const allAppVariables = store.state.CODE.lowCodeStore.appVariables
  const { isNeedTab } = store.state
  // 如果没开多tab页的话, 只允许同时存在一个应用的应用变量, 把其他不用的应用变量清掉
  const newAppVariables = { ...(isNeedTab ? allAppVariables : {}), [getUnionAppKey()]: appVariables }
  store.commit(UPDATE_APP_VARIABLES, { appVariables: newAppVariables })
}

// 获取当前是哪个模式
export function getCurrentMode() {
  const { name: routeName, path: routePath, query } = rootApp.$route
  // 应用编辑页
  if(routeName === authKey.AppDesign) {
    return 'appEdit'
  }
  // 不含基座头部和左侧菜单的页面
  else if(routeName === 'customCode') {
    // 新窗口打开的应用分享页
    if(query.code) {
      return 'pageShare_newTab'
    }
    // 应用预览页
    else if(query.vid) {
      return 'appPreview'
    }
    // 应用桌面访问实例页
    else if(query.id) {
      return 'appInstance'
    }
  }
  // 配成菜单嵌在基座的应用分享页
  else if(routePath.includes(instancePathPrefix)) {
    return 'pageShare'
  }
  return 'other'
}

// 根据当前模式获取唯一的appId标识
export function getUnionAppKey() {
  const { id, appId } = rootApp.$route.query
  const mode = getCurrentMode()
  const key = {
    'appPreview': appId,
    'appInstance': appId,
    'pageShare_newTab': id,
    'pageShare': getSharePageParams().appId,
    'appEdit': appId,
    'other': ''
  }[mode || 'other']
  return `${mode}_${key}`
}

export const animateCSS = ({
  element,
  query,
  animationName,
  prefix = animateCssPrefix,
  duration = 1,
  repeat = 1,
  delay = 0,
  }) =>
  new Promise((resolve, reject) => {
    const realAnimationName = `${prefix}${animationName}`;
    const nodeList = element ? [element] : document.querySelectorAll(query);
    nodeList.forEach(node => {
      node.style.setProperty('--animate-duration', `${duration}s`);
      node.style.setProperty('--animate-repeat', `${repeat}`);
      if (delay) {
        node.style.setProperty('animation-delay', `${delay}s`);
      }
      node.classList.add(`${prefix}animated`, realAnimationName);
      function handleAnimationEnd(event) {
        event.stopPropagation();
        node.classList.remove(`${prefix}animated`, realAnimationName);
        resolve('Animation ended');
      }
      node.addEventListener('animationend', handleAnimationEnd, {once: true});
    })
  });

// 获取静态资源存储模式
export function getAppSourcePrefix() {
  request({
    url: API.appDesignStorageProperties,
    method: 'POST',
    headers: getCommonHeaders(),
  }).then(({data}) => {
    const { pathPrefix } = data.data
    store.commit(UPDATE_APP_SOURCE_PREFIX, { pathPrefix })
  })
}