<template>
  <n-form-item
    ref="ElFormItem"
    v-show="state !== 'hidden'"
    class="color-picker-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :rules="rules"
    :label-width="labelWidth"
    @click.native="handleClickFormItem"
  >
    <div>
      <el-color-picker
        v-model="value"
        :size="size"
        :disabled="state === 'disabled'"
        :show-alpha="showAlpha"
        @change="handleChange"
      ></el-color-picker>
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
  import { CHANGE } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import form from '@/template/mixins/form'

  export default {
    mixins: [common, form],
    variables: {},
    data() {
      return {
      }
    },
    computed: {
      rules() {
        const { required, regExp, label } = this
        const res = []
        if (required) {
          res.push(this.getTrueRule({
            required: true,
            message: `请选择${label}`,
            trigger: 'change'
          }))
        }
        return regExp?.reduce((prev, cur) => {
          return prev.concat({
            pattern: cur.reg,
            message: cur.errorTips,
          })
        }, res)
      },
    },
    methods: {
      async handleChange(value) {
        this.value = value
        this._emitEvent(CHANGE)
        await this.$nextTick()
        this.dispatchFormItem(this.$refs.ElFormItem, 'el.form.change', value)
      },
    },
    mounted() {
      if (!this.valueInSlot && typeof this.value === 'undefined') {
        this.value = this.defaultValue
      }
    },
  }
</script>

<style lang="less" scoped>
  .color-picker-widget::v-deep {
    .bottom-tips {
      color: #aaa;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
    }
  }
</style>
