<template>
  <div class="slot-widget" :class='commonClassName' :style='componentStyle'>
    <layout-placeholder
      :showLayoutType="showLayoutType"
      :elementId="elementId"
      :elementType="elementType"
      v-if="!visibleChildren.length && isEditMode"
      :emptyText="emptyText"
    ></layout-placeholder>
    <template v-for="child in slotChildren">
      <component
        :key="child.elementId"
        :is='getElement(child.elementType, child.elementVersion)'
        :ref="child.elementId"
        :data-zone="`${child.elementId}_${child.elementType}`"
        v-bind="{...child, slotProps}"
        :id="child.elementId"
      ></component>
    </template>
  </div>
</template>

<script>
  import common from '@/template/mixins/common'
  import { FUNCTION_FLAG } from '@/config'
  export default {
    mixins: [common],
    variables: {},
    props: {
      slotProps: {
        type: Object,
        default: () => ({}),
      },
      emptyText: {
        type: String
      },
      showLayoutType: {
        type: Boolean,
        default: true
      }
    },
    computed: {
      slotChildren() {
        return this.isEditMode ? this.visibleChildren : this.visibleChildren.filter(item => {
          const currentRule = this.slotRules.find(rule => rule.child === item.elementId)
          return currentRule ? !this.runCodeByFunctionId(currentRule.functionId) : true
        })
      }
    },
    methods: {
      runCodeByFunctionId(functionId) {
        const realFunctionId = functionId.split(FUNCTION_FLAG)[1]
        const functionsData =
          this.getStore().functionsData
        const currentFunction = functionsData.find(
          (item) => item.id === realFunctionId
        )
        if (currentFunction) {
          const { paramsData, code } = currentFunction
          const data = paramsData.filter((d) => d.key)
          const params = Object.fromEntries(
            data.map((d) => {
              return [d.key, this.slotProps]
            })
          )
          const args = Object.keys(params)
          const values = Object.values(params)
          try {
            // todo: js 沙箱机制
            const func = new Function(...args, code)
            return func(...values)
          } catch (e) {
            return false
          }
        }
        return false
      }
    },
  }
</script>

<style lang="less" scoped>
  .slot-widget::v-deep {
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    position: relative;

    &.is-edit-mode {
      outline: 1px dashed #cccccc;
      padding: 1px;
    }
  }
</style>
