/**
 * @param {string} path
 * @returns {Boolean}
 */
export function isExternal(path) {
  return /^(https?:|mailto:|tel:)/.test(path)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validUsername(str) {
  const valid_map = ['admin', 'editor']
  return valid_map.indexOf(str.trim()) >= 0
}

/**
 * @param {string} url
 * @returns {Boolean}
 */
export function validURL(url) {
  return /^(((ht|f)tps?):\/\/)?[\w-]+(\.[\w-]+)+([\w.,@?^=%&:/~+#-]*[\w@?^=%&/~+#-])?$/.test(url)
}

export function isValidJsonString(string) {
  const str = string.trim()
  const array = /^\[[\s\S]*\]$/
  const object = /^{[\s\S]*\}$/
  return array.test(str) || object.test(str)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validLowerCase(str) {
  const reg = /^[a-z]+$/
  return reg.test(str)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validUpperCase(str) {
  const reg = /^[A-Z]+$/
  return reg.test(str)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function validAlphabets(str) {
  const reg = /^[A-Za-z]+$/
  return reg.test(str)
}

/**
 * @param {string} email
 * @returns {Boolean}
 */
export function validEmail(email) {
  const reg = /^(([^<>()\\[\]\\.,;:\s@"]+(\.[^<>()\\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
  return reg.test(email)
}

/**
 * @param {string} str
 * @returns {Boolean}
 */
export function isString(str) {
  if (typeof str === 'string' || str instanceof String) {
    return true
  }
  return false
}

/**
 * @param {Array} arg
 * @returns {Boolean}
 */
export function isArray(arg) {
  if (typeof Array.isArray === 'undefined') {
    return Object.prototype.toString.call(arg) === '[object Array]'
  }
  return Array.isArray(arg)
}

// 是否特殊字符
export const hasSpecialCharacter = (str) =>
  new RegExp(
    '[`~!@#$^&*()=|{}\':;\',\\[\\].<>《》/?~！@#￥……&*（）——|{}【】‘；：”“\'。，、？ ]'
  ).test(str)

// 是否手机号
export const isPhoneNumber = (str) => /^1\d{10}$/.test(str)

// 是否不合法url
export const isIllegal = (url) => {
  return !validURL(url)
}

export const lessThan = (length) => (value) => value.length < length

export const required = (value) => {
  if (typeof value === 'undefined') {
    return true
  }
  // 过滤掉所有空格和回车
  if (
    typeof value === 'string' &&
    value.replace(/ /g, '').replace(/\n/g, '').length === 0
  ) {
    return true
  }
}

export const getValidators = (options) => {
  const validators = []
  options.forEach((option) => {
    const { include, exclude, ...others } = option
    validators.push({
      ...others,
      validator: (rule, value, callback) => {
        if (
          option.include?.(value) ||
          (option.exclude && !option.exclude(value))
        ) {
          callback(new Error(option.message))
        } else {
          callback()
        }
      },
      trigger: option.trigger || 'blur',
    })
  })
  return validators
}
// 获取添加单位的key
export const getUnitKey = (key) => {
  return key.slice(0, 1).toUpperCase() + key.slice(1)
}

