export const getPrefixKey = (key, prefix = 'm') => {
  return prefix + key
}

export const isNumString = (str) => /^[0-9]+.?[0-9]*$/.test(str)

export const requiredNumber = (value) => {
  if(isNaN(value)) {
    return true
  }
  if(value === 0) {
    return true
  }
  return false
}
