<template>
  <el-card
    class="card-widget"
    :class='commonClassName'
    :style='componentStyle'
    :shadow='shadow'
  >
    <div class='card-header' slot="header">
      <span class='card-title' v-if='title' @click='click'>{{title}}</span>
      <component
        v-if="showButtons"
        class="card-buttons"
        :is='buttons.slotElement'
        :ref="buttons.elementId"
        :data-zone="`${buttons.elementId}_${buttons.elementType}`"
        v-bind="buttons"
        empty-text="拖拽按钮到这里"
        :showLayoutType="false"
        :id="buttons.elementId"
      ></component>
    </div>
    <component
      class="card-body"
      :is='body.slotElement'
      :ref="body.elementId"
      :data-zone="`${body.elementId}_${body.elementType}`"
      v-bind="body"
      :id="body.elementId"
    ></component>
  </el-card>
</template>

<script>
  import { CLICK } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import _ from 'lodash'

  export default {
    mixins: [common],
    variables: {},
    computed: {
      buttons() {
        return this.getSlotByName('buttons')
      },
      body() {
        return this.getSlotByName('body')
      },
    },
    methods: {
      click(e) {
        this._emitEvent(CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
    },
  }
</script>

<style lang="less" scoped>
  .card-widget {
    .card-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .card-title {
        white-space: nowrap;
        &:hover {
          cursor: pointer;
        }
      }
    }
  }
</style>
