import {
  VERSION_INFO,
  VERSION_JUDGE
} from './mutation-types'
import {
  Message,
  MessageBox
} from 'element-ui'
import API from '@/api/application'
export default {
  namespaced: true,
  state: {
    // 当前编辑的版本信息
    [VERSION_INFO]: {}
  },
  getters: {},
  mutations: {
    [VERSION_INFO](state, data) {
      state[VERSION_INFO] = data
    }
  },
  actions: {
    /**
     * 判断及新建版本的方法
     * @param {context} context
     * @param {object}
     * {object} data 版本信息,不传则拿store中的
     * {bollean} reload 新建完是否刷新页面
     * {function} createCallBack 新建完执行的回调
     * @returns promise
     */
    async [VERSION_JUDGE](context, { data, reload = true, createCallBack = () => {}, message = '', isCopy = false } = {}) {
      const {
        status,
        appId,
        id,
        version
      } = data || context.state[VERSION_INFO]
      // 未上架并且不是复制模式可以继续保存
      if (status === 1 && !isCopy) {
        return Promise.resolve()
      }
      MessageBox.prompt(message || `当前版本${{ '2': '已上架', '3': '已下架' }[status]}，保存将直接生成新版本!`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        closeOnClickModal: false,
        showCancelButton: true,
        inputPattern: /^(V)\d+(?:\.\d+){1,2}$/,
        inputErrorMessage: '版本号格式不正确',
        inputPlaceholder: '请输入版本号(格式例如: V1.0 或 V1.0.0)',
        inputValue: version,
        beforeClose: (action, instance, done) => {
          if (action === 'confirm') {
            instance.confirmButtonLoading = true;
            instance.confirmButtonText = '加载中...';

            rootApp.$http({
              url: API.saveFromOtherDesign,
              data: {
                appId: appId,
                sourceAppDesignId: id,
                targetVersion: instance.inputValue
              }
            }).then(({
              data
            }) => {
              done();
              if(reload) {
                Message({
                  type: 'success',
                  message: '创建成功,即将跳转新版本',
                  duration: 1000
                });
                setTimeout(() => {
                  rootApp.$router.replace({
                    name: rootApp.$route.name,
                    query: {
                      id: data.appId,
                      vid: data.appDesignId
                    }
                  })
                  setTimeout(() => {
                    window.location.reload()
                  }, 0)
                }, 1000)
              }
              createCallBack()
            }).finally(() => {
              instance.confirmButtonLoading = false;
              instance.confirmButtonText = '确定';
            })
          } else {
            done();
          }
        }
      })
      return Promise.reject('版本已上架')
    },
  },
}