<template>
  <n-form-item
    ref="ElFormItem"
    class="cascader-widget"
    :class="commonClassName"
    :style="componentStyle"
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    :rules="rules"
    @click.native="handleClickFormItem"
  >
    <div class="container" v-show="state != 'hidden'">
      <el-cascader
        v-if="rendered"
        v-load-sync="fetchLoading"
        v-model="value"
        :style="{ width: widgetWidth }"
        :options="realOptions"
        :separator="separator"
        :clearable="clearable"
        :disabled="state == 'disabled'"
        :placeholder="placeholder"
        :size="size"
        :show-all-levels="showAllLevels"
        :collapse-tags="collapseTags"
        :filterable="filterable"
        :filter-method="filterMethod"
        :props="computedProps"
        @change="handleChange"
      />
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
// 带m的属性代表是动态可变的
// 不带m的则代表为静态
import common from '@/template/mixins/common'
import form from '@/template/mixins/form'
import options from '@/template/mixins/options'
import treeTool from 'tree-tool'
import { CHANGE } from '@/config/eventName'
export default {
  mixins: [common, form, options],
  variables: {
    $dataList(n) {
      if (Array.isArray(n)) {
        this.dataList = n
        this.handleFormatOptions(n)
      } else {
        console.warn('dataList数据格式必须为数组！')
      }
    }
  },
  watch: {
  },
  data() {
    return {
      rendered: true,
      filterMethod(node, value) {
        return node.label.toLocaleLowerCase().includes(value?.toLocaleLowerCase())
      }
    }
  },
  computed: {
    useLazy() {
      const { lazy, apiRequest, sqlRequest } = this
      // 有api数据集才开启动态加载
      return Boolean(lazy && (apiRequest || sqlRequest))
    },
    computedProps() {
      return {
        multiple: this.multiple,
        checkStrictly: this.checkStrictly,
        lazy: this.useLazy,
        lazyLoad: this.lazyLoad,
      }
    },
    rules() {
      const { required, regExp, label } = this
      const res = []
      if (required) {
        res.push(
          this.getTrueRule({
            required: true,
            message: `请选择${label}`,
            trigger: 'change',
            type: 'array'
          })
        )
      }
      return regExp?.reduce((prev, cur) => {
        return prev.concat({
          pattern: cur.reg,
          message: cur.errorTips,
        })
      }, res)
    },
  },
  methods: {
    async handleChange(value) {
      this.value = value
      await this.$nextTick()
      this.dispatchFormItem(this.$refs.ElFormItem, 'el.form.change', value)
      this._emitEvent(CHANGE)
    },
    initVal() {
      const treeUtil = treeTool.createInstance({
        id: 'id',
        children: 'children',
        pid: 'parentId'
      })
      if (this.useSlotProps && this.prop && this.slotProps) {
        this.value = this.slotProps[this.prop]
      } else {
        treeUtil.forEach(this.realOptions, (item) => {
          if (item.isDefault) {
            const value = item.parentsKeys
            if (this.multiple) {
              this.value.push(value)
            } else {
              this.value = value
            }
          }
        })
      }
      this.rendered = true
      this.nodeValue = ''
    },
    async lazyLoad(node, resolve) {
      const { root, data: { value = '' } = {} } = node
      // 根节点不需要加载
      if(root) {
        return
      }
      this.nodeValue = value
      setTimeout(async () => {
        const res = await this.fetchRequestData()
        resolve(res)
      }, 60)
    },
    receiveValue(value) {
      if(value === undefined) {
        // 自动上传的时候, value是一个数组,重置表单是置为空数组而不是undefined
        this.value = []
      } else if(value instanceof Array) {
        this.value = value.filter(value => value)
      }
      this.update2form(this.value)
    }
  },
  mounted() {},
}
</script>

<style lang="less" scoped>
.cascader-widget::v-deep {
  margin-left: 0;
  .container {
  }
  .bottom-tips {
    color: #aaa;
    font-size: 12px;
    line-height: 1;
    padding-top: 4px;
  }
}
</style>
