// 驼峰转连字符
import _ from 'lodash'
import { isValidJsonString, isNumString } from '@/utils/validateUtils'
import dayjs from 'dayjs'
import { parse, compile } from 'path-to-regexp'
import URI from 'urijs'
import { FUNCTION_FLAG } from '@/config'
import { getMapType, Message, filterXss, getSharePageParams, getAppVariables } from './index'
import qs from 'querystring'


export const hyphenate = (s) => {
  return s.replace(/([A-Z])/g, '-$1').toLowerCase()
}

// 连字符转驼峰
export function toHump(name) {
  return name.replace(/\-(\w)/g, function(all, letter) {
    return letter.toUpperCase()
  })
}

export function getPathList(path = '') {
  // let arr = path.split('.')
  // arr = arr.map((p) => p.replace('[', '.').replace(']', ''))
  // if (arr.some((p) => p.includes('.'))) {
  //   return getPathList(arr.join('.'))
  // }
  // return arr
  return _.toPath(path)
}

export const getPathString = (path) =>
  path
    .map((p) => (isNumString(p) ? `[${p}]` : p))
    .join('.')
    .replace('.[', '[')

export function getObjectTree(obj, isParseString = false) {
  function generateTree(obj, tree) {
    for (const k in obj) {
      if (_.isObject(obj[k])) {
        tree.push({
          label: k,
          value: k,
          children: generateTree(obj[k], []),
        })
      } else if (typeof obj[k] === 'string') {
        let res
        if (isParseString && isValidJsonString(obj[k])) {
          try {
            res = JSON.parse(obj[k])
          } catch (e) {
            console.log(e)
          }
        }
        if (res) {
          tree.push({
            label: k,
            value: k,
            children: generateTree(res, []),
          })
        } else {
          tree.push({
            label: k,
            value: k,
          })
        }
      } else {
        tree.push({
          label: k,
          value: k,
        })
      }
    }
    return tree
  }

  const tree = []
  generateTree(obj, tree)
  return tree
}

export function getMapTree(obj, rootPath, isParseString = false) {
  function generateTree(obj, tree, pathList) {
    for (const k in obj) {
      if (_.isObject(obj[k])) {
        if (Array.isArray(obj[k])) {
          tree.push({
            type: getMapType(obj[k]),
            key: k,
            reference: {
              name: getPathString([...pathList, k]),
            },
          })
        } else {
          tree.push({
            type: getMapType(obj[k]),
            key: k,
            reference: {
              name: getPathString([...pathList, k]),
            },
            children: generateTree(obj[k], [], [...pathList, k]),
          })
        }
      } else if (typeof obj[k] === 'string') {
        let res
        if (isParseString && isValidJsonString(obj[k])) {
          try {
            res = JSON.parse(obj[k])
          } catch (e) {
            console.log(e)
          }
        }
        if (res) {
          if (Array.isArray(res)) {
            tree.push({
              type: getMapType([]),
              key: k,
              reference: {
                name: getPathString([...pathList, k]),
              },
            })
          } else {
            tree.push({
              type: getMapType({}),
              key: k,
              reference: {
                name: getPathString([...pathList, k]),
              },
              children: generateTree(res, [], [...pathList, k]),
            })
          }
        } else {
          tree.push({
            type: getMapType(obj[k]),
            key: k,
            reference: {
              name: getPathString([...pathList, k]),
            },
          })
        }
      } else {
        tree.push({
          type: getMapType(obj[k]),
          key: k,
          reference: {
            name: getPathString([...pathList, k]),
          },
        })
      }
    }
    return tree
  }

  const tree = []
  const pathList = [rootPath]
  generateTree(obj, tree, pathList)
  return tree
}

export const getPathFrom = (key, from) => {
  return from
    .find((d) => d.key === key)
    ?.path.map((p) => (isNumString(p) ? `[${p}]` : p))
    .join('.')
    .replace('.[', '[')
}

export function formatTime(time, template = 'YYYY/MM/DD HH:mm:ss') {
  return dayjs(time).format(template)
}

export function getApiRequestParams(req) {
  const uri = new URI(req.url)
  const pathString = uri.pathname()
  let dynamicPathArray = []
  try {
    dynamicPathArray = parse(pathString)
      .filter((item) => _.isObject(item))
      .map((item) => item.name)
  } catch (e) {
    console.log(e)
    dynamicPathArray = []
  }
  const bodyData = req.bodyData.filter((d) => d.key)
  const paramsData = req.paramsData.filter((d) => d.key)
  const headersData = req.headersData.filter((d) => d.key)
  const method = req.method
  const dynamicPath = Object.fromEntries(
    dynamicPathArray.map((key) => {
      const current = req.paramsData.find((d) => d.key === key)
      return [key, `${current?.test}`]
    })
  )
  const realPathString = compile(pathString)(dynamicPath)
  let url = uri.origin() + realPathString
  // 始终转换为当前域的网关
  const protocol = window.location.protocol
  const host = window.location.host
  if (req.type === 4 && url) {
    const uri = new URI(url)
    const pre = `${uri.origin()}/gateway`
    if (uri.origin()) {
      const current = `${protocol}//${host}/gateway`
      url = url.replace(pre, current)
    }
  }
  if (req.type === 5 && url) {
    const uri = new URI(url)
    const pre = `${uri.origin()}/app-data`
    const current = '/app-data'
    if (uri.origin()) {
      url = url.replace(pre, current)
    }
    const {appId: instanceId} = getSharePageParams()
    const search = window.location.search
    const { appId: queryAppId, id } = qs.parse(search.replace('?', ''))
    const currentAppId = queryAppId || id || instanceId
    const appPath = url.replace(`${current}/`, '').split('/')[0]
    const newAppPath = `app${currentAppId}`
    url = url.replace(appPath, newAppPath)
  }
  if (['script', 'orchestration', 'sncflower', 6].includes(req.type) && url) {
    const uri = new URI(url)
    const pre = uri.origin()
    if (!pre) {
      const current = `${protocol}//${host}`
      url = `${current}${url}`
    }
  }
  if ([6].includes(req.type) && req.useDynamicOrigin) {
    try {
      const pageId = this.getStore().layoutData.elementId
      const uri = new URI(url)
      const appVariables = getAppVariables() || {}
      const appVariablesKey = req.dynamicOrigin.replace(`${pageId}.$appVariables.`, '')
      let dynamicUrl = `${_.get(appVariables, appVariablesKey)}`
      if (dynamicUrl.endsWith('/')) {
        dynamicUrl = dynamicUrl.slice(0, -1)
      }
      const dynamicUri = new URI(dynamicUrl)
      const dynamicOrigin = dynamicUri.origin()
      const dynamicDir = dynamicUri.pathname()
      const preDir = uri.pathname()
      url = `${dynamicOrigin}${dynamicDir}${preDir}`
    } catch (e) {
      setTimeout(() => {
        Message({
          type: 'error',
          message: 'path路径解析失败，正确格式应为/aaa/bbb'
        })
      }, 500)
    }
  }
  const data = Object.fromEntries(
    bodyData.map((d) => {
      return [d.key, d.test]
    })
  )
  const headers = Object.fromEntries(
    headersData.map((d) => {
      return [d.key, d.test]
    })
  )
  if (['script', 'orchestration', 'sncflower', 5].includes(req.type)) {
    const loginKey = window.localStorage.getItem('loginKey')
    const sncToken = JSON.parse(loginKey)?.['snc-token']
    headers['snc-token'] = sncToken
  }
  const params = Object.fromEntries(
    paramsData
      .filter((d) => !dynamicPathArray.includes(d.key))
      .map((d) => {
        return [d.key, d.test]
      })
  )
  if (headers['Content-Type'] === 'multipart/form-data') {
    const formData = new FormData()
    Object.keys(data).forEach((key) => {
      formData.append(key, data[key])
    })
    delete headers['Content-Type']
    return {
      url,
      method,
      data: formData,
      params,
      headers,
    }
  }
  return {
    url,
    method,
    data,
    params,
    headers,
  }
}

// 获取格式化后的数据
export function getFormatValue({
  formatter, // 格式化配置对象
  context, // vue实例this
  value, // 原数据
  row, // 表格行数据
}) {
  let formatValue
  if (formatter.type === 'js') {
    switch (formatter.functionName) {
      case 'cellValueFormat': {
        const functionId = formatter.value.split(FUNCTION_FLAG)[1]
        const functionsData =
          context.getStore().functionsData
        const currentFunction = functionsData.find(
          (item) => item.id === functionId
        )
        if (currentFunction) {
          const { paramsData, code } = currentFunction
          const data = paramsData.filter((d) => d.key)
          const params = Object.fromEntries(
            data.map((d) => {
              if (d.key === 'cellValue') {
                return [d.key, value]
              }
              if (d.key === 'rowData') {
                return [d.key, row]
              }
              return [d.key, context.getObjectVariables(d.value)]
            })
          )
          const args = Object.keys(params)
          const values = Object.values(params)
          try {
            // todo: js 沙箱机制
            const func = new Function(...args, code)
            formatValue = func(...values)
            if (formatter.renderType === 'html') {
              formatValue = filterXss(formatValue)
            }
          } catch (e) {
            console.log(e)
          }
        }
        break
      }
    }
  }
  if (formatter.type === 'number') {
    switch (formatter.functionName) {
      case 'fixNumber': {
        const { fixed } = formatter
        formatValue = Number.parseFloat(`${value}`).toFixed(fixed)
        break
      }
    }
  }
  if (formatter.type === 'string') {
    switch (formatter.functionName) {
      case 'enumeration': {
        const { enumeration } = formatter
        const current = enumeration.find((item) => item.value == value)
        formatValue = current ? current.label : value
        break
      }
      case 'exec': {
        const { regexp, isI, isM } = formatter
        const flag = `${isI ? 'i' : ''}${isM ? 'm' : ''}` || undefined
        const reg = new RegExp(regexp, flag)
        const result = reg.exec(value)
        formatValue = result?.[0] || ''
        break
      }
    }
  }
  if (formatter.type === 'date') {
    switch (formatter.functionName) {
      case 'dateFormat': {
        const { valueFormat } = formatter
        formatValue = formatTime(value, valueFormat)
        break
      }
    }
  }
  return formatValue
}
