<template>
  <div
    v-loading="!chartInstance && fetchLoading"
    ref="chart"
    class="chart-content"
    :class="commonClassName"
  ></div>
</template>

<script>
import { CLICK, RESET } from '@/config/eventName'
import common from '@/template/mixins/common'
import chart from '@/template/mixins/chart'

import _ from 'lodash'

export default {
  mixins: [common, chart],
  variables: {
    $chartData(newV) {
      this.staticData = newV
      this[RESET](false)
    },
  },
  computed: {},
  watch: {},
  data() {
    return {
      seriesData: [],
      xAxis: [],
    }
  },
  mounted() {},
  created() {},
  methods: {
    getOption() {
      const option = this.getBaseOption()
      const {
        getLengthValue,
        innerRadius,
        outerRadius,
        startAngle,
        xSetting,
        splitNumber,
        useGridSetting,
        splitArea,
        splitLine,
        axisLine,
        cleanMode,
      } = this
      option.radar = {
        name: {
          show: xSetting.dataLabel,
          formatter: xSetting.dataLabelUseformatter
            ? xSetting.dataLabelFormatter
            : undefined,
        },
        shape: this.type,
        splitNumber,
        splitArea: {
          show: !cleanMode && useGridSetting && splitArea,
        },
        splitLine: {
          show: !cleanMode && useGridSetting && splitLine,
        },
        axisLine: {
          show: !cleanMode && useGridSetting && axisLine,
        },
        radius: [getLengthValue(innerRadius), getLengthValue(outerRadius)],
        startAngle: getLengthValue(startAngle),
        indicator: this.xAxis.map(({ name, max, useMax }) => {
          const result = { name }
          if (useMax) {
            result.max = max
          }
          return result
        }),
      }
      return option
    },
    // 初始化监听事件
    initEvents() {
      //点击
      this.chartInstance.on('click', (e) => {
        const { name, value } = e.data
        const result = { name, value }
        this.dotData = result
        this._emitEvent(CLICK)
        if (!this.isEditMode && this.stopPropagation) {
          e.event.event.stopPropagation()
        }
      })
    },
    getSeries() {
      const {
        cleanMode,
        ySetting: {
          useSymbol,
          symbolSize,
          dataLabelShow,
          dataLabelPosition,
          dataLabelUseformatter,
          dataLabelFormatter,
        } = {},
        areaStyle,
        areaStyleOpacity,
      } = this
      const size = {
        small: 5,
        medium: 10,
        large: 15,
      }[symbolSize]

      // 配置
      let setting = {}

      // 关闭了标记点
      if (!useSymbol) {
        Object.assign(setting, { symbol: 'none' })
      } else {
        Object.assign(setting, { symbolSize: size })
      }
      // 区域填充
      if (!cleanMode && areaStyle) {
        Object.assign(setting, {
          areaStyle: {
            opacity: areaStyleOpacity,
          },
        })
      }
      return [
        {
          ...setting,
          name: '预算 vs 开销（Budget vs spending）',
          type: 'radar',
          label: {
            show: dataLabelShow,
            position: dataLabelPosition,
            formatter: dataLabelUseformatter ? dataLabelFormatter : undefined,
          },
          data: !useSymbol
            ? this.seriesData.map((item) => ({ ...item, symbol: 'none' }))
            : this.seriesData,
        },
      ]
    },
    formatRequestData(response, dispose = true) {
      this.responseData = response
      const { dataVariables: data, dataType, dynamicVariables } = this
      this.xAxis = data.find((item) => item.key === 'xAxis')?.fields
      let dataPath,
        yAxis,
        result = []

      // 动态图例, 计算出xAxisKey,yAxis
      if (dataType === 'dynamic') {
        let legendName, legendData
        dynamicVariables.forEach((item) => {
          switch (item.key) {
            case 'sourcePath':
              dataPath = item.path
              break
            case 'legendName':
              legendName = item.path[item.path.length - 1]
              break
            case 'legendData':
              legendData = item.path[item.path.length - 1]
              break
          }
        })
        const xAxisKey = 'lowCode_xAxisFiledKey'
        let res
        // 有响应数据就用响应数据
        if (response && dataPath) {
          res = _.get({ response }, dataPath)
        }
        // 没有就用静态数据
        else {
          res = this.staticData
        }
        const yAxisMap = data.find((item) => item.key === 'yAxis')?.fields || []
        yAxis = []
        res.forEach((item) => {
          const name = item[legendName]
          // 没有图例名称
          if(!name) {
            return
          }
          // 相同key映射
          const mapField = yAxisMap.find(opt => opt.key === name) || {}
          yAxis.push({
            ...mapField,
            key: name,
            value: mapField.value || name
          })
          // 构建长度为图例数据长度, key为图例名称, value为图例数据第二项 [{key: value}]的数组
          let i = 0
          for (const key in item[legendData]) {
            const value = item[legendData][key]
            result[i] = result[i] || {}

            // 数组格式
            if (value instanceof Array) {
              result[i][xAxisKey] = value[0]
              result[i][name] = value[1]
            }
            // { key: value } 格式
            else {
              result[i][xAxisKey] = key
              result[i][name] = value
            }
            i++
          }
        })
      } else {
        dataPath = data.find((item) => item.key === 'sourcePath')?.path
        yAxis = data.find((item) => item.key === 'yAxis')?.fields

        // 有响应数据就用响应数据
        if (response && dataPath) {
          result = _.get({ response }, dataPath)
        }
        // 没有就用静态数据
        else {
          result = this.staticData
        }
      }
      // 赋值给可暴露属性
      this.chartData = result
      this.seriesData = yAxis.map((item) => {
        const { value, key, ...others } = item
        return {
          name: value || key,
          value: result.map((item) => item[key]),
          ...others,
        }
      })
      this.renderChart(dispose)
    },
  },
}
</script>

<style lang="less" scoped>
@import '~@/styles/index.less';

.chart-content {
  width: 100%;
  height: 400px;
}
</style>
