<template>
  <div class="page" :class="{ 'has-child': hasChild, ...commonClassName }" :style='componentStyle'>
    <layout-placeholder
      :elementId="elementId"
      :elementType="elementType"
      v-if="isShowPlaceholder"
      :showLayoutType="true"
    ></layout-placeholder>
    <template v-for="child in visibleChildren">
      <component
        :key="child.elementId"
        :is='getElement(child.elementType, child.elementVersion)'
        :ref="child.elementId"
        :data-zone="`${child.elementId}_${child.elementType}`"
        v-bind="child"
        :id="child.elementId"
      ></component>
    </template>
  </div>
</template>

<script>
  import common from '@/template/mixins/common'
  import { HTTP_REQUEST } from '@/config/eventName'
  import _ from 'lodash'
  import request from '@/request/request'
  import { isValidJsonString } from '@/utils/validateUtils'
  import triggerTarget from '@/renderer/tools/trigger'
  import { getCookies, getFileNameFromHeaders, isValidateVariable, getImageUrl, addCssByStyle, setAppVariables } from '@/utils'
  import { treeUtil } from '@/utils/tree-utils'
  import styleToCss from 'style-object-to-css-string'
  import { pageInject } from '@/renderer/tools/pageStore'
  import API from '@/api/application'
  import { getCommonHeaders } from '@/utils'
  export default {
    inject: [...pageInject],
    mixins: [common],
    variables: {
      $appVariables() {
        setAppVariables(this.appVariables)
      }
    },
    data() {
      return {
        response: {},
        localStorage: {},
        sessionStorage: {},
        cookie: {}
      }
    },
    computed: {
      isShowPlaceholder() {
        return this.isEditMode && !(
          this.children.some(
            (child) =>
              this.getElementPropsById(child.elementId).isVisible !== false &&
              !['dialog-container', 'timer-container'].includes(child.elementType)
          )
        )
      },
    },
    methods: {
      // 请求API
      async [HTTP_REQUEST]({ apiRequest, sqlRequest, callback, loadingConfig, emitParams, responseConfig, eventName }) {
        const successCallback = callback.filter(
          (item) => item.type === 'success'
        )
        const errorCallback = callback.filter((item) => item.type === 'error')
        if (apiRequest) {
          let params = this.getApiRequestParams(apiRequest, emitParams)
          if (!params.url) {
            return
          }
          try {
            switch(apiRequest.type) {
              case 'script': {
                params.data.params.entity.layoutId = await this.handleGetScriptId(apiRequest)
                break
              }
              case 'orchestration': {
                params.data.params.orchestrationId = await this.handleGetOrchestrationId(apiRequest)
                break
              }
              case 'sncflower': {
                params.data.params.workflowRunBean.workflowId = await this.handleGetWorkflowId(apiRequest)
                break
              }
            }
          } catch (e) {
            return
          }
          const loading = (!loadingConfig || loadingConfig?.enable) ? this.$loading({
            lock: !!loadingConfig?.showMask,
            text: loadingConfig?.text,
            background: loadingConfig?.showMask ? loadingConfig?.background || 'transparent' : 'transparent',
            customClass: loadingConfig?.showMask ? '' : 'events-none'
          }) : false

          const executeResult = {
            children: [],
            pid: triggerTarget.getUnionKey()
          }

          request({
            method: params.method,
            url: params.url,
            params: params.params,
            data: params.data,
            headers: params.headers,
            responseType: responseConfig?.isBlob ? 'blob' : 'json',
          })
            .then((res) => {
              if (responseConfig?.isBlob) {
                const fileName = getFileNameFromHeaders(res.headers)
                const name = responseConfig?.isUseHeaderFileName ? fileName : isValidateVariable(responseConfig?.fileName) ? this.getObjectVariables(responseConfig?.fileName) : responseConfig?.fileName || 'newFile'
                const url = URL.createObjectURL(res.data)
                const aLink = document.createElement('a')
                aLink.download = name
                aLink.href = url
                aLink.dispatchEvent(new MouseEvent('click', {}))
              }
              executeResult.children.push(triggerTarget.formatLinkMsg({ successText: '成功回调', tagList: { response: res } }))
              this.handleCallback(res.data, successCallback).then(callback => {
                callback.forEach(item => executeResult.children.push(item))
              }).finally(() => {
                triggerTarget.recordLog(executeResult)
              })
            })
            .catch((e) => {
              executeResult.children.push(triggerTarget.formatLinkMsg({ errorText: '失败回调', tagList: { response: e } }))
              this.handleCallback(e, errorCallback).then(callback => {
                callback.forEach(item => executeResult.children.push(item))
              }).finally(() => {
                triggerTarget.recordLog(executeResult)
              })
            })
            .finally(() => {
              loading && loading.close()
            })
            const typeNameMap = {
              script: '操作',
              orchestration: '编排',
              sncflower: '调度'
            }
            return triggerTarget.formatLinkMsg({
              eventName,
              type: typeNameMap[apiRequest.type] ? `调用自动化运维操作-${typeNameMap[apiRequest.type]}` : '调用API',
              id: executeResult.pid,
              tagList: {
                method: params.method,
                url: params.url,
                params: params.params,
                data: params.data,
                headers: params.headers,
                responseType: responseConfig?.isBlob ? 'blob' : 'json',
              },
            })
        }
        if (sqlRequest) {

          const params = this.getSqlParams(sqlRequest, emitParams)
          const loading = (!loadingConfig || loadingConfig?.enable) ? this.$loading({
            lock: !!loadingConfig?.showMask,
            text: loadingConfig?.text,
            background: loadingConfig?.showMask ? loadingConfig?.background || 'transparent' : 'transparent',
            customClass: loadingConfig?.showMask ? '' : 'events-none'
          }) : false
          const executeResult = {
            children: [],
            pid: triggerTarget.getUnionKey()
          }
          this.$http({
            url: params.url,
            data: triggerTarget.formatSQLParams(params.data, executeResult.pid),
          })
            .then((res) => {
              triggerTarget.recordSqlResponse(executeResult.pid)
              executeResult.children.push(triggerTarget.formatLinkMsg({ successText: '成功回调', tagList: { response: res } }))
              this.handleCallback(res, successCallback).then(callback => {
                callback.forEach(item => executeResult.children.push(item))
              }).finally(() => {
                triggerTarget.recordLog(executeResult)
              })
              res = null
            })
            .catch((e) => {
              triggerTarget.recordSqlResponse(executeResult.pid)
              executeResult.children.push(triggerTarget.formatLinkMsg({ errorText: '失败回调', tagList: { response: e } }))
              this.handleCallback(e, errorCallback).then(callback => {
                callback.forEach(item => executeResult.children.push(item))
              }).finally(() => {
                triggerTarget.recordLog(executeResult)
              })
            })
            .finally(() => {
              loading && loading.close()
            })


          return triggerTarget.formatLinkMsg({ eventName, type: '调用SQL', id: executeResult.pid })
        }
      },
      async handleCallback(res, callbacks) {
        let response = {...this.response, ...Object.fromEntries(callbacks.map(callback => [callback.id, res]))}
        this.response = _.cloneDeep(response)
        response = null
        const logs = await triggerTarget.triggerCallback(callbacks, this)
        return logs
      },
      // 预览模式一次性生成整个页面的样式
      updatePageStaticStyle() {
        const pageId = this.elementId
        let styleTextList = []
        treeUtil.forEach([this.getStore().layoutData], node => {
          const { styleConfigList = [] } = this.getElementPropsById(node.elementId)
          const styleText = styleConfigList.map(item => {
            const { cssText, ...styleObject  } = JSON.parse(JSON.stringify(item.styles))
            if (!cssText && Object.keys(styleObject).length === 0) {
              return ''
            }
            if (styleObject.backgroundImage) {
              const isPic = new RegExp(/\w(\.gif|\.jpeg|\.png|\.jpg|\.bmp)/i).test(styleObject.backgroundImage || '')
              styleObject.backgroundImage = isPic ? `url(${getImageUrl(styleObject.backgroundImage)})` : styleObject.backgroundImage
            }
            const styleString = `${styleToCss(styleObject) || ''}${cssText || ''}`
            return `#${node.elementId === pageId ? `page_${pageId}` : node.elementId}${item.query}{${styleString}}`
          }).join('')
          styleTextList.push(styleText)
        })
        const id = `static-style_${this.elementId}`
        addCssByStyle(id, styleTextList.join(''))
      },
      // 清除所有样式
      clearAllComponentStyle() {
        const staticStyleList = document.querySelectorAll(`style[id^="static-style_${this.elementId}"]`)
        const dynamicStyleList = document.querySelectorAll(`style[id^="dynamic-style_${this.elementId}"]`)
        staticStyleList.forEach(item => item.remove())
        dynamicStyleList.forEach(item => item.remove())
      },
      handleGetScriptId(apiRequest) {
        return new Promise((resolve, reject) => {
          request({
            url: API.atmScriptList,
            method: 'POST',
            data: {
              params: {
                condition: { isBuilt: apiRequest.apiDetail?.isBuilt, scriptName: apiRequest.apiDetail?.scriptName },
                pagination: { pagenum: 1, pagesize: 1 },
              },
            },
            headers: getCommonHeaders(),
          })
            .then(({data}) => {
              resolve(data.data.records[0].scriptId)
            })
            .catch((e) => {
              reject(e)
            })
        })
      },
      handleGetOrchestrationId(apiRequest) {
        return new Promise((resolve, reject) => {
          request({
            url: API.atmOrchestrationList,
            method: 'POST',
            data: {
              params: {
                condition: { executeType: 1, name: apiRequest.apiDetail?.name },
                pagination: { pagenum: 1, pagesize: 1 },
              },
            },
            headers: getCommonHeaders(),
          })
            .then(({data}) => {
              resolve(data.data.records[0].orchestrationId)
            })
            .catch((e) => {
              reject(e)
            })
        })
      },
      handleGetWorkflowId(apiRequest) {
        return new Promise((resolve, reject) => {
          request({
            url: API.atmSncflowerList,
            method: 'POST',
            data: {
              params: {
                condition: { name: apiRequest.apiDetail?.name },
                pagination: { pagenum: 1, pagesize: 1 },
              },
            },
            headers: getCommonHeaders(),
          })
            .then(({data}) => {
              resolve(data.data.records[0].id)
            })
            .catch((e) => {
              reject(e)
            })
        })
      },
    },
    created() {
      const dynamicVariables = this.getElementPropsById(this.elementId).dynamicVariables || {}
      const keys = Object.entries(dynamicVariables).filter(([key, value]) => value.isEntry).map(([key, value]) => key)
      const query = this.$route.query
      const newValue = Object.fromEntries(keys.map(key => [key, {...dynamicVariables[key], value: query[key] || dynamicVariables[key].value}]))
      if (!this.isEditMode) {
        this.dynamicVariables = {
          ...dynamicVariables,
          ...newValue
        }
      }

      const storageData = Object.entries(window.localStorage).map(([key, value]) => {
        if (value && isValidJsonString(value)) {
          return [key, JSON.parse(value)]
        }
        return [key, value]
      })
      const sessionData = Object.entries(window.sessionStorage).map(([key, value]) => {
        if (value && isValidJsonString(value)) {
          return [key, JSON.parse(value)]
        }
        return [key, value]
      })
      this.localStorage = Object.fromEntries(storageData)
      this.sessionStorage = Object.fromEntries(sessionData)
      this.cookie = getCookies()
    },
    mounted() {
      if (!this.isEditMode) {
        this.updatePageStaticStyle()
      }
    },
    beforeDestroy() {
      this.clearAllComponentStyle()
    }
  }
</script>

<style lang="less" scoped>
  .page::v-deep {
    box-sizing: border-box;
    height: 100%;
    padding: 10px;

    &.has-child {
      height: auto;
      min-height: 100%;
    }

    & > .layout-placeholder {
      height: 100%;
    }
  }
</style>
