<template>
  <div
    class="component-dialog"
    :class='commonClassName'
    v-if="isEditMode"
    :id='elementId'
    :style="{ width: width, margin: `${top || '0'} auto 0` }"
  >
    <div class="component-dialog-header">
      <div class="dialog-title">{{ title }}</div>
      <div class="dialog-close-btn">
        <i class="iconfont icon-liuchengtu-guanbi" v-if="showClose"></i>
      </div>
    </div>
    <component
      class="component-dialog-content"
      :is='body.slotElement'
      :ref="body.elementId"
      :data-zone="`${body.elementId}_${body.elementType}`"
      v-bind="body"
      :id="body.elementId"
    ></component>
    <component
      v-if='showFooter !== false'
      :showLayoutType="false"
      class="component-dialog-footer"
      :is='footer.slotElement'
      :ref="footer.elementId"
      :data-zone="`${footer.elementId}_${footer.elementType}`"
      v-bind="footer"
      :id="footer.elementId"
    ></component>
  </div>
  <el-dialog
    v-else
    :close-on-click-modal="false"
    :modal-append-to-body="true"
    :append-to-body="true"
    :title="title"
    :visible.sync="dialogProvide.visible"
    :width="width"
    :top="top"
    :modal="modal"
    :show-close="showClose"
    @close="handleClose"
  >
    <component
      class="component-dialog-content"
      :is='body.slotElement'
      :ref="body.elementId"
      :data-zone="`${body.elementId}_${body.elementType}`"
      v-bind="body"
      :id="body.elementId"
    ></component>
    <component
      v-if='showFooter !== false'
      class="dialog-footer"
      slot="footer"
      :is='footer.slotElement'
      :ref="footer.elementId"
      :data-zone="`${footer.elementId}_${footer.elementType}`"
      v-bind="footer"
      :id="footer.elementId"
    ></component>
  </el-dialog>
</template>

<script>
  import { CLOSE, OPEN } from '@/config/eventName'
  import common from '@/template/mixins/common'

  export default {
    components: {
    },
    mixins: [common],
    variables: {},
    data() {
      return {
        dialogProvide: {
          visible: false,
        }
      }
    },
    computed: {
      body() {
        return this.getSlotByName('body')
      },
      footer() {
        return this.getSlotByName('footer')
      },
    },
    provide() {
      return {
        dialogProvide: this.dialogProvide
      }
    },
    methods: {
      async handleClose() {
        await this[CLOSE]()
        this._emitEvent(CLOSE)
      },
      async [OPEN]() {
        this.dialogProvide.visible = true
        await this.$nextTick()
        this._emitEvent(OPEN)
      },
      async [CLOSE]() {
        this.dialogProvide.visible = false
        await this.$nextTick()
      },
    },
    mounted() {},
  }
</script>

