  .component-dialog::v-deep {
    padding: 2px;
    background-color: #fff;
    box-shadow: 0 1px 3px rgb(0, 0, 0, 0.3);

    .component-dialog-header {
      display: flex;
      align-items: center;
      justify-content: space-between;
      width: 100%;
      height: 40px;
      padding: 10px;
      background: #f1f3f6;

      .dialog-title {
        font-size: 14px;
        color: #333333;
        letter-spacing: 0;
        font-weight: 700;
      }

      .dialog-close-btn {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 20px;
        height: 20px;
        cursor: pointer;

        .iconfont {
          font-size: 12px;
        }
      }
    }

    .component-dialog-content {
      padding: 10px;
      width: 100%;
    }

    .component-dialog-footer {
      padding: 10px;
      margin-top: 4px;

      & > .slot-widget {
        display: flex;
        align-items: center;
        justify-content: flex-end;
        width: 100%;
      }
    }
  }
</style>
