<template>
  <div class="layout" :class='commonClassName' :style='layoutStyle' @click='click' @dblclick="dblclick">
    <layout-placeholder
      :elementId="elementId"
      :elementType="elementType"
      v-if="!visibleChildren.length && isEditMode"
    ></layout-placeholder>
    <template v-for="child in visibleChildren">
      <component
        :key="child.elementId"
        :is='getElement(child.elementType, child.elementVersion)'
        :ref="child.elementId"
        :data-zone="`${child.elementId}_${child.elementType}`"
        v-bind="{...child, slotProps}"
        :id="child.elementId"
      ></component>
    </template>
  </div>
</template>

<script>
  import common from '@/template/mixins/common'
  import { CLICK, DOUBLE_CLICK } from '@/config/eventName'

  export default {
    props: {
      containerScopeStyle: {
        type: Object,
        default: () => ({})
      }
    },
    mixins: [common],
    variables: {},
    computed: {
      layoutStyle() {
        const { span } = this
        return {...this.containerScopeStyle, ...this.componentStyle, gridArea: this.getGridArea(span)}
      }
    },
    methods: {
      getGridArea(span) {
        return `span 1 / span ${span ?? 12} / auto / auto`
      },
      click(e) {
        this._emitEvent(CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
      dblclick(e) {
        this._emitEvent(DOUBLE_CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
    },
  }
</script>

<style lang="less" scoped>
  .layout::v-deep {
    box-sizing: border-box;
    &.is-edit-mode {
      padding: 1px;
    }
  }
</style>
