import API from '@/api/application'

import moment from 'moment'
import { formatSetting } from '@/components/settingsManage/config'
class TriggerTarget {
  constructor() {
    this.elementInstances = {}
    // 日志信息
    this.logBuffer = []
  }
  /**
     *  eventsData: {
     *     [button]: {
     *         click: [{
     *             name: '点击',
     *             binds: {
     *                 [input]: {
     *                     variable: {
     *                         '$size': '$value'
     *                     },
     *                     fns: [{name, params}]
     *                 },

     *             },
     *             condition: [{child: [{},{}]},{child: [{},{}]}]
     *         }],
     *     }
     * }
     * 触发组件
     * @param {*} elementId
     */
  async triggerComponents({ eventName, elementId, args, vueThis, logId }) {
    elementId = elementId === 'app' ? vueThis.getStore().layoutData.elementId : elementId
    const eventData = this.getEventsData(elementId, vueThis) || {}
    if (Object.keys(eventData).length && eventName) {
      const eventsData = eventData[eventName] || []
      const time = new Date()
      const result = await this.execeteFns(eventsData, args, vueThis)
      // 开启了记录,并且该事件下有配置事件处理才去执行下面的记录
      if (this.isOpenLog() && eventsData.length) {
        const target = this.getElementProps(elementId, vueThis)
        const {
          elementType,
          elementVersion
        } = target
        const config = getConfigByKey.call(vueThis, {
          elementId,
          elementType,
          elementVersion
        })
        const name = config.events.concat([{
            eventName: MOUNT,
            name: '组件初始化',
          },
          {
            eventName: DESTROY,
            name: '组件销毁'
          }
        ]).find(item => item.eventName === eventName) ?.name
        const data = {
          pid: logId, // 有logId, 就证明有父日志, 记录pid
          tag: `${name}(${eventName})`,
          message: '触发事件',
          children: result,
          target,
          time,
        }
        this.recordLog(data)
      }
    }
  }

  // 执行所有方法
  async execeteFns(eventsData, args, vueThis) {
    const result = []
    for (const eventItem of eventsData) {
      const bindObj = eventItem.binds
      const eventName = eventItem.name

      // 事件未被禁止
      if (!eventItem.disabled) {
        const conditionResult = this.checkCondition({ res: eventItem.condition, args, vueThis })
        // 条件通过
        if (conditionResult.valid) {
          if (Object.keys(bindObj).length) {
            // 循环处理每个组件触发
            for (const triggerElementId in bindObj) {
              const realTriggerId = triggerElementId === 'app' ? vueThis.getStore().layoutData.elementId : triggerElementId
              // 方法之间加个延期
              await sleep(0)
              const fnRes = await this.hanlderFns({
                ...bindObj[triggerElementId],
                elementId: realTriggerId,
                args,
                eventName,
                vueThis
              })
              fnRes.forEach(item => {
                if (conditionResult.tagList && conditionResult.tagList.length) {
                  item.tagList = conditionResult.tagList.concat(item.tagList || [])
                  item.successText = item.successText ? item.successText + ',' + '执行条件通过' : '执行条件通过'
                }
                result.push(item)
              })
            }
          } else {
            result.push(this.formatLinkMsg({
              eventName,
              type: '其他错误',
              errorText: '事件配置不完整',
            }))
          }
        } else {
          result.push(this.formatLinkMsg({
            eventName,
            successText: '执行条件不通过',
            successTextType: 'danger',
            tagList: conditionResult.tagList
          }))
        }
      } else {
        result.push(this.formatLinkMsg({
          eventName,
          successText: '事件已被禁止',
          successTextType: 'danger',
        }))
      }
    }
    return result
  }

  /**
   * 执行方法
   * @param {array} fns 方法
   * @param {object} variable 变量
   * @param {string} elementId 要触发的组件id
   * return {promise}
   */
  async hanlderFns({
    fns,
    variable,
    elementId,
    args,
    eventName,
    vueThis
  }) {
    const result = []
    for (const fn of fns) {
      // 是公共方法,执行公共方法
      if (isCommonFn(fn.name)) {
        const fnRes = await this.handlerCommonFns({
          elementId,
          fns,
          variable,
          args,
          eventName,
          vueThis
        })
        fnRes.forEach(res => {
          result.push(res)
        })
      }
      // 非公共方法,即组件暴露的方法,发起组件通信
      else {
        const target = this.getElementProps(elementId, vueThis)
        const {
          elementVersion,
          elementType
        } = target

        const config = getConfigByKey.call(vueThis, {
          elementId,
          elementType,
          elementVersion
        })

        const fnRes = await this.triggerComponentFn({
          fns,
          elementId,
          args,
          eventName,
          target,
          config
        })
        fnRes.forEach(res => {
          result.push(res)
        })
      }
    }
    return result
  }

  // 触发回调
  async triggerCallback(eventsData, vueThis) {
    const result = await this.execeteFns(eventsData, [], vueThis)
    return result
  }

  async handlerCommonFns({
    elementId,
    fns,
    variable,
    args,
    eventName,
    vueThis
  }) {
    const result = []
    for (const {
        name,
        params
      } of fns) {
      // 变量赋值
      if (name === COMMON_FN_ASSIGN) {
        const fnRes = await this.handlerVariable({
          variable,
          elementId,
          args,
          eventName,
          vueThis
        })
        result.push(fnRes)

      }
      // 其他方法
      else if (actions[name] && typeof actions[name] === 'function') {
        const id = this.getUnionKey()
        await actions[name].call(vueThis, {
          params,
          elementId,
          args,
          logId: id
        })
        result.push(this.formatLinkMsg({
          eventName,
          type: nameMap[name],
          target: this.getElementProps(elementId, vueThis),
          id
        }))
      }
    }
    return result
  }
  /**
   * 调用组中的方法
   * @param {array} fns - 被调用的组件中的方法
   * @param {string} elementId - 被调用组件的elementId
   */
  async triggerComponentFn({
    fns,
    elementId,
    args,
    eventName,
    target,
    config
  }) {
    const result = []
    const _this = this.getRef(elementId)
    // 调用绑定函数
    for (const {
        name,
        params,
        execute_fns = []
      } of fns) {
        let log = {}
        let res
        // 不是“页面”组件时
        if (!(target.elementType === 'page' && name === HTTP_REQUEST)) {
          const formatName = config.fns.concat(COMMON_FNS).find(item => item.fn === name) ?.name
          log = this.formatLinkMsg({
            eventName,
            type: formatName,
            message: `被调用了方法 “${formatName}”`,
            target
          })
          result.push(log)
        }

        // 普通调用组件里面设置的方法
        if (typeof _this[name] === 'function') {
          res = await _this[name]({
            ...params,
            emitParams: args,
            eventName,
            logId: log.id
          })
        }
        // 触发对象是页面容器且是调用函数, 则找到其页面内配置的事件id和当前配置调用函数列表的id匹配, 然后触发
        else if(_this.elementType === 'page-container' && name === EXECUTE_CUSTOM_FN && execute_fns.length) {
          const execute_fns_ids = execute_fns.map(item => item.id)
          const equalEvents = []
          Object.entries(_this.storeData.eventsData).forEach(([,events]) => {
            Object.entries(events).forEach(([,value]) => {
              const treeUtil = treeTool.createInstance({
                id: 'id',
                children: 'callback',
                pid: 'callbackId',
              })
              treeUtil.forEach(value, item => {
                // 该页面中有id和要调用的函数id相同, 加入执行列表
                if(execute_fns_ids.includes(item.id)) {
                  equalEvents.push(item)
                }
              })
            })
          })
          // 传$children是因为让事件链里面的vueThis.getStore() 拿到的是它所在级的正确store
          if(_this.$children[0]) {
            const children = await this.execeteFns(equalEvents, [], _this.$children[0])
            let extraLog = []
            // 长度不一一, 可能是有些函数在页面容器内部找不到
            if(execute_fns.length !== equalEvents.length) {
              const equalEventsIds = equalEvents.map(item => item.id)
              const notExist = execute_fns.filter(item => !equalEventsIds.includes(item.id))
              extraLog = notExist.map(item => this.formatLinkMsg({
                eventName: item.name,
                successText: '找不到该函数',
                successTextType: 'danger',
              }))
            }
            // 记录日志
            this.recordLog({ pid: log.id, children: children.concat(extraLog) })
          }
        }
        // 添加日志
        if (res instanceof Array) {
          res.forEach(opt => {
            result.push(opt)
          })
        } else {
          res && result.push(res)
        }
    }
    return result
  }

  // 递归获取变量值
  getObjectVariables(data, args, vueThis) {
    const isFunc = typeof data === 'string' && data.indexOf(FUNCTION_FLAG) === 0
    const isUtilsFunc = typeof data === 'string' && data.indexOf(UTILS_FUNCTION) === 0
    const isVariableString = isValidateVariable(data)
    let isVariableObject = false
    if (data && typeof data === 'object') {
      const objString = JSON.stringify(data) || ''
      isVariableObject = (objString.includes('$') || objString.includes(FUNCTION_FLAG))
    }
    switch (true) {
      case isFunc: {
        const functionId = data.split(FUNCTION_FLAG)[1]
        const functionsData = vueThis.getStore().functionsData
        const currentFunction = functionsData.find(item => item.id === functionId)
        if (currentFunction) {
          const {
            paramsData,
            code
          } = currentFunction
          const data = paramsData.filter((d) => d.key)
          const params = Object.fromEntries(
            data.map((d) => {
              return [d.key, this.getObjectVariables(d.value, args, vueThis)]
            })
          )
          const fnArgs = Object.keys(params)
          const values = Object.values(params)
          try {
            // todo: js 沙箱机制
            const func = new Function(...fnArgs, code)
            return func(...values)
          } catch (e) {
            console.log(e)
            return undefined
          }
        }
        return undefined
      }
      case isVariableString: {
        // 储存在vuex中store的应用变量, 是可写数据,会变, 可读不可写的应用变量,例如$localStorage,是存在页面变量中的,不在此读
        if(getTrueAppVariables(data)) {
          return getTrueAppVariables(data)
        }

        let elementId = data.split('.')[0]
        const regx = getEmitParamsRegx(elementId)
        // 格式匹配并且参数是正整数
        if (regx.test(data)) {
          const body = data.replace(regx, '$1')
          // 没有任何后缀, 返回所有参数集合
          if (body === data) {
            return args
          }
          return _.get(args, data.replace(body + '.', ''))
        }
        elementId = elementId === 'app' ? vueThis.getStore().layoutData.elementId : elementId

        const path = data.substr(
          elementId.indexOf(elementId) + elementId.length + 1,
        )

        const variable = this.getRef(elementId)
        return _.get(variable, path.replace('$', ''))
      }
      case isVariableObject: {
        const isArray = Array.isArray(data)
        if (isArray) {
          return data.map(d => this.getObjectVariables(d, args, vueThis))
        } else {
          return Object.fromEntries(Object.entries(data).map(([key, value]) => [key, this.getObjectVariables(value, args, vueThis)]))
        }
      }
      case isUtilsFunc: {
        const currentUtil = utils.find(item => item.id === data)
        return currentUtil ?.code || data
      }
      default:
        return data
    }
  }

  /**
   * 根据传入预设参数名和实际值, 执行function并返回结果
   * @param {string} functionId
   * @param {array} params 参数名和参数值的对象
   * params.key 参数名
   * params.value 参数值
   * @param {object} vueThis vue实例
   * @returns {any} function执行结果
   */
  getFuncValueByParams({ functionId, extraParams = [], vueThis }) {
    const trueId = functionId.split(FUNCTION_FLAG)[1]
    const functionsData = vueThis.getStore().functionsData
    const currentFunction = functionsData.find(item => item.id === trueId)
    if (currentFunction) {
      const {
        paramsData,
        code
      } = currentFunction
      const initialParams = extraParams.map(item => item.key)
      const data = paramsData.filter((d) => d.key && !initialParams.includes(d.key))
      const params = Object.fromEntries(
        data.map((d) => {
          return [
            d.key,
            this.getObjectVariables(d.value, [], vueThis)
          ]
        })
      )
      const fnArgs = Object.keys(params)
      const values = Object.values(params)
      try {
        // todo: js 沙箱机制
        const func = new Function(...fnArgs, ...initialParams, code)
        return func(...values, ...extraParams.map(item => item.value))
      } catch (e) {
        console.log(e)
        return undefined
      }
    }
    return undefined
  }

  /**
   * 处理变量赋值
   * @param {object} variable 变量配置
   * @param {string} elementId 被触发的组件id
   */
  async handlerVariable({
    variable,
    elementId,
    args,
    eventName,
    vueThis
  }) {
    const tagList = []
    // 获取变量值
    const result = Object.keys(variable).reduce((obj, key) => {
      const value = this.getObjectVariables(variable[key], args, vueThis)
      obj[key.replace('$', '')] = value
      tagList.push({
        key,
        value: parseValue(value)
      })
      return obj
    }, {})
    await this.setCompVariable({ result, elementId, vueThis })
    return this.formatLinkMsg({
      eventName,
      type: '变量赋值',
      tagList,
      target: this.getElementProps(elementId, vueThis),
    })
  }

  // 设置组件变量
  async setCompVariable({ result, elementId, vueThis }) {
    // 组件已挂载
    if (Object.keys(this.getRef(elementId)).length) {
      await sleep(0)
      globalEmit.call(vueThis, TRIGGER, {
        elementId,
        data: {
          type: EXECUTE_VALUE,
          data: result
        }
      })
    }
    // 组件未挂载, 直接更新到它的props
    else {
      const data = vueThis.getStore().dynamicProps[elementId]
      // 找不到这个组件的配置, 不管它了, 可能被删了
      if (!data) {
        return console.warn(`没有找到组件${elementId}的属性配置`)
      }
      Object.keys(result).forEach(key => {
        const path = getPathList(key).slice(1)
        // 是数据路径
        if (path.length) {
          _.set(data, path, result[key])
        } else {
          // 是数据的key
          data[key] = result[key]
        }
      })

      vueThis._storeCommit(UPDATE_DYNAMIC_PROPS, {
        dynamicProps: {
          [elementId]: data
        }
      })
    }
  }

  // 获取目标组件的实时变量
  getRef(elementId) {
    const instance = this.elementInstances[elementId]
    if (!instance) {
      console.warn(`组件 ${elementId} 未挂载`)
    }
    return instance || {}
  }

  // 把组件的vue实例以elementId为键暴露到出去,挂载到供其他组件赋值操作时拿到实时的实例变量
  initElementInstance(that) {
    this.elementInstances[that.elementId] = that
    // TODO:临时记录方便调试
    window.elementInstances = this.elementInstances
  }
  // 组件销毁时销毁其实例
  deleteElementInstance(elementId) {
    delete this.elementInstances[elementId]
  }
  // 清空所有实例
  clearElementInstance() {
    this.elementInstances = {}
  }
  /**
   * 获取事件配置
   * @param {*} elementId
   */
  getEventsData(elementId, vueThis) {
    const eventsData =
      get(vueThis.getStore(), 'eventsData') || {}

    if (elementId) return eventsData[elementId]
    return eventsData
  }
  /**
   * 检查条件
   * @param {*} eventData
   * condition: [{child: [{
   * variable: $xx,
   * rule: '=',
   * value: '',
   * unit: 'OR'
   * },{}], unit: '&'},{child: [{},{}]}]
   */
  checkCondition({ res = [], args, vueThis }) {
    // const conditionArray = cloneDeep(condition);
    const condition = res
      .map((c) => ({
        ...c,
        child: c.child.filter((d) => ((d.variable !== undefined) || (d.value !== undefined))),
      }))
      .filter((c) => c.child.length)
    if (condition.length) {
      const tagList = []
      let allBoolean = false
      let allUnit
      condition.forEach((conditionItem, index) => {
        const child = conditionItem.child

        let boolean = false
        let childUni
        if (index != 0) {
          allUnit = condition[index - 1].unit
        }
        if (child.length) {
          child.forEach((childItem, j) => {
            const variable = childItem.variable
            const value = childItem.value
            const rule = childItem.rule
            if (j != 0) {
              childUni = child[j - 1].unit
            }
            const result = this.handlerCondition({ variable, value, rule, args, vueThis })
            tagList.push({
              key: `条件配置内容[${ index }][${j + 1}]`,
              value: parseValue(result.left)
            })
            tagList.push({
              key: `值[${ index }][${j + 1}]`,
              value: parseValue(result.right)
            })
            const bool = result.sum
            if (j > 0) {
              boolean = this.handlerUnit(boolean, bool, childUni)
            } else if (j == 0) {
              boolean = bool
            } else {
              boolean = this.handlerUnit(boolean, true, childUni)
            }
          })
        }

        if (index > 0) {
          allBoolean = this.handlerUnit(allBoolean, boolean, allUnit)
        } else {
          allBoolean = boolean
        }
      })
      return {
        tagList,
        valid: allBoolean
      }
    }
    return {
      tagList: [],
      valid: true
    }
  }
  handlerUnit(left, right, unit) {
    let result = true
    switch (unit) {
      case '&&':
        result = left && right
        break
      case '||':
        result = left || right
        break
      default:
        result = right
        break
    }
    return result
  }
  /**
   * 处理条件判断
   */
  handlerCondition({ variable, value, rule, args, vueThis }) {
    // const pool = this.getVariable(elementId)
    // const variable = pool.variablePool || null
    let sum
    //
    // if (!value) return true
    // if (variable) {
    //   let poolValue = get(variable, $key)
    //
    //   // 判断是否为true
    //
    // }
    const left = this.getObjectVariables(variable, args, vueThis)
    const right = this.getObjectVariables(value, args, vueThis)
    const isReg = right instanceof RegExp
    switch (rule) {
      case '=':
        sum = _.isEqual(left, right)
        break
      case '>':
        sum = left > right
        break
      case '<':
        sum = left < right
        break
      case '>=':
        sum = left >= right
        break
      case '<=':
        sum = left <= right
        break
      case '!=':
        sum = !_.isEqual(left, right)
        break
      case 'in':
        sum = left.includes(right)
        break
      case 'out':
        sum = !left.includes(right)
        break
      case 'RegExp':
        sum = new RegExp(isReg ? right : `${right}`, 'm').test(left)
        break
      case 'falsely':
        sum = !left
        break
      case 'truly':
        sum = !!left
        break
      default:
        sum = false
        break
    }

    return {
      left,
      right,
      sum
    }
  }

  // 根据id获取组件属性
  getElementProps(elementId, vueThis) {
    const target = vueThis.getStore().dynamicProps[elementId];
    !target && console.warn(`找不到${elementId}`)
    return target || {}
  }

  getUnionKey(date) {
    return getRandomHexadecimal(8) + '-' + new Date(date || new Date()).getTime()
  }

  formatSQLParams(data, traceId) {
    if (traceId && this.isOpenLog()) {
      data.traceId = traceId
    }
    return data
  }

  async recordSqlResponse(traceId) {
    if (traceId && this.isOpenLog()) {
      const time = new Date()
      const res = await rootApp.$http({
        url: API.listExecuteSqlLog,
        data: {
          traceId
        }
      })
      let text = ''
      try {
        res.data.forEach(item => text += `${item}\n\n`)
      } catch (error) {
        console.log(error)
        text = ''
      }
      this.recordLog({ pid: traceId, children: [this.formatLinkMsg({ time, successText: 'SQL执行结果', tagList: { 'SQL日志': text } })] })
    }
  }

  // 格式化当前链路消息, 附带上时间戳, 生成id
  formatLinkMsg({
    id,
    tagList,
    time,
    ...info
  }) {
    if (!this.isOpenLog()) {
      return {}
    }

    const occurTime = time || new Date()
    return {
      ...info,
      time: moment(occurTime).format('YYYY-MM-DD HH:mm:ss.SSS'),
      // 如果传进来的有id, 则覆盖默认生成的
      id: id || this.getUnionKey(occurTime),
      tagList: (() => {
        if (tagList instanceof Array) {
          return tagList
        } else if (Object.prototype.toString.call(tagList) === '[object Object]') {
          const result = []
          for (const key in tagList) {
            result.push({
              key,
              value: parseValue(tagList[key])
            })
          }
          return result
        } else {
          return []
        }
      })()
    }
  }

  /**
   * 记录bug,存在bug组件中, 数据随着组件销毁而回收
   * @param {string} type 日志类型,默认是error [error: 错误; log: 普通日志; 其他: 普通日志]
   * @param {string} message 折叠消息/消息标题
   * @param {string} stack 消息详细内容, 多行显示, 是js报错的 error.stack
   * @param {object} target 报错目标, 如果包含 elementId ,则会显示组件, 可以点击去到配置错误的地方
   * @param {string} panel 切换组件后切换到的设置面板, 可选['prop', 'style', 'event']
   * @param {string} clickType 点击跳转tag执行回调的类型
   * @param {function} callback 点击跳转tag执行的自定义回调
   */
  recordBug({
    type = 'error',
    message,
    error,
    target = {},
    panel,
    clickType,
    callback
  }) {
    error && console.error(error)
    if (!this.isLogShown()) {
      return
    }
    const time = new Date()
    this.recordInfo({
      type,
      message,
      stack: error && error.stack,
      target,
      panel,
      clickType,
      callback,
      time: moment(time).format('YYYY-MM-DD HH:mm:ss.SSS'),
      children: [],
      id: this.getUnionKey(time)
    })
  }

  /**
   *记录日志
   * @param {string} message 日志标题, 可记录事件所属组件和被触发的事件
   * @param {array} logs 该事件触发的日志列表
   */
  recordLog(log) {
    if (!this.isOpenLog()) {
      return
    }
    this.recordInfo({
      ...log,
      time: moment(log.time || new Date()).format('YYYY-MM-DD HH:mm:ss.SSS'),
      type: 'log',
      id: log.id || this.getUnionKey(log.time),
    })
  }

  // 更新日志池
  recordInfo(data) {
    // 把多余的属性去掉,避免数据太大
    const {
      target: { elementId, elementType, elementName, elementVersion } = {},
      children,
      ...others
    } = data
    const logItem = {
      ...others,
      target: { elementId, elementType, elementName, elementVersion },
    }
    if (children && children.length) {
      logItem.children = children.map((item) => {
        if (item.target && item.target.elementId) {
          item.target = {
            elementId: item.target.elementId,
            elementType: item.target.elementType,
            elementName: item.target.elementName,
            elementVersion: item.target.elementVersion,
          }
        }
        return item
      })
    }

    let isReceive = false
    EventBus.$emit(UPDATE_LOGS, logItem, () => isReceive = true)
    if (!isReceive) {
      this.logBuffer.push(logItem)
    }
  }

  // 清空日志
  clearLogBuffer() {
    this.logBuffer = []
  }

  // 日志面板是否可见
  isLogShown() {
    let settings = formatSetting(store.state.CODE.baseInfo.VERSION_INFO.settings)
    return isEditMode() ? settings.edit : settings.preview
  }

  // 是否记录日志
  isOpenLog() {
    return !isEditMode() && this.isLogShown()
  }
}

const instance = new TriggerTarget()

export default instance