// 有option选项的混入
import { RECEIVE_PROPS } from '@/config/eventKey'
import _ from 'lodash'
import {
  isNumString
} from '@/utils/validateUtils'
import request from '@/request/request'
import {
  DATA_LOAD_SUCCESS,
  RESET
} from '@/config/eventName'
import {
  asyncFunc
} from '@/utils'
import treeTool from 'tree-tool'
import trigger from '@/renderer/tools/trigger'
const mixin = {
  data() {
    return {
      fetchLoading: false,
      realOptions: []
    }
  },
  methods: {
    _receiveData({ type }) {
      if(type === RECEIVE_PROPS) {
        this[RESET]()
      }
    },
    getDataSourcePath(key, from) {
      const target = from.find((d) => d.key === key)
      return target ? target.path.map((p) => (isNumString(p) ? `[${p}]` : p))
        .join('.')
        .replace('.[', '[') :
        ''
    },
    getLastKey(key, data) {
      const result = data.find(item => item.key === key)
      const realKey = result ? result.path[result.path.length - 1] : ''
      return !['parentId', 'leaf'].includes(key) ? realKey || key : realKey
    },

    handleFormatOptions(dataList) {
      const { dataVariables } = this
      const labelKey = this.getLastKey('label', dataVariables)
      const valueKey = this.getLastKey('value', dataVariables)
      const iconKey = this.getLastKey('icon', dataVariables)
      const isDefaultKey = this.getLastKey('isDefault', dataVariables)
      const disabledKey = this.getLastKey('disabled', dataVariables)
      const childrenKey = this.getLastKey('children', dataVariables)
      const idKey = this.getLastKey('id', dataVariables)
      const parentIdKey = this.getLastKey('parentId', dataVariables)
      // 级联选择器
      const leafKey = this.getLastKey('leaf', dataVariables)

      this.nodeKey = idKey

      const treeUtil = treeTool.createInstance({
        id: idKey,
        children: childrenKey,
        pid: parentIdKey || 'parentId',
      })

      const source = dataList
      const data = parentIdKey ? treeUtil.fromList(source) : source
      this.realOptions = this.getFormatData({
        idKey,
        labelKey,
        valueKey,
        iconKey,
        isDefaultKey,
        disabledKey,
        childrenKey,
        data,
        leafKey,
        parentIdKey
      })
    },

    formatRequestData(res) {
      const { dataVariables } = this
      const dataPath = this.getDataSourcePath('data', dataVariables)
      const labelKey = this.getLastKey('label', dataVariables)
      const valueKey = this.getLastKey('value', dataVariables)
      const iconKey = this.getLastKey('icon', dataVariables)
      const isDefaultKey = this.getLastKey('isDefault', dataVariables)
      const disabledKey = this.getLastKey('disabled', dataVariables)
      const childrenKey = this.getLastKey('children', dataVariables)
      const idKey = this.getLastKey('id', dataVariables)
      const parentIdKey = this.getLastKey('parentId', dataVariables)
      // 级联选择器
      const leafKey = this.getLastKey('leaf', dataVariables)

      this.nodeKey = idKey

      const treeUtil = treeTool.createInstance({
        id: idKey,
        children: childrenKey,
        pid: parentIdKey || 'parentId',
      })

      const source = _.get(res, dataPath)
      const data = parentIdKey ? treeUtil.fromList(source) : source
      return this.getFormatData({
        idKey,
        labelKey,
        valueKey,
        iconKey,
        isDefaultKey,
        disabledKey,
        childrenKey,
        data,
        leafKey,
        parentIdKey
      })
    },
    getFormatData({
      idKey,
      labelKey,
      valueKey,
      iconKey,
      isDefaultKey,
      disabledKey,
      childrenKey = 'children',
      data,
      parentsKeys = [],
      parentsLabels = [],
      leafKey,
      parentIdKey
    }) {
      if (!data) {
        return []
      }
      return data.map(item => {
        // 级联 || 树状选择器默认值需要收集它父级所有的value
        const appendValues = parentsKeys.concat([item[valueKey]])
        const appendLabels = parentsLabels.concat([item[labelKey]])

        const result = {
          id: item[idKey],
          label: item[labelKey],
          value: item[valueKey],
          icon: item[iconKey],
          sourceData: item.sourceData || item,
          disabled: item[disabledKey],
          isDefault: item[isDefaultKey],
          parentsKeys: appendValues,
          parentsLabels: appendLabels,
        }

        if (parentIdKey || (childrenKey && item[childrenKey] && item[childrenKey].length)) {
          const children = this.getFormatData({
            idKey,
            labelKey,
            valueKey,
            iconKey,
            isDefaultKey,
            disabledKey,
            childrenKey,
            data: item[childrenKey],
            parentsKeys: appendValues,
            parentsLabels: appendLabels,
            leafKey,
            parentIdKey
          })
          result.children = children.length ? children : undefined
        }

        if(leafKey) {
          result.leaf = item[leafKey]
        } else {
          result.leaf = !result.children || result.children.length === 0
        }

        return result
      })
    },
    initVal() {
      const {
        elementId,
        elementName,
        elementType
      } = this
      if(!['dropdown'].includes(elementType)) {
        console.error(`${elementName}(${elementId})当前组件没有定义自己设置默认值的initVal方法`)
      }
    },
    async [RESET]({ logId } = {}) {
      const reset2UndefinedKeys = ['currentNode', 'currentSource', 'currentValue']
      const reset2ArrayKeys = ['checkedNodeList', 'checkedSourceList', 'checkedValueList']
      reset2UndefinedKeys.forEach(key => this[key] = undefined)
      reset2ArrayKeys.forEach(key => this[key] = [])
      const {
        isEditMode,
        apiRequest,
        sqlRequest,
        staticData
      } = this
      if (isEditMode || (!isEditMode && !(apiRequest || sqlRequest))) {
        this.realOptions = this.getFormatData({
          idKey: 'id',
          labelKey: 'label',
          valueKey: 'value',
          iconKey: 'icon',
          isDefaultKey: 'isDefault',
          disabledKey: 'disabled',
          childrenKey: 'children',
          leafKey: 'leaf',
          data: staticData
        })
        this.dataList = staticData
      } else if (!isEditMode && (apiRequest || sqlRequest)) {
        this.fetchLoading = true
        this.realOptions = await this.fetchRequestData({ logId })
        this.fetchLoading = false
      }
      if(!this.valueInSlot) {
        this.initVal()
      }
      this._emitEvent(DATA_LOAD_SUCCESS)
    },
    async fetchRequestData({ logId } = {}) {
      const log = {
        pid: logId,
        id: trigger.getUnionKey(),
        tag: '数据集',
        target: this,
        children: []
      }

      const {
        apiRequest,
        sqlRequest
      } = this
      // api 请求
      if (apiRequest) {
        const params = this.getApiRequestParams(this.apiRequest)
        if (!params.url) {
          return
        }
        // 记录日志
        const unionId = trigger.getUnionKey()
        log.children.push(trigger.formatLinkMsg({
          id: unionId,
          type: '调用API',
          tagList: {
            method: params.method,
            url: params.url,
            params: params.params,
            data: params.data,
            headers: params.headers,
          },
        }))
        trigger.recordLog(log)
        // 记录日志
        const [err, res] = await asyncFunc(request, {
          method: params.method,
          url: params.url,
          params: params.params,
          data: params.data,
          headers: params.headers,
        })
        this.handleUpdateDataList(res?.data)
        if (res) {
          // 记录日志
          trigger.recordLog({ pid: unionId, children: [trigger.formatLinkMsg({ successText: '成功回调', tagList: { response: res } })] })
          return this.formatRequestData(res.data)
        } else {
          // 记录日志
          trigger.recordLog({ pid: unionId, children: [trigger.formatLinkMsg({ errorText: '失败回调', tagList: { err } })] })
        }
        return []
      }
      // sql 请求
      else if (sqlRequest) {
        const params = this.getSqlParams(sqlRequest)

        // 记录日志
        const unionId = trigger.getUnionKey()
        log.children.push(trigger.formatLinkMsg({
          id: unionId,
          type: '调用SQL',
          tagList: {
            method: params.method,
            url: params.url,
            params: params.params,
            data: params.data,
            headers: params.headers,
          },
        }))
        trigger.recordLog(log)
        // 记录日志

        let res
        try {
          res = await this.$http({
            url: params.url,
            data: trigger.formatSQLParams(params.data, unionId),
          })
          // 记录日志
          trigger.recordSqlResponse(unionId)
          trigger.recordLog({ pid: unionId, children: [trigger.formatLinkMsg({ successText: '成功回调', tagList: { response: res } })] })
        } catch (err) {
          console.log(err)
          // 记录日志
          trigger.recordSqlResponse(unionId)
          trigger.recordLog({ pid: unionId, children: [trigger.formatLinkMsg({ errorText: '失败回调', tagList: { err } })] })
        }
        this.handleUpdateDataList(res)
        if (res) {
          return this.formatRequestData(res)
        }
        return []
      }
    },
    handleUpdateDataList(res = {}) {
      const dataPath = this.getDataSourcePath('data', this.dataVariables)
      const source = _.get(res, dataPath)
      this.dataList = source || []
    }
  },
  watch: {},
  created() {
    if (this.autoFetch !== false) {
      this[RESET]()
    }
  }
}
export default mixin
