<template>
  <el-tag
    class="tag-widget"
    :class='commonClassName'
    :style='componentStyle'
    :type="type"
    :effect="effect"
    :size="size"
    :hit="hit"
    :closable="closable"
    :disableTransitions="disableTransitions"
    @click="click"
    @close='close'
    >{{ value }}
  </el-tag>
</template>

<script>
  import { CLICK, CLOSE_TAG } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import _ from 'lodash'

  export default {
    mixins: [common],
    variables: {},
    computed: {
      value() {
        const value = this.useSlotProps ? _.get(this.slotProps, this.prop) : this.text
        return this.isEditMode && this.useSlotProps && !value ? `动态数据${this.prop}` : value
      }
    },
    methods: {
      click(e) {
        this._emitEvent(CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
      close() {
        this._emitEvent(CLOSE_TAG)
      },
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .tag-widget {
    display: inline-block;
  }
</style>
