<template>
  <div class="data-table" :class="commonClassName" :style='componentStyle' v-loading="tableLoading" @click="handleStopPropagation">
    <div class="data-table-form" :class='{"onlySearch": !showButtons && showSearch}' v-if="showSearch || showButtons">
      <div class="table-search" v-if="showSearch">
        <el-select size="mini" v-model="queryForm.field" @change="handleLocalSearch" placeholder="请选择">
          <el-option label="全部" value=""></el-option>
          <el-option
            v-for="item in column"
            :key="item.key"
            :label="item.label"
            :value="item.key"
          >
          </el-option>
        </el-select>
        <el-input
          class="search-input"
          size="mini"
          v-model="queryForm.keyword"
          clearable
          @clear="handleLocalSearch"
          placeholder="请输入关键字"
        >
          <el-button
            class="input-append-btn"
            slot="append"
            size="mini"
            icon="el-icon-search"
            @click="handleLocalSearch"
          >
          </el-button>
        </el-input>
      </div>
      <component
        v-if="showButtons"
        class="table-button-container"
        :is='buttons.slotElement'
        :ref="buttons.elementId"
        :data-zone="`${buttons.elementId}_${buttons.elementType}`"
        v-bind="buttons"
        empty-text="拖拽按钮到这里"
        :showLayoutType="false"
        :id="buttons.elementId"
      ></component>
    </div>
    <div class="data-table-container">
      <vxe-table
        v-if='isTableVisible'
        v-show="!hasError"
        ref="xTable"
        :cell-class-name="getCellClassName"
        :sync-resize="watchList"
        :auto-resize='true'
        :style='{height}'
        :data="tableData"
        :size='tableSize'
        :max-height="maxHeight"
        :align="align"
        :header-align="headerAlign"
        :show-header-overflow='isEditMode ? false : showOverflow'
        :show-overflow="isEditMode ? false : showOverflow"
        :show-header="showHeader"
        :border="border"
        :stripe="stripe"
        :resizable="!isEditMode && resizable"
        :scroll-x='scrollXOptions'
        :scroll-y='scrollYOptions'
        :column-key="true"
        :empty-text="emptyText"
        :highlight-current-row="!isEditMode"
        :highlight-hover-row="!isEditMode"
        :highlight-hover-column="!isEditMode"
        :tooltip-config='{ leaveDelay: 500 }'
        :row-style='{height: isEditMode ? undefined : rowHeight}'
        :import-config="{}"
        :export-config="exportConfig"
        :sort-config="{remote, trigger: sortTrigger, showIcon: showSortIcon, multiple: sortMultiple}"
        @cell-click="ROW_CLICK"
        @checkbox-change='handleCheckboxChange'
        @checkbox-all='handleCheckboxChange'
        @radio-change='handleRadioChange'
        @sort-change='handleSortChange'
      >
        <vxe-table-column
          key="column_checkbox"
          v-if="checkbox !== 'none'"
          :type="checkbox"
          width="42px"
          align="center"
        />
        <vxe-table-column
          v-if="showSeq"
          key="column_seq"
          title="序号"
          type="seq"
          width="60px"
          align="center"
        />
        <template v-if="tableHeaderGroup.length && !dynamicColumn">
          <template v-for="group in tableHeaderGroup">
            <ColumnGroup
              v-if="!group.isLeaf"
              :key="group.filed"
              :group="group"
              :isEditMode="isEditMode"
              :getSlotByName="getSlotByName"
              :getDataZone="getDataZone"
              :filterSlotProps="filterSlotProps"
              :captureRowClick="captureRowClick"
              :getColByField="getColByField"
            ></ColumnGroup>
            <template v-else>
              <vxe-table-column
                v-if="getColByField(group.field).isUseSlot"
                :key="getColByField(group.field).key"
                :field="getColByField(group.field).key"
                :align="getColByField(group.field).align"
                :width="getColByField(group.field).width"
                :min-width="getColByField(group.field).minWidth"
                :sortable="getColByField(group.field).sortable"
                :title="group.label"
                :visible='!getColByField(group.field).isHidden'
                :fixed='isEditMode ? undefined : getColByField(group.field).fixed'
              >
                <template #default="{ row }">
                  <component
                    :showLayoutType="false"
                    class="control-button-container"
                    :is='getSlotByName(getColByField(group.field).key).slotElement'
                    :ref="getSlotByName(getColByField(group.field).key).elementId"
                    :data-zone="getDataZone(getColByField(group.field).key)"
                    v-bind="{ ...getSlotByName(getColByField(group.field).key), slotProps: filterSlotProps(row) }"
                    :id="getSlotByName(getColByField(group.field).key).elementId"
                    @click.native.capture="captureRowClick(row)"
                  ></component>
                </template>
              </vxe-table-column>
              <vxe-table-column
                v-else
                :key="getColByField(group.field).key"
                :field="getColByField(group.field).field"
                :type='getColByField(group.field).formatter && getColByField(group.field).formatter.renderType === "html" ? "html" : undefined'
                :align="getColByField(group.field).align"
                :width="getColByField(group.field).width"
                :min-width="getColByField(group.field).minWidth"
                :sortable="getColByField(group.field).sortable"
                :title="group.label"
                :visible='!getColByField(group.field).isHidden'
                :fixed='isEditMode ? undefined : getColByField(group.field).fixed'
              >
                <template v-if='isEditMode' #default>
                  <span>{{`动态数据${getColByField(group.field).key}`}}</span>
                </template>
              </vxe-table-column>
            </template>
          </template>
        </template>
        <template v-else v-for="col in column">
          <vxe-table-column
            v-if="col.isUseSlot"
            :key="col.key"
            :field="col.key"
            :align="col.align"
            :width="col.width"
            :min-width="col.minWidth"
            :sortable="col.sortable"
            :title="col.label"
            :visible='!col.isHidden'
            :fixed='isEditMode ? undefined : col.fixed'
          >
            <template #default="{ row }">
              <component
                :showLayoutType="false"
                class="control-button-container"
                :is='getSlotByName(col.key).slotElement'
                :ref="getSlotByName(col.key).elementId"
                :data-zone="getDataZone(col.key)"
                v-bind="{ ...getSlotByName(col.key), slotProps: filterSlotProps(row) }"
                :id="getSlotByName(col.key).elementId"
                @click.native.capture="captureRowClick(row)"
              ></component>
            </template>
          </vxe-table-column>
          <vxe-table-column
            v-else
            :key="col.key"
            :field="col.field"
            :type='col.formatter && col.formatter.renderType === "html" ? "html" : undefined'
            :align="col.align"
            :width="col.width"
            :min-width="col.minWidth"
            :sortable="col.sortable"
            :title="col.label"
            :visible='!col.isHidden'
            :fixed='isEditMode ? undefined : col.fixed'
          >
            <template v-if='isEditMode' #default>
              <span>{{`动态数据${col.key}`}}</span>
            </template>
          </vxe-table-column>
        </template>
        <vxe-table-column
          key="column_control"
          v-if='showControl'
          :show-overflow="false"
          :fixed="isEditMode ? undefined : 'right'"
          title="操作"
          min-width="100"
          :width='controllerWidth'
        >
          <template #default="{ row }">
            <component
              :showLayoutType="false"
              class="control-button-container"
              :is='controller.slotElement'
              :ref="controller.elementId"
              :data-zone="`${controller.elementId}_${controller.elementType}`"
              v-bind="{ ...controller, slotProps: filterSlotProps(row) }"
              :id="controller.elementId"
              @click.native.capture="captureRowClick(row)"
            ></component>
          </template>
        </vxe-table-column>
      </vxe-table>
      <div v-if="hasError" class="error-msg">{{ errorMsg }}</div>
      <div
        class="pagination"
        v-if="showPage"
        :style="{ justifyContent: paginationAlign }"
      >
        <vxe-pager
          :background="pagerBackground"
          :size="paginationSize"
          :current-page="page"
          :page-size="size"
          :total="totalCount"
          :page-sizes="pageSizes"
          :layouts="layouts"
          @page-change="handlePageChange"
        >
        </vxe-pager>
      </div>
    </div>
  </div>
</template>

<script>
  import { VXETable } from 'vxe-table'
  import ColumnGroup from './components/ColumnGroup.vue'
  import _ from 'lodash'
  import { RESET, ROW_CLICK, PAGE_CHANGE, TABLE_IMPORT, TABLE_EXPORT, DATA_LOAD_SUCCESS, TABLE_CLEAR_SELECT, TABLE_EXPORT_TEMPLATE } from '@/config/eventName'
  import request from '@/request/request'
  import common from '@/template/mixins/common'
  import { isValidJsonString } from '@/utils/validateUtils'
  import { getPathList, getPathString, getPathFrom, getFormatValue } from '@/utils/format'
  import { getPropsValues } from '@/renderer/tools/config'
  import trigger from '@/renderer/tools/trigger'

  export default {
    mixins: [common],
    components: {
      ColumnGroup
    },
    variables: {
      $dataList(n) {
        const variables = this.dynamicColumn || this.dataVariables
        const dataListPath = getPathFrom('dataList', variables)
        const response = dataListPath ? {
          [dataListPath]: Array.isArray(n) ? n : [],
        } : Array.isArray(n) ? n : []
        this.handleSuccess(response)
      },
      $mergeCells(n) {
        if (Array.isArray(n)) {
          this.mergeCells = n
          const xTable = this.$refs.xTable
          xTable?.setMergeCells(this.mergeCells)
        }
      }
    },
    inject: ['dialogProvide', 'tabsProvide'],
    data() {
      return {
        isTableVisible: true,
        totalCount: 0,
        queryForm: {
          keyword: '',
          field: '',
        },
        tableLoading: false,
        selectedRows: [],
        dataList: [],
        tableData: [],
        fieldOptions: [],
        currentRow: {},
        layoutsMap: {
          default: [
            'Total',
            'PrevPage',
            'JumpNumber',
            'NextPage',
            'FullJump',
            'Sizes',
          ],
          type1: [
            'Total',
            'PrevPage',
            'JumpNumber',
            'NextPage',
            'Sizes',
          ],
          type2: [
            'Total',
            'PrevPage',
            'JumpNumber',
            'NextPage',
          ],
          type3: [
            'PrevPage',
            'JumpNumber',
            'NextPage',
          ],
        },
        exportConfig: {
          types: ['xlsx', 'csv', 'html', 'xml', 'txt'],
          type: 'xlsx',
          sheetName: 'sheet1'
        },
        dynamicColumnData: null,
        staticPageSize: 10,
        hasError: false,
        errorMsg: ''
      }
    },
    computed: {
      eventsData() {
        return this.getStore().eventsData[this.elementId]
      },
      buttons() {
        return this.getSlotByName('buttons')
      },
      controller() {
        return this.getSlotByName('controller')
      },
      column() {
        return (this.dynamicColumnData || this.dataColumn).map(col => ({...col, field: this.getField(col)}))
      },
      columnConfig() {
        return (this.dynamicColumnData || this.dataColumn).map(col => ({ key: col.key, label: col.label }))
      },
      layouts() {
        return this.layoutsMap[this.paginationLayout]
      },
      watchList() {
        return [this.column, this.checkbox, this.showControl, this.height, this.maxHeight, this.dialogProvide?.visible, this.tabsProvide?.visible]
      },
      scrollXOptions() {
        return {
          enabled: !this.isEditMode && this.virtualScroll,
          gt: 30,
          oSize: 10,
        }
      },
      scrollYOptions() {
        return {
          enabled: !this.isEditMode && this.virtualScroll,
          mode: 'default',
          gt: 60,
          oSize: 10,
        }
      },
      pathList() {
        const variables = this.dynamicColumn || this.dataVariables
        let dataListPath = getPathFrom('dataList', variables)
        return getPathList(dataListPath)
      },
      pageSizes() {
        const sizes = [
          this.staticPageSize,
          10,
          20,
          50,
          100,
          500,
          1000,
        ]
        return _.uniq(sizes).sort((a, b) => a - b)
      },
      controllerWidth() {
        const { styleConfigList = [] } = this.getElementPropsById(this.controller.elementId)
        const styleConfig = styleConfigList[0]
        return styleConfig?.styles?.width || undefined
      }
    },
    methods: {
      getPathFromColumn(col) {
        if (!col.path?.length) {
          return col.key
        }
        const deepPath = col.path.slice(this.pathList.length + 1)
        const deepKey = getPathString(deepPath)
        return deepKey || col.key
      },
      getField(col) {
        return col.formatter ? `${col.key}__format` : col.key
      },
      getDataZone(key) {
        const slot = this.getSlotByName(key)
        if (!slot) {
          return ''
        }
        return `${slot.elementId}_${slot.elementType}`
      },
      handlePageChange({ currentPage, pageSize }) {
        this.page = currentPage
        this.size = pageSize

        if (this.isLocalPage || this.staticData) {
          this.dataList = this.dataList.map((item, index) => {
            if (
              index + 1 > (currentPage - 1) * pageSize &&
              index + 1 <= currentPage * pageSize
            ) {
              return { ...item, isVisible: true }
            }
            return { ...item, isVisible: false }
          })
          this.handleLoadData()
        } else {
          this.handleSearch()
        }
        this._emitEvent(PAGE_CHANGE)
      },
      handleLocalSearch() {
        const keyword = this.queryForm.keyword?.toLowerCase() || ''
        this.dataList = this.dataList.map((d) => {
          const rowData = _.cloneDeep({...d, isVisible: undefined})
          const isFormatKey = key => this.column.some(item => `${item.key}` === `${key}` && item.formatter)
          const isColumnIncludes = this.queryForm.field && (isFormatKey(this.queryForm.field) ? `${rowData[`${this.queryForm.field}__format`]}`.toLowerCase().includes(keyword) : `${rowData[this.queryForm.field]}`.toLowerCase().includes(keyword))
          const isCellValueIncludes = !this.queryForm.field && Object.keys(rowData).map((k) => isFormatKey(k) ? `${rowData[`${k}__format`]}`.toLowerCase() : `${rowData[k]}`.toLowerCase()).some((v) => v.includes(keyword))
          const isVisible = !keyword || isColumnIncludes || isCellValueIncludes
          return {...d, isVisible}
        })
        this.handleLoadData()
      },
      handleSearch({ logId } = {}) {

        const log = {
          pid: logId,
          id: trigger.getUnionKey(),
          tag: '数据集',
          target: this,
          children: []
        }

        if (this.apiRequest) {
          const params = this.getApiRequestParams(this.apiRequest)
          if (!params.url) {
            return
          }
          this.tableLoading = true

          // 记录日志
          const unionId = trigger.getUnionKey()
          log.children.push(trigger.formatLinkMsg({
            id: unionId,
            type: '调用API',
            tagList: {
              method: params.method,
              url: params.url,
              params: params.params,
              data: params.data,
              headers: params.headers,
            },
          }))
          trigger.recordLog(log)
          // 记录日志

          request({
            method: params.method,
            url: params.url,
            params: params.params,
            data: params.data,
            headers: params.headers,
          })
            .then((res) => {
              this.handleSuccess(res.data)

              // 记录日志
              trigger.recordLog({ pid: unionId, children: [trigger.formatLinkMsg({ successText: '成功回调', tagList: { response: res } })] })
            })
            .catch((err) => {
              // 记录日志
              trigger.recordLog({ pid: unionId, children: [trigger.formatLinkMsg({ errorText: '失败回调', tagList: { err } })] })
            })
            .finally(() => {
              this.tableLoading = false
            })
          return
        }
        if (this.sqlRequest) {
          const params = this.getSqlParams(this.sqlRequest)
          this.tableLoading = true

          // 记录日志
          const unionId = trigger.getUnionKey()
          log.children.push(trigger.formatLinkMsg({
            id: unionId,
            type: '调用SQL',
            tagList: {
              method: params.method,
              url: params.url,
              params: params.params,
              data: params.data,
              headers: params.headers,
            },
          }))
          trigger.recordLog(log)
          // 记录日志

          this.$http({
            url: params.url,
            data: trigger.formatSQLParams(params.data, unionId),
          })
            .then((res) => {
              this.handleSuccess(res)

              // 记录日志
              trigger.recordSqlResponse(unionId)
              trigger.recordLog({ pid: unionId, children: [trigger.formatLinkMsg({ successText: '成功回调', tagList: { response: res } })] })
            }).catch(err => {

              // 记录日志
              trigger.recordSqlResponse(unionId)
              trigger.recordLog({ pid: unionId, children: [trigger.formatLinkMsg({ errorText: '失败回调', tagList: { err } })] })
            })
            .finally(() => {
              this.tableLoading = false
            })
          return
        }
        if (this.staticData) {
          this.handleSuccess(this.staticData)

          log.children.push(trigger.formatLinkMsg({
            type: '使用静态数据',
            tagList: { staticData: this.staticData },
          }))
          trigger.recordLog(log)
        }
      },
      // 请求成功
      handleSuccess(response) {
        const variables = this.dynamicColumn || this.dataVariables
        const dataListPath = getPathFrom('dataList', variables)
        let dataList = dataListPath ? _.get(response, dataListPath) : response
        try {
          this.tableLoading = true
          let dataColumn = this.dataColumn
          if (!Array.isArray(dataList)) {
            if (typeof dataList === 'string' && isValidJsonString(dataList)) {
              try {
                const res = JSON.parse(dataList.trim())
                dataList = Array.isArray(res) ? res : []
              } catch (e) {
                dataList = []
              }
            } else {
              dataList = []
            }
          }
          if (this.dynamicColumn) {
            // 动态表格
            const columnConfig = _.cloneDeep(this.dataColumn)
            const pathLength = 2
            dataColumn = dataList[0].map(item => {
              const keyPath = getPathFrom('key', variables).replace(dataListPath, '')
              const keyPathList = getPathList(keyPath)
              const key = _.get(item, keyPathList.slice(pathLength))
              const namePath = getPathFrom('name', variables).replace(dataListPath, '')
              const namePathList = getPathList(namePath)
              const name = _.get(item, namePathList.slice(pathLength))
              const valuePath = getPathFrom('value', variables).replace(dataListPath, '')
              const valuePathList = getPathList(valuePath)
              const path = [dataListPath, '0', key]
              const vPath = valuePathList.slice(pathLength)
              const kPath = keyPathList.slice(pathLength)
              const currentConfig = columnConfig.find(col => `${col.key}` === `${key}`)
              return currentConfig ? { ...currentConfig, key, label: name, path, vPath, kPath } : {
                label: name,
                key,
                align: 'left',
                width: undefined,
                minWidth: 160,
                sortable: false,
                isHidden: false,
                isUseSlot: false,
                path,
                vPath,
                kPath,
                formatter: undefined,
                fixed: undefined,
              }
            })
            dataList = dataList.map(item => {
              return Object.fromEntries(item.map((d, i) => {
                return [_.get(d, dataColumn[i]?.kPath), _.get(d, dataColumn[i]?.vPath)]
              }))
            })
            const totalCountPath = getPathFrom('totalCount', variables)
            const totalCount = _.get(response, getPathFrom('totalCount', variables))
            response = {
              [dataListPath]: dataList,
              [totalCountPath]: totalCount
            }
            this.dynamicColumnData = dataColumn.map(item => {
              delete item.vPath
              delete item.kPath
              return item
            })
          }
          this.dataList = dataList.map((d) => {
            const customData = Object.fromEntries(dataColumn.map(col => {
              return [col.key, _.get(d, this.getPathFromColumn(col))]
            }))
            const customPathList = dataColumn.filter(
              (c) => !getPathFrom(c.key, dataColumn).includes(dataListPath)
            )
            if (customPathList.length) {
              return {
                ...d,
                ...Object.fromEntries(
                  customPathList.map((c) => [
                    [c.key],
                    _.get(response, getPathFrom(c.key, customPathList)),
                  ])
                ),
                isVisible: true,
              }
            }
            return { ...d, ...customData, isVisible: true }
          })

          const formatDataList = this.dataList.map(row => {
            const formatEntries = []
            const entries = Object.entries(row).map(([key, value]) => {
              const formatter = dataColumn.find(item => `${item.key}` === `${key}`)?.formatter
              if (formatter) {
                const formatValue = getFormatValue({formatter, context: this, row, value})
                formatEntries.push([`${key}__format`, formatValue])
              }
              return [key, value]
            })
            return Object.fromEntries([...entries, ...formatEntries])
          })
          this.dataList = formatDataList
          const totalNum = _.get(response, getPathFrom('totalCount', variables))
          this.totalCount = this.isLocalPage ? this.dataList.length : typeof totalNum === 'number' ? totalNum : this.totalCount
          if (this.isLocalPage) {
            this.dataList = this.dataList.map((item, index) => {
              if (
                index + 1 > (this.page - 1) * this.size &&
                index + 1 <= this.page * this.size
              ) {
                return { ...item, isVisible: true }
              }
              return { ...item, isVisible: false }
            })
          }
          this.handleLoadData()
        } catch (e) {
          this.tableLoading = false
          this.hasError = true
          if (Array.isArray(dataList) && dataList.length === 0 && this.dynamicColumn) {
            this.errorMsg = '暂无数据'
            this.dataList = []
            const totalNum = _.get(response, getPathFrom('totalCount', variables)) || 0
            this.totalCount = this.isLocalPage ? this.dataList.length : totalNum
            this.page = this.totalCount === 0 ? 1 : this.page
          } else {
            this.errorMsg = '表格数据解析失败'
            console.warn('数据解析失败，表格数据格式有误：', e)
          }
        }
      },
      handleLoadData() {
        this.hasError = false
        this.$nextTick(() => {
          const visibleData = this.dataList.filter((d) => d.isVisible)
          const dataList = this.isEditMode ? [{}] : visibleData
          const xTable = this.$refs.xTable
          xTable?.loadData(dataList).then(() => {
            xTable?.setMergeCells(this.mergeCells)
            this._emitEvent(DATA_LOAD_SUCCESS)
            this.hasError = false
          }).catch(e => {
            this.hasError = true
            console.warn('数据解析失败：', e)
            this.errorMsg = '表格数据解析失败'
          }).finally(() => {
            this.tableLoading = false
          })
        })
      },
      handleForceUpdate() {
        this.isTableVisible = false
        this.$nextTick(() => {
          this.isTableVisible = true
        })
      },
      async [RESET]({ logId } = {}) {
        if (this.isEditMode) {
          return
        }
        this.handleSearch({ logId })
      },
      ROW_CLICK({ row }) {
        if (this.isEditMode) {
          return
        }
        const xTable = this.$refs.xTable
        xTable.setRadioRow(row)
        this.currentRow = _.cloneDeep(row)
        this._emitEvent(ROW_CLICK)
      },
      handleCheckboxChange({ records, reserves, indeterminates, checked, row, rowIndex, $rowIndex, column, columnIndex, $columnIndex, $event }) {
        if (this.isEditMode) {
          return
        }
        this.selectedRows = _.cloneDeep(records)
      },
      handleRadioChange({ newValue, oldValue, row, rowIndex, $rowIndex, column, columnIndex, $columnIndex, $event }) {
        if (this.isEditMode) {
          return
        }
        const xTable = this.$refs.xTable
        xTable.setCurrentRow(row)
        this.currentRow = _.cloneDeep(row)
      },
      [TABLE_EXPORT]() {
        if (this.isEditMode) {
          return
        }
        import(/* webpackChunkName: "VXEExportXLSX" */ 'vxe-table-plugin-export-xlsx').then(({ VXETablePluginExportXLSX }) => {
          VXETable.use(VXETablePluginExportXLSX)
          this.$refs.xTable.openExport()
        })
      },
      [TABLE_EXPORT_TEMPLATE]() {
        if (this.isEditMode) {
          return
        }
        import(/* webpackChunkName: "VXEExportXLSX" */ 'vxe-table-plugin-export-xlsx').then(({ VXETablePluginExportXLSX }) => {
          VXETable.use(VXETablePluginExportXLSX)
          this.$refs.xTable.exportData({ filename: '表格模板导出', type: 'xlsx', original: true, data: [] })
        })
      },
      [TABLE_IMPORT]() {
        if (this.isEditMode) {
          return
        }
        import(/* webpackChunkName: "VXEExportXLSX" */ 'vxe-table-plugin-export-xlsx').then(({ VXETablePluginExportXLSX }) => {
          VXETable.use(VXETablePluginExportXLSX)
          this.$refs.xTable.importData({types: ['xlsx']})
        })
      },
      filterSlotProps(row) {
        const copy = _.cloneDeep(row)
        this.$delete(copy, '_XID')
        return copy
      },
      getCellClassName({ columnIndex }) {
        return this.column[columnIndex]?.isUseSlot ? 'is-use-slot' : ''
      },
      handleStopPropagation(e) {
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
      captureRowClick(row) {
        if (this.isEditMode) {
          return
        }
        this.currentRow = _.cloneDeep(row)
      },
      handleSortChange({ column, property, order, sortBy, sortList, $event }) {
        this.sortList = sortList
        this.sortProperty = this.sortList[0]?.property
        this.sortOrder = this.sortList[0]?.order
        if (this.remote) {
          this[RESET]()
        }
      },
      [TABLE_CLEAR_SELECT]() {
        const xTable = this.$refs.xTable
        if (xTable) {
          xTable.clearCurrentRow()
          xTable.clearCurrentColumn()
          xTable.clearRadioRow()
          xTable.clearCheckboxRow()
          this.currentRow = undefined
          this.selectedRows = []
        }
      },
      getColByField(field) {
        return this.column.find(item => item.field === field) || {}
      }
    },
    async mounted() {
      if (isNaN(this.size)) {
        this.size = getPropsValues.call(this, {elementType: this.elementType, elementVersion: this.elementVersion}).size
      }
      this.staticPageSize = this.size
      // 初始化赋值
      await this.$nextTick()
      this.tableData = this.isEditMode ? [{}] : []
      if (this.staticData && !this.isEditMode) {
        setTimeout(() => {
          this.handleSuccess(this.staticData)
        }, 60)
      }
      if (this.autoFetch && (this.sqlRequest || this.apiRequest)) {
        setTimeout(() => {
          this[RESET]()
        }, 60)
      }
      if (this.isEditMode) {
        ['dataColumn', 'tableHeaderGroup', 'controllerWidth'].forEach(watchKey => {
          this.$watch(watchKey, this.handleForceUpdate, {deep: true})
        })
      }
    },
  }
</script>

