<template>
  <div
    v-loading="!chartInstance && fetchLoading"
    ref="chart"
    class="chart-content"
    :class="commonClassName"
  ></div>
</template>

<script>
import { CLICK, RESET } from '@/config/eventName'
import common from '@/template/mixins/common'
import chart from '@/template/mixins/chart'

import _ from 'lodash'

export default {
  mixins: [common, chart],
  variables: {
    $chartData(newV) {
      this.staticData = newV
      this[RESET](false)
    },
  },
  computed: {},
  watch: {},
  data() {
    return {
      seriesData: [],
    }
  },
  mounted() {},
  created() {},
  methods: {
    getOption() {
      const option = this.getBaseOption()
      return option
    },
    // 初始化监听事件
    initEvents() {
      //点击
      this.chartInstance.on('click', (e) => {
        const { name, value } = e.data
        const result = { name, value }
        this.dotData = result
        this._emitEvent(CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.event.event.stopPropagation()
        }
      })
    },
    getTypeSetting(item, index) {
      const {
        startAngle,
        endAngle,
        radius,
        getLengthValue,
        min,
        max,
        pointer,
        progress,
        axisTick,
        splitLine,
        color,
        seriesData,
        axisLine,
        cleanMode,
        centerX,
        centerY
      } = this

      function getPointerColor() {
        return {
          progress: () => 'auto',
          custom: () => pointer.color,
        }[pointer.colorType]()
      }

      function getLineStyleColor() {
        const { type, color: colorValue, colorRange } = color
        if (type === 0) {
          if (!colorValue) {
            return undefined
          }
          return [[1, colorValue]]
        } else {
          return colorRange
        }
      }
      const extra = {
        data: index === 0 ? seriesData : [],
        min,
        max,
        startAngle: startAngle && startAngle.value + startAngle.unit,
        endAngle: endAngle && endAngle.value + endAngle.unit,
        radius: radius && radius.value + radius.unit,
        progress: {
          show: progress.use,
          width: getLengthValue(progress.width),
          itemStyle: {
            color: progress.color,
          },
        },
        splitLine: {
          show: cleanMode ? false : splitLine.use,
        },
        splitNumber: splitLine.splitNumber,
        axisLabel: cleanMode
          ? false
          : {
              show: splitLine.use && splitLine.axisLabelShow,
              formatter: splitLine.axisLabelUseFormat
                ? splitLine.axisLabelFormatter
                : undefined,
            },
        axisTick: {
          show: cleanMode ? false : axisTick.use,
          splitNumber: axisTick.splitNumber,
        },
        axisLine: {
          show: cleanMode ? true : axisLine.use,
          lineStyle: {
            width: getLengthValue(axisLine.width),
          },
        },
        // 指针
        pointer: {
          show: pointer.use,
          length: getLengthValue(pointer.length),
          width: getLengthValue(pointer.width),
          itemStyle: {
            color: getPointerColor(),
          },
        },
      }
      // 仪表盘轴线样式
      if (getLineStyleColor()) {
        extra.axisLine.lineStyle.color = getLineStyleColor()
      }
      // 圆心垂直水平偏移
      if(centerX && centerY) {
        extra.center = [getLengthValue(centerX), getLengthValue(centerY)]
      }
      return extra
    },
    formatRequestData(response, dispose = true) {
      this.responseData = response
      const { dataVariables: data } = this
      const dataPath = data.find((item) => item.key === 'sourcePath')?.path

      this.yAxis = data.find((item) => item.key === 'yAxis')?.fields
      // 没有响应数据就用静态数据
      const res = response
        ? _.get(
            {
              response,
            },
            dataPath
          )
        : this.staticData || [{}]
      const [result] = res
      // 赋值给可暴露属性
      this.chartData = result

      const { getLengthValue, cleanMode } = this
      this.seriesData = this.yAxis.map((item) => {
        const {
          titleShow,
          titleWidth,
          titleHeight,
          titleOffsetX,
          titleOffsetY,
          detailShow,
          detailOffsetX,
          detailOffsetY,
          detailUseformatter,
          detailFormatter,
          titleColor,
          titleBackgroundColor,
        } = item
        return {
          name: item.value || item.key,
          value: result[item.key],
          title: {
            show: cleanMode ? false : titleShow,
            color: titleColor,
            backgroundColor: titleBackgroundColor,
            width: getLengthValue(titleWidth),
            height: getLengthValue(titleHeight),
            lineHeight: getLengthValue(titleHeight),
            offsetCenter: [
              getLengthValue(titleOffsetX),
              getLengthValue(titleOffsetY),
            ],
          },
          detail: {
            show: cleanMode ? false : detailShow,
            fontSize: 20,
            offsetCenter: [
              getLengthValue(detailOffsetX),
              getLengthValue(detailOffsetY),
            ],
            formatter: detailUseformatter ? detailFormatter : '',
          },
        }
      })
      this.renderChart(dispose)
    },
  },
}
</script>

<style lang="less" scoped>
@import '~@/styles/index.less';

.chart-content {
  width: 100%;
  height: 400px;
}
</style>
