<template>
  <n-form-item
    v-show="state != 'hidden'"
    class="switch-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    :rules="rules"
    @click.native="handleClickFormItem"
  >
    <div class="radio-container">
    <el-switch slot="reference" :disabled="state == 'disabled'" :active-text="activeTip" :inactive-text="inactiveTip" :active-value="activeText || true" :inactive-value="inactiveText || false" v-model="value" @change="handleChange"></el-switch>
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
  import common from '@/template/mixins/common'
  import form from '@/template/mixins/form'
  import { CHANGE } from '@/config/eventName'
  export default {
    mixins: [common, form],
    variables: {},
    data() {
      return {
        value: false,
        visible: false,
        timer: null
      }
    },
    computed: {
      rules() {
        const { required, regExp, label } = this
        const res = []
        if (required) {
          res.push(this.getTrueRule({
            required: true,
            message: `请输入${label}`,
            trigger: 'change',
          }))
        }
        return regExp?.reduce((prev, cur) => {
          return prev.concat({
            pattern: cur.reg,
            message: cur.errorTips,
          })
        }, res)
      },
    },
    mounted() {
      if(!this.valueInSlot) {
        const { activeText, inactiveText } = this
        this.value = this.defaultValue ? (activeText || true) : (inactiveText || false)
      }
    },
    methods: {
      handleChange() {
        this._emitEvent(CHANGE)
        this.visible = true
        clearTimeout(this.timer)
        this.timer = setTimeout(() => {
          this.visible = false
        }, 2000)
      },
    },
  }
</script>

<style lang="less" scoped>

  .switch-widget::v-deep {
    margin-left: 0;
    .radio-container {
    }
    .bottom-tips {
      color: #aaa;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
    }
  }
</style>
