<template>
  <div
    class="rich-text-widget"
    :class='commonClassName'
    :style='componentStyle'
    v-html='filterXss(htmlText)'
    @click="click"
    ></div>
</template>

<script>
  import { CLICK } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import _ from 'lodash'
  import { filterXss } from '@/utils'

  export default {
    mixins: [common],
    variables: {},
    methods: {
      filterXss,
      click(e) {
        this._emitEvent(CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
    },
  }
</script>

<style lang="less" scoped>
  .rich-text-widget::v-deep {
    &.is-edit-mode {
      & > * {
        pointer-events: none;
      }
    }
  }
</style>
