<template>
  <div class="dialog-container" :class='{...commonClassName, "drop-zone": false, "is-accept-drag": false}'>
    <template v-for="child in dialogs">
      <component
        :key="child.elementId"
        :is='getElement(child.elementType, child.elementVersion)'
        :ref="child.elementId"
        :data-zone="`${child.elementId}_${child.elementType}`"
        v-bind="child"
        :id="child.elementId"
      ></component>
    </template>
  </div>
</template>

<script>
  import common from '@/template/mixins/common'

  export default {
    mixins: [common],
    variables: {},
    computed: {
      dialogs() {
        return this.isEditMode ? this.visibleChildren : this.children
      }
    },
  }
</script>

