<template>
  <n-form-item
    ref="ElFormItem"
    class="tree-widget"
    :class="commonClassName"
    :style="componentStyle"
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    :rules="rules"
    @click.native="handleClickFormItem"
  >
    <div class="container" v-show="state != 'hidden'">
      <tree-selector
        ref="tree-selector"
        v-load-sync="fetchLoading"
        :height="200"
        :treeRef="elementId"
        :width="widgetWidth"
        :size="size"
        :disabled="disabled"
        :placeholder="placeholder"
        :data="realOptions"
        :defaultProps="defaultProps"
        :filterable="filterable"
        :multiple="multiple"
        :clearable="clearable"
        :collapseTags="collapseTags"
        :checkStrictly="selectType === 'single' ? true : checkStrictly"
        :nodeKey="nodeKey"
        :checkedKeys="defaultCheckedKeys"
        :showAllLevels="showAllLevels"
        :separator="separator"
        @popoverHide="popoverHide"
        :lazy="useLazy"
        :load="loadNode"
        @change="handleChange"
        @nodeClick="handleNodeClick"
        @checkChange="handleCheckChange"
        @expandChange="handleExpandChange"
        @collapseChange="handleCollapseChange"
      />
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
import TreeSelector from '@/components/TreeSelect'
import common from '@/template/mixins/common'
import form from '@/template/mixins/form'
import options from '@/template/mixins/options'
import treeTool from 'tree-tool'
import { CHANGE, CLICK, EXPAND_CHANGE, COLLAPSE_CHANGE } from '@/config/eventName'
import _ from 'lodash'

export default {
  mixins: [common, form, options],
  components: {
    TreeSelector,
  },
  variables: {
    $defaultCheckedKeys(n) {
      if (Array.isArray(n)) {
        this.$refs['tree-selector'].setCheckedKeys(n)
      }
    },
    $dataList(n) {
      if (Array.isArray(n)) {
        this.dataList = n
        this.handleFormatOptions(n)
      } else {
        console.warn('dataList数据格式必须为数组！')
      }
    }
  },
  watch: {
    multiple(v) {
      this.value = v ? [] : undefined
    },
  },
  data() {
    return {
      defaultProps: {
        children: 'children',
        label: 'label',
        isLeaf: 'leaf'
      },
      nodeKey: 'id',
      value: [],
    }
  },
  computed: {
    useLazy() {
      const { lazy, apiRequest, sqlRequest } = this
      // 有api数据集才开启动态加载
      return Boolean(lazy && (apiRequest || sqlRequest))
    },
    computedProps() {
      return {
        multiple: this.multiple,
        checkStrictly: this.checkStrictly,
      }
    },
    rules() {
      const { required, regExp, label } = this
      const res = []
      if (required) {
        res.push(
          this.getTrueRule({
            required: true,
            message: `请选择${label}`,
            trigger: 'change'
          })
        )
      }
      return regExp?.reduce((prev, cur) => {
        return prev.concat({
          pattern: cur.reg,
          message: cur.errorTips,
        })
      }, res)
    },
  },
  methods: {
    handleNodeClick(node, data) {
      this.currentNode = data
      this.currentSource = data.sourceData
      this.currentValue = data.value
      this._emitEvent(CLICK)
    },
    handleCheckChange: _.debounce(function(data, checkedNodes) {
      const valueKey = this.getLastKey('value', this.dataVariables)
      this.currentNode = data
      this.currentSource = data.sourceData
      this.currentValue = data.value
      this.checkedNodeList = checkedNodes
      this.checkedSourceList = checkedNodes.map(item => item.sourceData)
      this.checkedValueList = this.checkedSourceList.map(item => item[valueKey])
      this._emitEvent(CHANGE)
    }, 10),
    handleExpandChange: _.debounce(function(data) {
      this.currentNode = data
      this.currentSource = data.sourceData
      this.currentValue = data.value
      this._emitEvent(EXPAND_CHANGE)
    }, 10),
    handleCollapseChange: _.debounce(function(data) {
      this.currentNode = data
      this.currentSource = data.sourceData
      this.currentValue = data.value
      this._emitEvent(COLLAPSE_CHANGE)
    }, 10),
    async handleChange(value) {
      this.value = value
      this.currentValue = value
      await this.$nextTick()
      // 触发formItem的change校验
      this.dispatchFormItem(this.$refs.ElFormItem, 'el.form.change', value)
      this._emitEvent(CHANGE)
    },
    loadNode(node, resolve) {
      const { level } = node
      // 第一级不用加载
      if(level === 0) {
        return
      }

      setTimeout(async () => {
        const res = await this.fetchRequestData()
        resolve(res)
      }, 60)
    },
    initVal() {
      const treeUtil = treeTool.createInstance({
        id: 'id',
        children: 'children',
        pid: 'parentId'
      })
      if (this.useSlotProps && this.prop && this.slotProps) {
        this.value = this.slotProps[this.prop]
      } else {
        treeUtil.forEach(this.realOptions, (item) => {
          if (item.isDefault) {
            let value = item.value
            let key = item.id
            if (this.multiple) {
              this.value.push(value)
              this.defaultCheckedKeys.push(key)
            } else {
              this.value = value
              this.defaultCheckedKeys = [key]
            }
          }
        })
      }
    },
  },
}
</script>

<style lang="less" scoped>
.tree-widget::v-deep {
  margin-left: 0;
  .container {
  }
  .bottom-tips {
    color: #aaa;
    font-size: 12px;
    line-height: 1;
    padding-top: 4px;
  }
}
</style>
