<template>
  <n-form-item
    class="input-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    :rules="rules"
    @click.native="handleClickFormItem"
  >
    <div class="input-container">
      <el-input
        :clearable="clearable"
        :auto-complete="'on'"
        :style="{ width: inputWidth }"
        :disabled="state === 'disabled'"
        :readonly="state === 'readonly'"
        :type="state === 'hidden' ? 'hidden' : type"
        :class="{ 'show-password': showPassword }"
        :size="size"
        :placeholder="placeholder"
        @input="handleChange"
        v-model="value"
      >
        <i
          slot="append"
          :class="['iconfont', passwordIcon]"
          v-if="showPassword"
          @click="handleIconClick"
        ></i>
      </el-input>
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
  import { CHANGE } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import form from '@/template/mixins/form'

  export default {
    mixins: [common, form],
    variables: {},
    data() {
      return {
        passwordIcon: 'icon-guan',
      }
    },
    computed: {
      rules() {
        const { required, maxLength, minLength, regExp, label } = this
        const res = []
        if (required) {
          res.push(this.getTrueRule({
            required: true,
            message: `请输入${label}`,
            trigger: 'change',
          }))
        }
        if (maxLength > 0) {
          res.push({
            min: minLength,
            max: maxLength,
            message: `长度在 ${minLength} 到 ${maxLength} 个字符`,
            trigger: 'change',
          })
        }
        const result = regExp?.reduce((prev, cur) => {
          return prev.concat({
            pattern: cur.reg,
            message: cur.errorTips,
          })
        }, res)
        return result
      },
    },
    methods: {
      handleChange(value) {
        if (this.isTrim && value) {
          this.value = value.trim?.()
        }
        this._emitEvent(CHANGE)
      },
      handleIconClick() {
        if (this.passwordIcon === 'icon-guan') {
          this.type = 'text'
          this.passwordIcon = 'icon-kai'
        } else {
          this.type = 'password'
          this.passwordIcon = 'icon-guan'
        }
      },
    },
    mounted() {
      if (!this.valueInSlot && this.value === undefined) {
        this.value = this.defaultValue
      }
    },
  }
</script>

<style lang="less" scoped>
  .input-widget::v-deep {
    .input-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      .show-password {
        .el-input-group__append, .el-input-group__prepend {
          padding: 0 10px;
          cursor: pointer;
          .iconfont {
            font-size: 13px;
          }
        }
      }
    }
    .bottom-tips {
      color: #aaa;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
    }
  }
</style>
