<template>
  <div
    class="component-timer"
    :class='commonClassName'
    v-if="isEditMode"
    :id='elementId'
  >
    <img :src="$getImgPrefix('/com-img/timer.png')" alt="">
    <div class="desc">右侧属性中配置定时器功能</div>
    <div class="desc">退出配置后可在底部收纳区中重新打开</div>
    <div class="desc">切换页面时所有定时器会自动停止</div>
  </div>
</template>

<script>
  import common from '@/template/mixins/common'
  import { START_TIMER, CLEAR_TIMER, TIMER_RUN } from '@/config/eventName'

  export default {
    mixins: [common],
    variables: {},
    data() {
      return {
        timer: null
      }
    },
    methods: {
      runTimer() {
        clearTimeout(this.timer)
        this.timer = setTimeout(() => {
          this._emitEvent(TIMER_RUN)
          if (this.mode === 'loop') {
            this.runTimer()
          }
        }, this.timeout * 1000)
      },
      [START_TIMER]() {
        this[CLEAR_TIMER]()
        if (this.isEditMode) {
          return
        }
        if (this.firstRun) {
          this._emitEvent(TIMER_RUN)
        }
        this.runTimer()
      },
      [CLEAR_TIMER]() {
        clearTimeout(this.timer)
      }
    },
    mounted() {
      if (this.autoRun) {
        this[START_TIMER]()
      }
    },
    beforeDestroy() {
      this[CLEAR_TIMER]()
    }
  }
</script>

