<template>
  <div :class="commonClassName" :style="componentStyle" @click="handleStopPropagation">
    <n-tree
      ref="treeRef"
      class="tree-cmpl"
      :class="{ cannotOperate: isEditMode }"
      :data="realOptions"
      :props="defaultProps"
      :show-checkbox="multiple"
      :node-key="nodeKey"
      :check-strictly="selectType === 'single' ? true : checkStrictly"
      :default-expand-all="defaultExpandAll"
      :expand-on-click-node="expandOnClickNode"
      :check-on-click-node="checkOnClickNode"
      :default-checked-keys="defaultCheckedKeys"
      highlight-current
      @check-change="handleChange"
      @check="handleCheck"
      :draggable="draggable"
      icon-class="icon-right"
      :lazy="useLazy"
      :load="loadNode"
      @node-expand="handleExpandChange"
      @node-collapse="handleCollapseChange"
    >
      <div
        slot-scope="{ node, data }"
        class="tree-node"
        :class="computedClass(node, data)"
        @click="handleNodeClick(node, data)"
        :title="data.label"
      >
        <i v-if="data.icon && (!iconPosition || iconPosition === 'left')" :class="data.icon" style="margin-right: 3px;"></i>
        {{ data.label }}
        <i v-if="data.icon && iconPosition === 'right'" :class="data.icon" style="margin-left: 3px;"></i>
      </div>
    </n-tree>
  </div>
</template>

<script>
import TreeSelect from '@/components/TreeSelect'
import common from '@/template/mixins/common'
import options from '@/template/mixins/options'
import treeTool from 'tree-tool'
import { CLICK, CHANGE, EXPAND_CHANGE, COLLAPSE_CHANGE } from '@/config/eventName'
import _ from 'lodash'

export default {
  mixins: [common, options],
  components: {
    TreeSelect,
  },
  variables: {
    $defaultCheckedKeys(n) {
      if (Array.isArray(n)) {
        this.setCheckedKeys(n)
      }
    },
    $dataList(n) {
      if (Array.isArray(n)) {
        this.dataList = n
        this.handleFormatOptions(n)
      } else {
        console.warn('dataList数据格式必须为数组！')
      }
    }
  },
  data() {
    return {
      defaultProps: {
        children: 'children',
        label: 'label',
        isLeaf: 'leaf',
      },
      nodeKey: 'id'
    }
  },
  computed: {
    useLazy() {
      const { lazy, apiRequest, sqlRequest } = this
      // 有api数据集才开启动态加载
      return Boolean(lazy && (apiRequest || sqlRequest))
    },
    // 多选
    multiple() {
      const { selectType: type } = this
      return ['single', 'multiple'].includes(type)
    },
    computedClass() {
      return (node, data) => {
        return {
          'node-disabled': node.disabled && !this.multiple,
          active: data && data.value === this.active,
        }
      }
    },
  },
  methods: {
    handleNodeClick(node, data) {
      this.currentNode = data
      this.currentSource = data.sourceData
      this.currentValue = data.value
      this._emitEvent(CLICK)
    },
    handleCheck(node, data) {
      if (this.selectType === 'single') {
        this.$refs.treeRef?.setCheckedKeys([node.id])
      }
    },
    handleChange: _.debounce(function (data) {
      if (!['single', 'multiple'].includes(this.selectType)) {
        return
      }
      const valueKey = this.getLastKey('value', this.dataVariables)
      const idKey = this.getLastKey('id', this.dataVariables)
      this.currentNode = data
      this.currentSource = data.sourceData
      this.currentValue = data.value
      this.checkedNodeList = this.$refs.treeRef.getCheckedNodes()
      this.checkedSourceList = this.$refs.treeRef.getCheckedNodes().map(item => item.sourceData)
      this.checkedValueList = this.checkedSourceList.map(item => item[valueKey])
      this.defaultCheckedKeys = this.checkedSourceList.map(item => item[idKey])
      this._emitEvent(CHANGE)
    }, 10),
    handleExpandChange: _.debounce(function (data) {
      this.currentNode = data
      this.currentSource = data.sourceData
      this.currentValue = data.value
      this._emitEvent(EXPAND_CHANGE)
    }, 10),
    handleCollapseChange: _.debounce(function (data) {
      this.currentNode = data
      this.currentSource = data.sourceData
      this.currentValue = data.value
      this._emitEvent(COLLAPSE_CHANGE)
    }, 10),
    loadNode(node, resolve) {
      const { level } = node
      // 第一级不用加载
      if (level === 0) {
        return
      }

      setTimeout(async () => {
        const res = await this.fetchRequestData()
        resolve(res)
      }, 60)
    },
    setCheckedKeys(keys) {
      // 只有多选才可以设置默认值
      if(this.multiple) {
        this.$refs.treeRef?.setCheckedKeys(keys)
      }
    },
    initVal() {
      const valueKey = this.getLastKey('value', this.dataVariables)
      const treeUtil = treeTool.createInstance({
        id: 'id',
        children: 'children',
        pid: 'parentId'
      })
      treeUtil.forEach(this.realOptions, (item) => {
        if (item.isDefault) {
          if (this.multiple) {
            this.defaultCheckedKeys.push(item.id)
          } else {
            this.defaultCheckedKeys = [item.id]
          }
        }
      })
      this.$nextTick(() => {
        this.setCheckedKeys(this.defaultCheckedKeys)
        this.checkedNodeList = this.$refs.treeRef.getCheckedNodes()
        this.checkedSourceList = this.$refs.treeRef.getCheckedNodes().map(item => item.sourceData)
        this.checkedValueList = this.checkedSourceList.map(item => item[valueKey])
      })
    },
    handleStopPropagation(e) {
      if(!this.isEditMode && this.stopPropagation) {
        e.stopPropagation()
      }
    }
  },
}
</script>

<style lang="less" scoped>
@import '~@/styles/index.less';
.tree-cmpl::v-deep {
  &.cannotOperate {
    pointer-events: none;
  }
  .tree-node {
    width: 100%;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    &.node-disabled {
      cursor: not-allowed;
      color: #aaaaaa;
    }
    &.active {
      color: #3f92fe;
    }
  }
  & > .el-tree-node > .el-tree-node__content {
    background-color: unset;
  }
  .el-tree-node__content .el-tree-node__expand-icon {
    margin-right: 3px;
  }
  & > .el-tree-node > .el-tree-node__content {
    background-color: unset;
  }
  .el-tree-node__expand-icon.expanded {
    color: unset;
  }
}
</style>
