<template>
  <div
    class='calendar-widget'
    :class='commonClassName'
    :style='componentStyle'
    v-loading='loading'
    @click="handleStopPropagation"
  >
    <FullCalendar
      ref='fullCalendar'
      :options='calendarOptions'
    >
      <template v-slot:eventContent='arg'>
        <el-tooltip
          effect="dark"
          :content="`${formatTime(arg.event.start)} ~ ${formatTime(arg.event.end)}\n${arg.event.title}`"
          placement="top-start"
        >
          <i :style='{backgroundColor: arg.backgroundColor, color: arg.textColor}' class='event-title-content'>{{ arg.event.title }}</i>
        </el-tooltip>
      </template>
    </FullCalendar>
    <div class='event-list' v-if='showEventsController'>
      <div class='list-header'>
        <el-button type='primary' icon='el-icon-plus' @click='handleAddEvent'>新建提醒</el-button>
      </div>
      <el-collapse class='list-collapse' v-model='activeCollapse'>
        <el-collapse-item
          v-for='group in groups'
          :key='group.groupName'
          :name='group.groupName'
        >
          <template slot="title">
            <div class='list-collapse-title'>
              <span :style='{backgroundColor: group.backgroundColor}' class='theme'></span>
              <span class='title'>{{ group.groupName }}</span>
            </div>
          </template>
          <div class='list-item' v-for='event in getEventsByGroupName(group.groupName)' :key='event.id'>
            <div class='name' @click='handleEdit(event)'>{{ event.title }}</div>
            <i style='margin: 0 10px;' class='iconfont el-icon-edit-outline' @click='handleEdit(event)'></i>
            <i class='iconfont el-icon-delete' @click='handleDelete(event)'></i>
          </div>
          <div v-if='!getEventsByGroupName(group.groupName).length' class='emptyText' style='text-align: center;font-size: 12px;color: #888888;'>暂无提醒</div>
        </el-collapse-item>
      </el-collapse>
    </div>
    <el-dialog
      custom-class="low-code-dialog"
      :title="eventForm.id ? '编辑提醒' : '新增提醒'"
      :visible.sync="dialogVisible"
      :close-on-click-modal="false"
      :modal-append-to-body="true"
      :append-to-body="true"
      width="600px"
      top='15vh'
      :before-close="handleClose"
    >
      <n-form
        ref="variableForm"
        :model="eventForm"
        label-width="85px"
        @submit.native.prevent
        size='mini'
      >
        <n-form-item label="创建时间" v-if='eventForm.createTime'>
          <span>{{ eventForm.createTime }}</span>
        </n-form-item>
        <n-form-item label="提醒时间">
          <el-date-picker
            v-model="eventTimeRange"
            type="datetimerange"
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期">
          </el-date-picker>
        </n-form-item>
        <n-form-item label="提醒分类" prop="groupName">
          <el-select v-model='eventForm.groupName' @change='eventForm.title = ""'>
            <el-option v-for='group in groups' :key='group.groupName' :label='group.groupName' :value='group.groupName'></el-option>
          </el-select>
        </n-form-item>
        <n-form-item label="提醒内容" prop="title">
          <el-button-group style='margin-bottom: 10px;'>
            <el-button :type='includesTemplate ? "primary" : ""' @click='handleChangeTemplate'>内容模板</el-button>
            <el-button :type='!includesTemplate ? "primary" : ""' :disabled='!templateEditable' @click='eventForm.title = ""'>自定义</el-button>
          </el-button-group>
          <div>
            <el-select style='width: 100%;' v-if='includesTemplate' v-model='eventForm.title'>
              <el-option v-for='template in currentTemplates' :key='template' :label='template' :value='template'></el-option>
            </el-select>
            <el-input style='width: 100%;' v-else v-model='eventForm.title' placeholder='请输入'></el-input>
          </div>
        </n-form-item>
      </n-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import FullCalendar from '@fullcalendar/vue'
  import dayGridPlugin from '@fullcalendar/daygrid'
  import timeGridPlugin from '@fullcalendar/timegrid'
  import interactionPlugin from '@fullcalendar/interaction'
  import zhLocale from '@fullcalendar/core/locales/zh-cn'

  import common from '@/template/mixins/common'
  import { formatTime } from '@/utils/format'
  import _ from 'lodash'
  import API from '@/api/application'

  export default {
    components: {
      FullCalendar
    },
    mixins: [common],
    variables: {},
    data() {
      return {
        loading: false,
        dialogVisible: false,
        activeCollapse: [],
        initialEvents: [],
        eventForm: {
          id: '',
          title: '',
          groupName: '',
          start: '',
          end: '',
          backgroundColor: '',
          textColor: '',
          createTime: ''
        },
      }
    },
    computed: {
      toolbarLeft() {
        const tools = {
          dayGridMonth: this.dayGridMonth,
          timeGridWeek: this.timeGridWeek,
          timeGridDay: this.timeGridDay,
        }
        return Object.keys(tools).filter(key => tools[key]).join(',')
      },
      currentTemplates() {
        return this.groups.find(item => item.groupName === this.eventForm.groupName)?.templates || []
      },
      templateEditable() {
        return this.groups.find(item => item.groupName === this.eventForm.groupName)?.editable
      },
      includesTemplate() {
        const current = this.groups.find(item => item.groupName === this.eventForm.groupName)
        return current?.templates.includes(this.eventForm.title) || !this.templateEditable
      },
      headerToolbar() {
        const left = this.toolbarLeft.length ? this.toolbarLeft : this.title ? 'title' : undefined
        const center = left === 'title' ? undefined : this.title ? 'title' : undefined
        return {
          left,
          center,
          right: this.showTools ? 'prev today next' : ''
        }
      },
      calendarOptions() {
        return {
          plugins: [
            dayGridPlugin,
            timeGridPlugin,
            interactionPlugin // needed for dateClick
          ],
          headerToolbar: this.headerToolbar,
          locale: zhLocale,
          initialView: 'dayGridMonth',
          initialEvents: this.initialEvents, // alternatively, use the `events` setting to fetch from a feed
          editable: true,
          selectable: true,
          selectMirror: true,
          dayMaxEvents: true,
          weekends: true,
          select: this.handleDateSelect,
          eventClick: this.handleEventClick,
          eventsSet: this.handleEvents,
          eventChange: this.handleEventChange,
          /* you can update a remote database when these fire:
          eventAdd:
          eventChange:
          eventRemove:
          */
        }
      },
      eventTimeRange: {
        get() {
          return [this.eventForm.start, this.eventForm.end]
        },
        set(value) {
          this.eventForm.start = this.formatTime(value[0])
          this.eventForm.end = this.formatTime(value[1])
        }
      }
    },
    watch: {
      showEventsController(n) {
        this.$nextTick(() => {
          const calendarApi = this.$refs.fullCalendar.getApi()
          calendarApi.resetOptions(this.calendarOptions)
        })
      }
    },
    mounted() {
      if (!this.isEditMode) {
        this.handleGetEvents()
      }
      this.activeCollapse = this.groups.map(item => item.groupName)
    },
    methods: {
      formatTime(time) {
        const time_stamp = new Date(time).getTime()
        return formatTime(time_stamp, 'YYYY-MM-DD HH:mm')
      },
      getEventsByGroupName(groupName) {
        return this.initialEvents.filter(item => item.groupName === groupName)
      },
      handleUpdateEvents(events) {
        const calendarApi = this.$refs.fullCalendar.getApi()
        calendarApi.unselect()
        calendarApi.setOption('events', events)
      },
      handleDateSelect(selectInfo) {
        // if (!this.showEventsController) {
        //   return
        // }
        // this.eventForm = {
        //   id: '',
        //   title: '',
        //   groupName: '',
        //   start: `${selectInfo.startStr} 00:00`,
        //   end: `${selectInfo.startStr} 23:59`,
        //   backgroundColor: '',
        //   textColor: ''
        // }
        // this.handleShowEditDialog()
      },
      handleEventClick(clickInfo) {
        if (!this.showEventsController) {
          return
        }
        const { id, title, start, end, backgroundColor, textColor, extendedProps } = clickInfo.event
        this.eventForm = {
          id,
          title,
          backgroundColor,
          textColor,
          groupName: extendedProps.groupName,
          start: this.formatTime(start),
          end: this.formatTime(end),
          createTime: this.formatTime(extendedProps.createTime)
        }
        this.handleShowEditDialog()
      },
      handleEvents(events) {
        // console.log(events)
      },
      handleAddEvent() {
        this.eventForm = {
          id: '',
          title: '',
          groupName: '',
          start: '',
          end: '',
          backgroundColor: '',
          textColor: '',
          createTime: ''
        }
        this.handleShowEditDialog()
      },
      handleShowEditDialog() {
        this.dialogVisible = true
      },
      handleClose(done) {
        this.dialogVisible = false
        done()
      },
      handleChangeTemplate() {
        if (this.includesTemplate) {
          return
        }
        const current = this.groups.find(item => item.groupName === this.eventForm.groupName)
        this.eventForm.title = current?.templates?.[0]
      },
      handleEdit(event) {
        this.eventForm = _.cloneDeep(event)
        this.handleShowEditDialog()
      },
      async handleDelete(event) {
        await this.$confirm(`确认删除事件[${event.title}]吗？`)
        const value = this.initialEvents.filter(item => item.id !== event.id)
        const appId = this.$route.query.id
        const appDesignId = this.$route.query.vid
        const data = {
          appId: appId ? Number(appId) : undefined,
          appDesignId: appDesignId ? Number(appDesignId) : undefined,
          neverExpire: true,
          preferences: [
            {
              key: this.getKey(),
              value: JSON.stringify(value)
            }
          ]
        }
        this.handleSaveEvents(data, value)
      },
      getKey() {
        let username
        try {
          username = JSON.parse(window.localStorage.getItem('userInfo')).username
        } catch (e) {
          console.log(e)
        }
        if (!username) {
          return
        }
        return `${username}__${this.elementId}__calendar`
      },
      confirm() {
        if (!this.eventForm.start) {
          this.$message.error('请选择开始日期')
          return
        }
        if (!this.eventForm.end) {
          this.$message.error('请选择结束日期')
          return
        }
        if (!this.eventForm.groupName) {
          this.$message.error('请选择提醒分类')
          return
        }
        if (!this.eventForm.title) {
          this.$message.error('请输入提示内容')
          return
        }
        let copy = _.cloneDeep(this.initialEvents)
        const currentGroup = this.groups.find(item => item.groupName === this.eventForm.groupName)
        if (!this.eventForm.id) {
          copy.push({
            ...this.eventForm,
            id: `${copy.length + 1}`,
            backgroundColor: currentGroup.backgroundColor,
            textColor: currentGroup.textColor,
            createTime: this.formatTime(new Date())
          })
        } else {
          copy = copy.map(item => {
            if (item.id === this.eventForm.id) {
              return {
                ...this.eventForm,
                createTime: this.formatTime(new Date())
              }
            }
            return item
          })
        }
        const appId = this.$route.query.id
        const appDesignId = this.$route.query.vid
        const data = {
          appId: appId ? Number(appId) : undefined,
          appDesignId: appDesignId ? Number(appDesignId) : undefined,
          neverExpire: true,
          preferences: [
            {
              key: this.getKey(),
              value: JSON.stringify(copy)
            }
          ]
        }
        this.handleSaveEvents(data, copy)
      },
      handleEventChange({event}) {
        let copy = _.cloneDeep(this.initialEvents)
        copy = copy.map(item => {
          if (item.id === event.id) {
            return {
              ...item,
              start: this.formatTime(event.start),
              end: this.formatTime(event.end),
              createTime: this.formatTime(new Date())
            }
          }
          return item
        })
        const appId = this.$route.query.id
        const appDesignId = this.$route.query.vid
        const data = {
          appId: appId ? Number(appId) : undefined,
          appDesignId: appDesignId ? Number(appDesignId) : undefined,
          neverExpire: true,
          preferences: [
            {
              key: this.getKey(),
              value: JSON.stringify(copy)
            }
          ]
        }
        this.handleSaveEvents(data, copy)
      },
      handleSaveEvents(data, events) {
        this.loading = true
        this.$http({
          url: data.appId ? API.appStorePreferencesSaveOrUpdate : API.appDesignJsonDataConfigSave,
          data
        })
          .then(() => {
            this.initialEvents = _.cloneDeep(events)
            this.handleUpdateEvents(this.initialEvents)
            this.dialogVisible = false
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleGetEvents() {
        const appId = this.$route.query.id
        const appDesignId = this.$route.query.vid
        const data = {
          appId: appId ? Number(appId) : undefined,
          appDesignId: appDesignId ? Number(appDesignId) : undefined,
          key: this.getKey()
        }
        this.loading = true
        this.$http({
          url: appId ? API.appStorePreferencesGet : API.appDesignJsonDataConfigQuery,
          data
        })
          .then(({ data }) => {
            this.initialEvents = JSON.parse(data || '[]')
            this.handleUpdateEvents(this.initialEvents)
          })
          .finally(() => {
            this.loading = false
          })
      },
      handleStopPropagation(e) {
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      }
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .calendar-widget::v-deep {
    display: flex;
    width: 100%;
    height: 100%;
    &.is-edit-mode {
      .event-list {
        pointer-events: none;
      }
      .fc {
        pointer-events: none;
      }
    }
    .event-list {
      display: flex;
      flex-direction: column;
      width: 250px;

      .list-header {
        display: flex;
        align-items: flex-start;
        justify-content: flex-end;
        height: 44px;
        padding-right: 10px;
      }

      .list-item {
        display: flex;
        align-items: center;
        font-size: 12px;
        .name {
          flex: 1;
          text-overflow: ellipsis;
          overflow: hidden;
          white-space: nowrap;
          &:hover {
            cursor: pointer;
            color: @drop-main-color;
          }
        }
        .iconfont {
          &:hover {
            cursor: pointer;
            color: @drop-main-color;
          }
        }
      }

      .list-collapse {
        flex: 1;
        overflow: auto;
        border-left: none;
        border-right: none;
        .el-collapse-item__header {
          padding-right: 0;
          height: 30px;
          line-height: 30px;
        }
        .list-collapse-title {
          display: flex;
          align-items: center;
          .title {
            width: 200px;
            text-overflow: ellipsis;
            overflow: hidden;
            white-space: nowrap;
            font-size: 12px;
            color: #333333;
          }
          .theme {
            display: inline-block;
            width: 4px;
            height: 15px;
            margin-right: 10px;
          }
        }
      }
    }
    .fc {
      flex: 1;
      .fc-h-event {
        border: none;
        background-color: #fff;
      }
      .fc-toolbar-chunk {
        & > * {
          margin-bottom: 10px;
        }
        & > .fc-toolbar-title {
          margin-bottom: 18px;
        }
      }
    }

    b { /* used for event dates/times */
      margin-right: 3px;
    }

    .event-title-content {
      display: inline-block;
      width: 100%;
      height: 100%;
      border-radius: 3px;
    }

    .fc-header-toolbar.fc-toolbar {
      padding: 0 10px;
      margin-bottom: 0;
    }

    .fc-button-primary {
      display: inline-block;
      white-space: nowrap;
      cursor: pointer;
      background: #fff;
      border: 1px solid #dcdfe6;
      color: #606266;
      -webkit-appearance: none;
      text-align: center;
      box-sizing: border-box;
      outline: none;
      margin: 0;
      transition: .1s;
      font-weight: 500;
      -moz-user-select: none;
      -webkit-user-select: none;
      -ms-user-select: none;
      padding: 7px 15px;
      font-size: 12px;
      border-radius: 3px;
      box-shadow: none;
      &:hover {
        color: #409eff;
        border-color: #c6e2ff;
        background-color: #ecf5ff;
      }
      &:focus {
        color: #409eff;
        border-color: #c6e2ff;
        background-color: #ecf5ff;
        box-shadow: none;
      }

      &.fc-prev-button {
        margin-right: 10px;
        padding: 6.55px 10px;
      }
      &.fc-next-button {
        margin-left: 10px;
        padding: 6.55px 10px;
      }
    }

    .fc-button-primary.fc-button-active {
      color: #fff;
      background-color: #409eff;
      border-color: #409eff;
      &:focus {
        box-shadow: none;
      }
    }
  }
</style>
