<template>
  <el-image
    class="image-widget"
    :class='commonClassName'
    :style='componentStyle'
    :fit="fit"
    :alt="alt"
    :lazy="lazy"
    :showPreview="showPreview"
    :src='getImageUrl(src, false)'
    :preview-src-list='previewSrcList'
    @click.native="click"
  >
    <div slot="error" class="image-slot">
      <span v-if="alt">{{alt}}</span>
    </div>
  </el-image>
</template>

<script>
  import { CLICK } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import { getImageUrl } from '@/utils'

  export default {
    mixins: [common],
    variables: {},
    computed: {
      previewSrcList() {
        return this.showPreview && !this.isEditMode ? [getImageUrl(this.src)] : undefined
      }
    },
    methods: {
      getImageUrl,
      click(e) {
        this._emitEvent(CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
    },
  }
</script>

<style lang="less" scoped>
  .image-widget::v-deep {
    &.is-edit-mode {
      > img {
        pointer-events: none;
      }
    }
    .image-slot {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      background: #f5f7fa;
      color: #ccc;
    }
  }
</style>
