<template>
  <n-form
    v-loading='loading'
    ref="formRef"
    class="layout-container"
    :class='{...commonClassName, "table-mode": tableMode}'
    :style='{...componentStyle, rowGap: rowGap, columnGap: columnGap}'
    :model="formData"
    :size="size"
    :labelPosition="labelPosition"
    :labelWidth="labelWidth"
    @submit.native.prevent
    @click.native="handleStopPropagation"
  >
    <template v-for="child in visibleChildren">
      <component
        v-if='isFormItemVisible(child)'
        :key="child.elementId"
        :is='getElement(child.elementType, child.elementVersion)'
        :ref="child.elementId"
        :data-zone="`${child.elementId}_${child.elementType}`"
        v-bind="{ ...child, slotProps: formData }"
        :id="child.elementId"
        :containerScopeStyle='tableMode ? {display: "flex", alignItems, justifyContent, flexDirection, flexWrap} : undefined'
      ></component>
    </template>
  </n-form>
</template>

<script>
  import { treeUtil } from '@/utils/tree-utils'
  import _ from 'lodash'
  import { VALIDATE, RESET_FIELDS, DATA_LOAD_SUCCESS } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import { formTypes } from '@/config/widgets'
  import request from '@/request/request'
  import { isValidJsonString } from '@/utils/validateUtils'
  import { debounce } from '@/utils'
  import { globalEmit } from '@/renderer/tools/eventBus'
  import { PROPS } from '@/config/eventKey'
  import { pageInject } from '@/renderer/tools/pageStore'
  export default {
    inject: [...pageInject],
    mixins: [common],
    variables: {
      $formData() {
        this.$refs.formRef.clearValidate()
      }
    },
    provide() {
      return {
        update: (key, value) => {
          if(key) {
            this.$set(this.formData, key, value)
            this.refreshData()
          }
        },
      }
    },
    data() {
      return {
        rowGap: 10,
        columnGap: 10,
        formData: {},
        fields: [],
        loading: false
      }
    },
    methods: {
      async [VALIDATE]() {
        this.$refs.formRef.clearValidate()
        await this.$refs.formRef.validate()

      },
      [RESET_FIELDS]() {
        this.$refs.formRef.resetFields()
        this.refreshData()
        setTimeout(() => {
          this.$refs.formRef.clearValidate()
        }, 0)
      },
      // 深拷贝整个对象,解决数据没更新的bug
      refreshData: debounce(function () {
        this.formData = _.cloneDeep(this.formData)
      }),
      handleSearch() {
        if (this.apiRequest) {
          const params = this.getApiRequestParams(this.apiRequest)
          if (!params.url) {
            return
          }
          this.loading = true
          request({
            method: params.method,
            url: params.url,
            params: params.params,
            data: params.data,
            headers: params.headers,
          })
            .then((res) => {
              this.handleSuccess(res.data)
            })
            .finally(() => {
              this.loading = false
            })
          return
        }
        if (this.sqlRequest) {
          const params = this.getSqlParams(this.sqlRequest)
          this.loading = true
          this.$http({
            url: params.url,
            data: params.data,
          })
            .then((res) => {
              this.handleSuccess(res)
            })
            .finally(() => {
              this.loading = false
            })
          return
        }
        if (this.staticData) {
          this.handleSuccess(this.staticData)
        }
      },
      handleSuccess(res) {
        const response = {
          response: res
        }
        this.loading = true
        const formDataPath = this.dataVariables.find(item => item.key === 'formData').path
        const fields = _.get(response, formDataPath)
        let obj
        if (_.isObject(fields)) {
          obj = fields
        } else if (fields && typeof fields === 'string' && isValidJsonString(fields)) {
          try {
            obj = JSON.parse(fields)
          } catch (e) {
            console.log(e)
          }
        }
        if (obj && _.isObject(obj)) {
          this.fields = Object.keys(obj)
          this.formData = Object.fromEntries(this.fields.map(key => ([key, undefined])))
          const allFormItems = treeUtil.findNodeAll(this.children, node => formTypes.includes(node.elementType) && this.getElementPropsById(node.elementId).isVisible !== false)
          if (allFormItems && allFormItems.length) {
            allFormItems.forEach(item => {
              const { prop, useSlotProps } = this.getElementPropsById(item.elementId)
              globalEmit.call(this, PROPS, { elementId: item.elementId, data: {isVisible: this.fields.includes(prop) && useSlotProps} })
            })
          }
        }
        this.$nextTick(() => {
          this.loading = false
          this._emitEvent(DATA_LOAD_SUCCESS)
        })
      },
      isFormItemVisible(item) {
        if (this.isEditMode) {
          return true
        }
        const formDataPath = this.dataVariables.find(item => item.key === 'formData').path
        if (!formDataPath.length) {
          return true
        }
        const formItems = treeUtil.findNodeAll(item.children, node => formTypes.includes(node.elementType) && this.getElementPropsById(node.elementId).isVisible !== false)
        if (!formItems || !formItems.length) {
          return false
        }
        const isEnable = formItems.some(formItem => {
          const { useSlotProps, prop } = this.getElementPropsById(formItem.elementId)
          return this.fields.includes(prop) && useSlotProps
        })
         return isEnable
      },
      handleStopPropagation(e) {
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      }
    },
    async mounted() {
      if (!this.isEditMode) {
        const formDataPath = this.dataVariables.find(item => item.key === 'formData').path
        if (formDataPath.length) {
          this.handleSearch()
        }
      }
      // 初始渲染值改变页不触发校验规则
      this.$nextTick(() => {
        this.$refs.formRef.clearValidate()
      })
    }
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .layout-container::v-deep {
    box-sizing: border-box;
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    row-gap: @layout-margin;
    column-gap: @layout-margin;
    place-items: stretch;
    grid-auto-rows: auto;
    grid-area: span 1 / span 1 / auto / auto;
    position: relative;

    &.table-mode {
      row-gap: 1px !important;
      column-gap: 1px !important;
      & > .layout {
        box-shadow: 0 0 0 1px #999999 !important;
      }
    }

    &.is-edit-mode {
      outline: 1px dashed #cccccc;
      padding: 2px;
    }

    & > .layout::v-deep {
      height: 100%;
    }
  }
</style>
