import { previewRoutePath } from '@/config/code-var'
import {
  formatQuery
} from '@/utils'
import qs from 'querystring'
/**
 * 跳转到预览页面
 * @param { vid } 版本id
 * @param { pageId } 选中的页面id
 * @param { type } 访问类型 0 [预览] 1 [访问实例]
 */
export const jumpPreview = ({
  appVersionId,
  pageId,
  appId,
  type = 0,
}) => {
  const idKey = {
    0: 'vid',
    1: 'id'
  } [type]

  const params = {
    [idKey]: appVersionId,
    pageId,
    appId
  }
  const url = `/front${previewRoutePath}?${formatQuery(params)}`
  if (type === 0) {
    window.open(url)
  } else {
    return url
  }
}

/**
 * 往链接上追加参数
 * @param {string} url 需要追加的url
 * @param {object} params 参数
 */
export const addParamsToUrl = (url, params) => {
  const index = url.indexOf('?')
  let prefix = ''
  let paramsText = ''
  // 获取链接上自带的参数
  if (index !== -1) {
    prefix = url.slice(0, index)
    paramsText = url.slice(index + 1)
  } else {
    prefix = url
  }
  // 合并参数
  const concatParams = {
    ...qs.parse(paramsText),
    ...params
  }
  // 过滤没用的参数
  const keys = Object.keys(concatParams).filter(item => item.trim())
  let finallyParams = {}
  keys.forEach(key => {
    finallyParams[key] = concatParams[key]
  })
  const result = `${prefix}?${formatQuery(finallyParams)}`
  return result
}