import {
  RESET,
  DATA_LOAD_SUCCESS,
  UPDATE_DATA
} from '@/config/eventName'
import echarts from '@/lib/echarts'
import request from '@/request/request'
import {
  asyncFunc,
  debounce,
} from '@/utils'
import _ from 'lodash'

import { chartType } from '@/config/widgets'
import { RECEIVE_PROPS, RECEIVE_STYLES } from '@/config/eventKey'
import trigger from '@/renderer/tools/trigger'
export default {
  computed: {
    // 是否是简洁模式
    cleanMode() {
      return this.styleMode === 'clean'
    },
  },
  watch: {
    fetchLoading(val) {
      if(this.useFetchLoading) {
        if (val) {
          this.chartInstance ?.showLoading()
        } else {
          this.chartInstance ?.hideLoading()
        }
      }
    },
  },
  data() {
    return {
      chartInstance: null,
      yAxis: [],
      xAxisData: [],
      fetchLoading: false,
      dataset: {
        // 用 dimensions 指定了维度的顺序。直角坐标系中，
        // 默认把第一个维度映射到 X 轴上，第二个维度映射到 Y 轴上。
        // 如果不指定 dimensions，也可以通过指定 series.encode
        // 完成映射，参见后文。
        dimensions: [],
        source: [],
      },
      hadInitEvent: false,
      showSymbol: true
    }
  },
  mounted() {
    this.getChartData()
  },
  created() {
    // 渲染添加防抖,避免出现重复画面, 这里在created写是因为如果在methods里面写,多个相同实例会共用了一个方法,只执行了最后一次
    this.renderChart = debounce(this.renderChart)
  },
  beforeDestroy () {
    this.chartInstance ?.dispose()
  },
  methods: {
    _receiveData({ type }) {
      // 配置更新,刷新图表
      if([RECEIVE_STYLES, RECEIVE_PROPS].includes(type)) {
        this.getChartData()
      }
    },
    /**
     * 渲染方法
     * @param {*} dispose 是否要把旧的实例销毁
     */
    async renderChart (dispose = true) {
      // 此处 $nextTick是为了能拿到$refs.chart
      await this.$nextTick()
      const { chart } = this.$refs
      if(!chart) {
        return
      }
      dispose && this.chartInstance ?.dispose()

      // 未加载完依赖的话继续等待
      if(!echarts.instance) {
        await echarts.init()
      }
      if(!this.chartInstance || dispose) {
        // 基于准备好的dom，初始化echarts实例
        this.chartInstance = echarts.instance.init(
          chart,
          this.styleMode === 'dark' ? 'dark' : 'light'
        )
      }
      // 指定图表的配置项和数据
      let option = this.getOption()
      this.chartOptions = _.cloneDeep(option)
      if(this.optionsOverwriteId) {
        // 格式化配置得到最终配置
        option = trigger.getFuncValueByParams({
          functionId: this.optionsOverwriteId,
          extraParams: [{ key: 'options', value: option }],
          vueThis: this
        })
      }
      // 使用刚指定的配置项和数据显示图表。
      this.chartInstance.setOption(option, {
        notMerge: true,
        // 是否不立即更新(在动画完了再更新)
        lazyUpdate: false,
      })

      // 编辑模式不初始化监听事件
      if (!this.isEditMode && (dispose || !this.hadInitEvent)) {
        this.initEvents()
        this.hadInitEvent = true
      }
    },
    getDataZoomRange({
      start,
      end,
      data
    }) {
      if (!data.length) {
        return []
      }
      const startIndex = Math.floor((start * data.length) / 100)
      const endIndex = Math.floor((end * data.length) / 100)
      const startX = data[startIndex]
      const endX = data[endIndex < 1 ? 0 : endIndex - 1]
      return [startX, endX]
    },
    getBaseOption() {
      const {
        showTitle,
        title,
        titleAlign,
        tooltip,
        xSetting = {},
        ySetting = {},
        grid,
        theme = {},
        elementType
      } = this
      const {
        useMax,
        max,
        useMin,
        min
      } = ySetting
      // 有坐标轴的
      const hasCoord = ['line', 'bar', 'scatter'].includes(chartType[elementType])
      // 边距设置, 关闭的时候四个方向的值为空
      const gridSetting = (function() {
        if(!grid) {
          return {}
        }
        const { useGrid, ...others } = grid
        return Object.prototype.hasOwnProperty.call(grid, 'useGrid') && !useGrid ? {
          top: '0',
          bottom: '0',
          left: '0',
          right: '0',
        } : others
      })()
      let option
      // 简洁模式下,只保留基础的样式
      if (this.cleanMode) {
        option = {
          color: theme.color,
          dataset: this.dataset,
          grid: {
            ...gridSetting,
            containLabel: true,
          },
          xAxis: hasCoord && {
            type: 'category',
            // 刻度线
            axisTick: {
              show: false,
            },
            // 刻度线标签
            axisLabel: {
              show: false,
            },
            axisLine: {
              show: false,
            },
            // 网格
            splitLine: {
              show: false
            }
          },
          yAxis: hasCoord && {
            type: 'value',
            // 网格
            splitLine: {
              show: false,
            },
            // 刻度线标签
            axisLabel: {
              show: false,
            },
            // 刻度线
            axisTick: {
              show: false,
            },
            axisLine: {
              show: false
            }
          },
          series: this.getSeries(),
        }
      } else {
        option = {
          color: theme.color,
          title: showTitle && {
            text: title,
            left: titleAlign,
          },
          tooltip,
          dataset: this.dataset,
          legend: this.getLegend(),
          grid: {
            ...gridSetting,
            containLabel: true,
          },
          toolbox: this.getToolbox(),
          xAxis: hasCoord && {
            name: xSetting.title,
            nameLocation: xSetting.position,
            nameTextStyle: {
              padding: [15, 0, 0, 0],
            },
            type: 'category',
            // 网格
            splitLine: {
              show: xSetting.splitLine,
            },
            // 刻度线
            axisTick: {
              show: xSetting.axisTick,
            },
            // 刻度线标签
            axisLabel: {
              show: xSetting.axisLabel,
              rotate: xSetting.rotate,
              formatter: xSetting.useFormat ?
                this.getFormatFn(xSetting) : undefined,
            },
            axisLine: {
              show: xSetting.axisLine,
            },
            boundaryGap: xSetting.boundaryGap !== undefined ? xSetting.boundaryGap : true
          },
          yAxis: hasCoord && {
            name: ySetting.title,
            type: 'value',
            nameLocation: ySetting.position,
            // 网格
            splitLine: {
              show: ySetting.splitLine,
            },
            // 刻度线
            axisTick: {
              show: ySetting.axisTick,
            },
            // 刻度线标签
            axisLabel: {
              show: ySetting.axisLabel,
            },
            axisLine: {
              show: ySetting.axisLine,
            },
            max: useMax ? max : undefined,
            min: useMin ? min : undefined,
          },
          series: this.getSeries(),
          dataZoom: this.getDataZoom(),
        }
      }
      return option
    },
    // 未设置该名字方法时会报错,需要各个图表组件自定义同名方法覆盖mixins里同名方法
    throwErrorTip(str) {
      const {
        elementId,
        elementName
      } = this
      throw new Error(`${elementName}(${elementId})该组件没有初始化的自己的${str}方法`)
    },
    // 不同图表类型特有的设置
    getTypeSetting() {
       this.throwErrorTip('getTypeSetting')
    },
    // 图表配置
    getOption() {
      this.throwErrorTip('getOption')
    },
    // 初始化事件
    initEvents() {
      this.throwErrorTip('initEvents')
    },
    getLegend() {
      const {
        showLegend,
        legendPosition,
        legendScroll
      } = this
      const topPosition = '30px'
      const position = {
        top: {
          top: topPosition,
        },
        bottom: {
          bottom: '0px',
        },
        left: {
          left: '0px',
          top: 'center',
          orient: 'vertical',
        },
        right: {
          right: '0px',
          top: 'center',
          orient: 'vertical',
        },
      } [legendPosition]
      return showLegend && { ...position, type: legendScroll ? 'scroll' : 'plain' }
    },
    getToolbox() {
      const {
        toolbox
      } = this
      const {
        saveAsImage,
        dataView,
        magicType,
        dataZoom,
        restore,
        orient,
      } = toolbox
      return {
        show: !!Object.keys(toolbox).length,
        orient,
        feature: {
          saveAsImage: {
            show: saveAsImage,
            title: '导出图片'
          },
          dataView: {
            show: dataView,
            readOnly: false,
            title: '数据视图',
            lang: ['数据视图', '关闭', '刷新'],
          },
          magicType: {
            show: magicType,
            type: ['line', 'bar', 'stack'],
            title: {
              line: '折线图',
              bar: '柱状图',
              stack: '堆叠',
              tiled: '平铺',
            },
          },
          dataZoom: {
            show: dataZoom,
            title: {
              zoom: '区域缩放',
              back: '区域缩放还原'
            },
          },
          restore: {
            show: restore,
            title: '重置'
          },
        },
      }
    },
    getSeries() {
      const {
        cleanMode,
        elementType,
        ySetting: {
          useSymbol,
          symbolSize,
          dataLabelShow,
          dataLabelPosition,
          dataLabelUseformatter,
          dataLabelFormatter,
        } = {},
      } = this
      const size = {
        small: 5,
        medium: 10,
        large: 15,
      } [symbolSize]
      return this.yAxis.map((item, index) => {
        const echartType = chartType[elementType]
        const hasSymbolSetting = ['line', 'bar', 'scatter'].includes(echartType)
        // 标记点配置
        let symbolSetting = {}
        if(hasSymbolSetting) {
          // 关闭了标记点或者除散点图之外的简洁模式
          if(!useSymbol || cleanMode && echartType !== 'scatter') {
            symbolSetting = { symbol: 'none' }
          } else {
            symbolSetting = {
              symbol: item.symbol,
              symbolSize: size,
            }
          }
        }
        const labelSetting = {
          label: {
            show: dataLabelShow && !cleanMode,
            position: dataLabelPosition,
            formatter: dataLabelUseformatter ?
              dataLabelFormatter : undefined,
          }
        }
        return {
          // 如果不设为false的话, 在某些机器上,每次echart.setOption的时候, 都无法释放内存
          showSymbol: this.showSymbol,
          type: item.type || echartType,
          ...symbolSetting,
          ...labelSetting,
          ...this.getTypeSetting(item, index, symbolSetting),
        }
      })
    },
    getDataZoom() {
      const {
        dataZoom: {
          start,
          end,
          orient
        } = {}
      } = this
      return (
        orient && [{
            type: 'inside',
            start,
            end,
            orient,
          },
          {
            start: 0,
            end: 20,
            orient,
          },
        ]
      )
    },
    formatRequestData(response, dispose = true) {
      this.responseData = response
      const {
        dataVariables,
        dynamicVariables,
        dataType,
        isEditMode
      } = this
      let dataPath, xAxisKey, result = []
      // 动态图例, 计算出xAxisKey,yAxis
      if (dataType === 'dynamic') {
        let legendName, legendData
        dynamicVariables.forEach(item => {
          switch (item.key) {
            case 'sourcePath':
              dataPath = item.path;
              break;
            case 'legendName':
              legendName = item.path[item.path.length - 1];
              break;
            case 'legendData':
              legendData = item.path[item.path.length - 1];
              break;
          }
        })
        xAxisKey = 'lowCode_xAxisFiledKey'
        let res
        // 有响应数据就用响应数据
        if (response && dataPath) {
          res = _.get({ response }, dataPath)
        }
        // 没有就用静态数据
        else {
          res = this.staticData
        }

        this.yAxis = []
        const yAxis = dataVariables.find(item => item.key === 'yAxis')?.fields || []
        res.forEach(item => {
          const name = item[legendName]
          // 没有图例名称
          if(!name) {
            return
          }
          // 相同key映射
          const mapField = yAxis.find(opt => opt.key === name) || {}
          this.yAxis.push({
            ...mapField,
            key: name,
            value: mapField.value || name
          })
          // 构建长度为图例数据长度, key为图例名称, value为图例数据第二项 [{key: value}]的数组
          let i = 0;
          for (const key in item[legendData]) {
            const value = item[legendData][key]
              result[i] = result[i] || {}

            // 数组格式
            if(value instanceof Array) {
              result[i][xAxisKey] = value[0]
              result[i][name] = value[1]
            }
            // { key: value } 格式
            else {
              result[i][xAxisKey] = key
              result[i][name] = value
            }
            i++
          }
        })
      }
      // 取配置里的
      else {
        dataVariables.forEach(item => {
          switch(item.key) {
            case 'sourcePath': dataPath = item.path; break;
            case 'xAxis': xAxisKey = item.fields[0]?.key; break;
            case 'yAxis': this.yAxis = item.fields; break;
          }
        })
        // 有响应数据就用响应数据
        if(response && dataPath) {
          result = _.get({ response }, dataPath)
        }
        // 没有就用静态数据
        else {
          result = this.staticData
        }
      }

      // 数据兜底,起码显示个轮廓
      if(!result) {
        result = []
      }
      // 超过100条数据, 把点配置隐藏掉, 并数据降下来也不再开启,不然每次调用setOption时候都会导致内存上升
      if(result.length >= 100) {
        this.showSymbol = false
      }

      // 赋值给可暴露属性
      this.chartData = result


      // x坐标的左右集合, 用于计算缩放事件对应的两个数据
      this.xAxisData = []
      const source = result.map((item) => {
        const obj = {
          // 数据原本有的键
          ...item,
          [xAxisKey]: item[xAxisKey],
        }
        this.yAxis.forEach((opt) => {
          // 为了显示图例名称的键 : 此处图例名称需要是key,所以才这样偷梁换柱
          obj[opt.value || opt.key] = item[opt.key]
        })
        this.xAxisData.push(item[xAxisKey])
        return obj
      })
      this.dataset = {
        dimensions: [
          xAxisKey,
          ...this.yAxis.map((item) => item.value || item.key),
        ],
        source,
      }
      this.renderChart(dispose)
    },
    async [RESET](args) {
      this.getChartData(args)
    },
    async getChartData({ dispose = true, logId = '' } = {}) {
      const time = new Date()
      let executeResult
      // 由其他组件触发的
      if(logId) {
        executeResult = {
          children: [],
          pid: logId
        }
      }
      // 自身初始化触发的, 不是
      else {
        executeResult = {
          children: [],
          pid: '',
          tag: '数据集',
          id: trigger.getUnionKey(time),
          target: this,
          time
        }
      }

      const {
        apiRequest,
        sqlRequest,
        isEditMode
      } = this
      // api 请求
      if (apiRequest && !isEditMode) {
        const params = this.getApiRequestParams(this.apiRequest)
        if (!params.url) {
          return
        }
        const unionId = trigger.getUnionKey()
        const requestResult = {
          children: [],
          pid: unionId
        }
        trigger.recordLog({
          pid: logId || executeResult.id,
          children: [
            trigger.formatLinkMsg({
              id: unionId,
              type: '调用API',
              tagList: {
                method: params.method,
                url: params.url,
                params: params.params,
                data: params.data,
                headers: params.headers,
              }
            })
          ]
        })
        this.fetchLoading = true
        const [err, res] = await asyncFunc(request, {
          method: params.method,
          url: params.url,
          params: params.params,
          data: params.data,
          headers: params.headers,
        })
        this.fetchLoading = false

        requestResult.children.push(trigger.formatLinkMsg(res ? { successText: '成功回调', tagList: { response: res } } : { errorText: '失败回调', tagList: { response: err } } ))

        if(res) {
          this.formatRequestData(res.data, dispose)
          this._emitEvent(DATA_LOAD_SUCCESS)
          requestResult.children.push(trigger.formatLinkMsg({ successText: '数据加载完成', tagList: { chartData: this.chartData } }))
        }
        trigger.recordLog(requestResult)
      }
      // sql 请求
      else if (sqlRequest && !isEditMode) {
        const params = this.getSqlParams(sqlRequest)
        const unionId = trigger.getUnionKey()
        trigger.recordLog({
          pid: logId || executeResult.id,
          children: [
            trigger.formatLinkMsg({
              id: unionId,
              type: '调用SQL'
            })
          ]
        })
        const requestResult = {
          children: [],
          pid: unionId,
        }

        this.fetchLoading = true
        let res, err
        try {
          res = await this.$http({
            url: params.url,
            data: trigger.formatSQLParams(params.data, requestResult.pid),
          })
        } catch (e) {
          console.log(e)
          err = e
        }
        this.fetchLoading = false

        if(res) {
          this.formatRequestData(res, dispose)
          this._emitEvent(DATA_LOAD_SUCCESS)
        }

        // 日志记录开始
        // 记录sql执行结果
        trigger.recordSqlResponse(requestResult.pid)

        requestResult.children.push(trigger.formatLinkMsg(res ? { successText: '成功回调', tagList: { response: res } } : { errorText: '失败回调', tagList: { response: err } } ))

        if(res) {
          requestResult.children.push(trigger.formatLinkMsg({ successText: '数据加载完成', tagList: { chartData: this.chartData } }))
        }
        trigger.recordLog(requestResult)
      }
      // 使用静态数据
      else {
        this.formatRequestData(undefined, dispose)
        this._emitEvent(DATA_LOAD_SUCCESS)
        executeResult.children.push(trigger.formatLinkMsg({ successText: '数据加载完成', tagList: { chartData: this.chartData } }))
      }
      trigger.recordLog(executeResult)
    },
    [UPDATE_DATA](args) {
      this.getChartData({ ...args, dispose: false })
    },
    getFormatFn(setting) {
      const {
        formatter,
        formatOption: {
          type
        },
      } = setting
      if (type === 'text') {
        return formatter
      } else {
        // todo: js 沙箱机制
        const func = new Function('value,index,$format', formatter)
        return (value, index) => {
          // 如果是纯字符数字,转换为数字
          if (/^\d+$/.test(value)) {
            value = Number(value)
          }
          return func(value, index, this.$format)
        }
      }
    },
    getLengthValue(obj) {
      if (!obj) {
        return undefined
      }
      const { value, unit } = obj
      if (unit === '') {
        return Number(value)
      }
      return `${value}${unit}`
    },
  },
}
