import {
  getNodeByElementId,
  getParentNodeListByElementId,
  isIncludes,
} from '@/utils/tree-utils'
import { ignoreDragTypes } from '@/config/widgets'
import { pageInject } from '@/renderer/tools/pageStore'

export default {
  inject: ['getCurrentDialog', ...pageInject],
  data() {
    return {
    }
  },
  computed: {
    dialogContainer() {
      if(!this.isEditMode) {
        return false
      }
      return getNodeByElementId(this.getStore().layoutData, 'dialog-container')
    },
    isInCanvas() {
      if(!this.isEditMode) {
        return false
      }
      const currentDialog = this.getCurrentDialog()
      return (
        !currentDialog ||
        (currentDialog &&
          isIncludes(currentDialog.children, this.elementId))
      )
    },
    isAcceptDrag() {
      if(!this.isEditMode) {
        return false
      }
      return !ignoreDragTypes.includes(this.elementType)
    },
    isInGridDndCanvas() {
      if(!this.isEditMode) {
        return false
      }
      const parentNodeList = getParentNodeListByElementId(
        this.getStore().layoutData.children,
        this.elementId
      )
      return (
        parentNodeList &&
        parentNodeList
          .slice(0, -1)
          .some((p) => ['grid-dnd'].includes(p.elementType))
      )
    },
    isIgnoreDrag() {
      if(!this.isEditMode) {
        return false
      }
      return this.getElementPropsById(this.elementId).isIgnoreDrag
    },
    editModeClassName() {
      if(!this.isEditMode) {
        return false
      }
      return {
        'drop-zone': this.isEditMode && this.isInCanvas,
        'is-accept-drag':
          this.isEditMode &&
          this.isInCanvas &&
          this.isAcceptDrag &&
          !this.isInGridDndCanvas &&
          !this.isIgnoreDrag,
        'is-edit-mode': this.isEditMode,
        'is-ignore-drag': this.isInGridDndCanvas || this.isIgnoreDrag,
      }
    },
  },
  watch: {},
  methods: {
  },
  created() {
    if(!this.isEditMode) {
      return
    }
    this.$watch('children', () => {
        this.$nextTick(() => {
          this._updateShowIds()
        })
      }, {
      immediate: true,
    })
  }
}
