import { previewRoutePath, instancePathPrefix } from '@/config/code-var'
import { getSharePageParams } from '@/utils'

export const authKey = {
  Application: 'LOW_CODE_DEVELOP_PLATFORM_APP_MENU',
  AppDetail: 'LOW_CODE_DEVELOP_PLATFORM_APP_DETAIL_MENU',
  AppDesign: 'LOW_CODE_DEVELOP_PLATFORM_APP_DESIGN_MENU',
  myAppDesktop: 'LOW_CODE_DEVELOP_PLATFORM_APP_DESKTOP_MENU',
  appMarket: 'LOW_CODE_DEVELOP_PLATFORM_APP_MARKET_MENU',
  appMarketDetail: 'LOW_CODE_DEVELOP_PLATFORM_APP_MARKET_DETAIL_MENU'
}

const dynamicMenu = [
  // 应用列表
  {
    vue: () => import( /* webpackChunkName: "Application" */ '@/pages/application/index.vue'),
    authorityCode: authKey.Application,
  },
  // 应用详情
  {
    vue: () => import( /* webpackChunkName: "AppDetail" */ '@/pages/application/app-detail/index.vue'),
    authorityCode: authKey.AppDetail,
  },
  // 应用设计器
  {
    vue: () => import( /* webpackChunkName: "AppDesign" */ '@/pages/application/app-design/index.vue'),
    authorityCode: authKey.AppDesign,
  },
  // 我的应用
  {
    vue: () => import( /* webpackChunkName: "myAppDesktop" */ '@/pages/app-desktop/index.vue'),
    authorityCode: authKey.myAppDesktop,
  },
  {
    vue: () => import( /* webpackChunkName: "appMarket" */ '@/pages/app-desktop/app-market/index.vue'),
    authorityCode: authKey.appMarket,
  },
  {
    vue: () => import( /* webpackChunkName: "appMarketDetail" */ '@/pages/app-desktop/detail/index.vue'),
    authorityCode: authKey.appMarketDetail,
  }
]

const staticMenu = [
  // 预览页面
  {
    path: previewRoutePath,
    component: () => {
      if(getSharePageParams().code) {
        return import( /* webpackChunkName: "appInstance" */ '@/renderer/render/components/compContent/index.vue')
      }
      return import( /* webpackChunkName: "appInstance" */ '@/renderer/render/index.vue')
    },
    props: (route) => {
      return {
        isPageShare: !!route.query.code
      }
    }
  }
]


// 动态增加菜单
getAllMenus(rootApp.$store.state.menuJson).forEach(item => {
  // 路径匹配规则且没有子页面才是页面分享
  if (new RegExp(`^/?${instancePathPrefix}\\w+_.+_\\w+$`).test(item.menuPath) && !item.children?.length) {
    const path = item.calcParentPath + '/' + item.menuPath
    // 不存在同路径才加
    if (!staticMenu.find(menu => menu.path === path)) {
      staticMenu.push({
        path,
        name: item.menuCode,
        component: () => import( /* webpackChunkName: "appInstance" */ '@/renderer/render/components/compContent/index.vue'),
        props: { isPageShare: true }
      })

    }
  }
})

// 扁平化并获取菜单的全路径
function getAllMenus(menus) {
  const result = []
  getMenu(menus)
  function getMenu(menu, parentPath) {
    if (menu instanceof Array) {
      menu.forEach(item => getMenu(item))
    } else {
      const obj = {
        ...menu
      }
      // path
      obj.calcParentPath = parentPath ? parentPath : ''

      // 有子节点就遍历子节点并加上 path
      if (menu.children && menu.children.length) {
        const parentPath = obj.calcParentPath + (menu.menuPath[0] === '/' ? '' : '/') + menu.menuPath
        menu.children.forEach(item => getMenu(item, parentPath))
      }
      result.push(obj)
    }
  }
  return result
}
export {
  dynamicMenu,
  staticMenu
}