<template>
  <el-button
    class="button-widget"
    :class='commonClassName'
    :style='componentStyle'
    :type="type"
    :size="size"
    :plain="plain"
    :round="round"
    :circle="circle"
    :loading="loading"
    :disabled="disabled"
    @click="click"
    >
      <i v-if="icon && (!iconPosition || iconPosition === 'left')" :class="icon" style="margin-right: 3px;"></i>
      {{ value }}
      <i v-if="icon && iconPosition === 'right'" :class="icon" style="margin-left: 3px;"></i>
  </el-button>
</template>

<script>
  import { CLICK } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import _ from 'lodash'

  export default {
    mixins: [common],
    variables: {},
    computed: {
      value() {
        const value = this.useSlotProps ? _.get(this.slotProps, this.prop) : this.title
        return this.isEditMode && this.useSlotProps && !value ? `动态数据${this.prop}` : value
      }
    },
    mounted() {
    },
    methods: {
      click(e) {
        this._emitEvent(CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
    },
  }
</script>
