<template>
  <span class="icon" :class="{ ...commonClassName, disabled }"  :style="iconStyle" @click="click">
    <template v-if="tips && tips.trim()">
      <n-tooltip :value="tips">
        <i :class="iconClass"></i>
      </n-tooltip>
    </template>
    <i v-else :class="iconClass"></i>
  </span>
</template>

<script>
import common from '@/template/mixins/common'
import { CLICK } from '@/config/eventName'

export default {
  variables: {},
  mixins: [common],
  data() {
    return {}
  },
  computed: {
    iconClass() {
      const { icon } = this
      return {
        [icon || 'el-icon-question']: true,
      }
    },
    iconStyle() {
      const { color, fontSize, componentStyle } = this
      return {
        color,
        fontSize,
        ...componentStyle,
      }
    },
  },
  methods: {
    click(e) {
      if (!this.isEditMode && this.stopPropagation) {
        e.stopPropagation()
      }
      !this.disabled && this._emitEvent(CLICK)
    },
  },
  watch: {},
}
</script>

<style lang="less" scoped>
@import '~@/styles/index.less';

.icon {
  display: inline-block;
  line-height: 1;
  &.disabled {
    cursor: not-allowed;
  }
}
</style>
