<template>
  <div
    class="text-widget"
    :class='commonClassName'
    :style='componentStyle'
    @click="click"
    :title="useTitle ? value : ''"
    >{{ value }}
  </div>
</template>

<script>
  import { CLICK } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import _ from 'lodash'

  export default {
    mixins: [common],
    variables: {},
    computed: {
      value() {
        const value = this.useSlotProps ? _.get(this.slotProps, this.prop) : this.text
        return this.isEditMode && this.useSlotProps && !value ? `动态数据${this.prop}` : value
      }
    },
    methods: {
      click(e) {
        this._emitEvent(CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
    },
  }
</script>

<style lang="less" scoped>
  .text-widget {
    font-size: 12px;
    padding: 3px;
  }
</style>
