<template>
  <a-collapse
    v-if="isTabsVisible"
    class="collapse-widget"
    :class='commonClassName'
    :style='componentStyle'
    v-model="activeKey"
    :bordered="bordered"
    :accordion="accordion"
    :expandIconPosition="expandIconPosition"
    @change="handlePanelChange"
    @click.native="handleStopPropagation"
  >
    <a-collapse-panel
      class='pane-content'
      v-for="pane in dataList"
      :key="pane.key"
      :header="pane.header"
      :disabled="!isEditMode && pane.disabled"
      :forceRender="!pane.lazy"
      :showArrow="pane.showArrow"
    >
      <component
        class="panel-content"
        :is='getSlotByName(pane.key).slotElement'
        :ref="getSlotByName(pane.key).elementId"
        :data-zone="getDataZone(pane.key)"
        v-bind="{ ...getSlotByName(pane.key), slotProps: pane }"
        :id="getSlotByName(pane.key).elementId"
      ></component>
    </a-collapse-panel>
  </a-collapse>
</template>

<script>
  import { Collapse } from 'ant-design-vue'
  import common from '@/template/mixins/common'
  import _ from 'lodash'
  import { CHANGE, CHANGE_COLLAPSE, TOGGLE_COLLAPSE } from '@/config/eventName'

  export default {
    components: {
      ACollapse: Collapse,
      ACollapsePanel: Collapse.Panel
    },
    mixins: [common],
    variables: {},
    data() {
      return {
        isTabsVisible: true,
        tabsProvide: {
          visible: false,
        },
      }
    },
    provide() {
      return {
        tabsProvide: this.tabsProvide
      }
    },
    computed: {
      dataList() {
        const showedList = this.collapseData.filter(pane => {
          return this.isEditMode || (!this.isEditMode && pane.hidden === false)
        })
        this.showedList = showedList
        return this.showedList
      }
    },
    methods: {
      handlePanelChange(key) {
        this.activeKey = key
        this._emitEvent(CHANGE)
      },
      handleStopPropagation(e) {
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
      getDataZone(key) {
        const slot = this.getSlotByName(key)
        if (!slot) {
          return ''
        }
        return `${slot.elementId}_${slot.elementType}`
      },
      [CHANGE_COLLAPSE](params) {
        const { activeKey, dynamicActiveKey } = params
        if (!activeKey && !dynamicActiveKey) {
          return
        }
        if (dynamicActiveKey) {
          const realKey = this.getObjectVariables(dynamicActiveKey)
          this.activeKey = Array.isArray(realKey) ? realKey : []
        } else if (activeKey) {
          this.activeKey = activeKey
        }
        this._emitEvent(CHANGE)
      },
      [TOGGLE_COLLAPSE](params) {
        const { activeKey, dynamicActiveKey } = params
        if (!activeKey && !dynamicActiveKey) {
          return
        }
        if (dynamicActiveKey) {
          const realKey = this.getObjectVariables(dynamicActiveKey)
          if (Array.isArray(realKey)) {
            this.collapseData = this.collapseData.map(item => ({...item, hidden: !realKey.includes(item.key)}))
          }
        } else if (activeKey) {
          this.collapseData = this.collapseData.map(item => ({...item, hidden: !activeKey.includes(item.key)}))
        }
      },
      forceUpdateTabs:_.debounce(function() {
        this.isTabsVisible = false
        this.$nextTick(() => {
          this.activeKey = []
          this.isTabsVisible = true
        })
      }, 100),
    },
    mounted() {
      this.activeKey = this.isEditMode ? [] : this.dataList.filter(item => item.active).map(item => item.key)
      ;['collapseData'].forEach(watchKey => {
          this.$watch(watchKey, this.forceUpdateTabs)
      })
    },
  }
</script>

<style lang="less" scoped>
  .collapse-widget {
    &.is-edit-mode {
      .pane-content {
        padding: 1px;
      }
    }
  }
</style>
