<!--
    /**
     * 树形下拉选择组件，下拉框展示树形结构，提供选择某节点功能，方便其他模块调用
     * 调用示例：
     * <tree-select :height="400" // 下拉框中树形高度
     *              :width="200" // 下拉框中树形宽度
     *              size="small"  // 输入框的尺寸: medium/small/mini
     *              :data="data" // 树结构的数据
     *              :defaultProps="defaultProps" // 树结构的props
     *              multiple   // 多选
     *              clearable   // 可清空选择
     *              collapseTags   // 多选时将选中值按文字的形式展示
     *              checkStrictly // 多选时，严格遵循父子不互相关联
     *              :nodeKey="nodeKey"   // 绑定nodeKey，默认绑定'id'
     *              :checkedKeys="defaultCheckedKeys"  // 传递默认选中的节点key组成的数组
     *              @popoverHide="popoverHide"> // 事件有两个参数：第一个是所有选中的节点ID，第二个是所有选中的节点数据
     *              </tree-select>
     */
-->
<template>
  <div class="tree-select">
    <div
      class="mask"
      v-show="isShowSelect"
      @click="isShowSelect = !isShowSelect"
    ></div>
    <el-popover
      placement="bottom-start"
      :width="width"
      popper-class="tree-popper"
      trigger="manual"
      v-model="isShowSelect"
      @hide="popoverHide"
    >
      <el-input
        placeholder="搜索"
        prefix-icon="el-icon-search"
        v-model="keyword"
      >
      </el-input>
      <n-tree
        class="common-tree"
        :style="style"
        :ref="`tree${treeRef}`"
        :data="data"
        :props="defaultProps"
        :show-checkbox="multiple"
        :node-key="nodeKey"
        :check-strictly="checkStrictly"
        :expand-on-click-node="false"
        :check-on-click-node="multiple"
        @check-change="handleCheckChange"
        :filter-node-method="filterMethod"
        icon-class="icon-right"
        :lazy="lazy"
        :load="load"
        highlight-current
        @node-expand="handleExpandChange"
        @node-collapse="handleCollapseChange"
      >
        <div
          slot-scope="{ node, data }"
          class="tree-node"
          :class="computedClass(node, data)"
          @click="handleNodeClick(node, data)"
          :title="data.label"
        >
          {{ data.label }}
        </div>
      </n-tree>
      <el-select
        :style="selectStyle"
        slot="reference"
        :ref="`select${treeRef}`"
        :size="size"
        :disabled="disabled"
        v-model="selectedData"
        :multiple="multiple"
        :filterable="filterable"
        :clearable="clearable"
        :collapse-tags="collapseTags"
        @click.native="isShowSelect = !isShowSelect"
        @change="changeSelectedNodes"
        class="tree-select"
        :class="{ 'select-focus': isShowSelect }"
        :placeholder="placeholder"
      >
        <el-option
          v-for="item in options"
          :key="item.value"
          :label="item.label"
          :value="item.value"
        ></el-option>
      </el-select>
    </el-popover>
  </div>
</template>

<script>
import _ from 'lodash'
export default {
  name: 'tree-select',
  components: {},
  props: {
    treeRef: {
      type: String,
      default() {
        return 'tree'
      },
    },
    // 树结构数据
    data: {
      type: Array,
      default() {
        return []
      },
    },
    defaultProps: {
      type: Object,
      default() {
        return {}
      },
    },
    // 配置是否可多选
    multiple: {
      type: Boolean,
      default() {
        return false
      },
    },
    // 配置是否可清空选择
    clearable: {
      type: Boolean,
      default() {
        return false
      },
    },
    // 是否禁用
    disabled: {
      type: Boolean,
      default() {
        return false
      },
    },
    // 是否可搜索
    filterable: {
      type: Boolean,
      default() {
        return false
      },
    },
    // 配置多选时是否将选中值按文字的形式展示
    collapseTags: {
      type: Boolean,
      default() {
        return false
      },
    },
    nodeKey: {
      type: String,
      default() {
        return 'id'
      },
    },
    // 显示复选框情况下，是否严格遵循父子不互相关联
    checkStrictly: {
      type: Boolean,
      default() {
        return false
      },
    },
    // 默认选中的节点key数组
    checkedKeys: {
      type: Array,
      default() {
        return []
      },
    },
    size: {
      type: String,
      default() {
        return 'small'
      },
    },
    width: {
      type: Number,
      default() {
        return 250
      },
    },
    height: {
      type: Number,
      default() {
        return 300
      },
    },
    // 占位符
    placeholder: {
      type: String,
      default: '请选择',
    },
    // 是否懒加载
    lazy: {
      type: Boolean,
      default: false,
    },
    // 懒加载方法
    load: {
      type: Function,
      default: () => {},
    },
    showAllLevels: {
      type: Boolean
    },
    separator: {
      type: String
    }
  },
  data() {
    return {
      keyword: '',
      isShowSelect: false, // 是否显示树状选择器
      options: [],
      selectedData: [], // 选中的节点
      checkedIds: [],
      checkedData: [],
      isInited: false,
    }
  },
  methods: {
    handleExpandChange(data) {
      this.$emit('expandChange', data)
    },
    handleCollapseChange(data) {
      this.$emit('collapseChange', data)
    },
    filterMethod(value, data) {
      if (!value) return true
      return data.label.indexOf(value) !== -1
    },
    // 单选时点击tree节点，设置select选项
    setSelectOption(node) {
      if(!node || !node.data) {
        return
      }
      const { data } = node
      let tmpMap = {}
      // 显示全路径
      if (this.showAllLevels) {
        tmpMap.value = data.value
        tmpMap.label = data.parentsLabels.join(this.separator)
        this.selectedData = data.value
      } else {
        tmpMap.value = data.value
        tmpMap.label = data.label
        this.selectedData = data.value
      }
      this.options = []
      this.options.push(tmpMap)
    },
    // 单选，选中传进来的节点
    checkSelectedNode(checkedKeys) {
      this.$nextTick(() => {
        this.$refs[`tree${this.treeRef}`].setCurrentKey(checkedKeys)
        let node = this.$refs[`tree${this.treeRef}`].getNode(checkedKeys)
        this.setSelectOption(node)
      })
    },
    // 多选，勾选上传进来的节点
    checkSelectedNodes(checkedKeys) {
      this.$nextTick(() => {
        this.$refs[`tree${this.treeRef}`].setCheckedKeys(checkedKeys)
        this.handleCheckChange()
      })
    },
    // 单选，清空选中
    clearSelectedNode() {
      this.selectedData = ''
      this.$refs[`tree${this.treeRef}`].setCurrentKey(null)
    },
    // 多选，清空所有勾选
    clearSelectedNodes() {
      let checkedKeys = this.$refs[`tree${this.treeRef}`].getCheckedKeys() // 所有被选中的节点的 key 所组成的数组数据
      for (let i = 0; i < checkedKeys.length; i++) {
        this.$refs[`tree${this.treeRef}`].setChecked(checkedKeys[i], false)
      }
    },
    initCheckedData() {
      if (this.multiple) {
        // 多选
        if (this.checkedKeys.length > 0) {
          this.checkSelectedNodes(this.checkedKeys)
        } else {
          this.clearSelectedNodes()
        }
      } else {
        this.checkSelectedNode(this.checkedKeys)
      }
    },
    popoverHide() {
      if (this.multiple) {
        this.checkedIds = this.$refs[`tree${this.treeRef}`].getCheckedKeys() // 所有被选中的节点的 key 所组成的数组数据
        this.checkedData = this.$refs[`tree${this.treeRef}`].getCheckedNodes() // 所有被选中的节点所组成的数组数据
      } else {
        this.checkedIds = this.$refs[`tree${this.treeRef}`].getCurrentKey()
        this.checkedData = this.$refs[`tree${this.treeRef}`].getCurrentNode()
      }
      this.$emit('popoverHide', this.checkedIds, this.checkedData)
    },
    // 单选，节点被点击时的回调,返回被点击的节点数据
    handleNodeClick(node, data) {
      if (this.isDisabled(node)) {
        return
      }
      if (!this.multiple) {
        this.setSelectOption(node)
        this.isShowSelect = !this.isShowSelect
        this.updateValue(this.selectedData)
      }
      this.$emit('nodeClick', node, data)
    },
    // 多选，节点勾选状态发生变化时的回调
    handleCheckChange(data) {
      let checkedKeys = this.$refs[`tree${this.treeRef}`].getCheckedKeys() // 所有被选中的节点的 key 所组成的数组数据
      this.options = checkedKeys.map((item) => {
        let node = this.$refs[`tree${this.treeRef}`].getNode(item) // 所有被选中的节点对应的node
        if (this.showAllLevels) {
          const { value, parentsLabels } = node.data
          return {
            value: value,
            label: parentsLabels.join(this.separator),
          }
        } else {
          return node.data
        }
      })
      this.selectedData = this.options.map((item) => {
        return item.value
      })
      this.updateValue(this.selectedData)
      if (data) {
        const checkedNodes = this.$refs[`tree${this.treeRef}`].getCheckedNodes()
        this.$emit('checkChange', data, checkedNodes)
      }
    },
    treeToList(tree) {
      let queen = []
      let out = []
      queen = queen.concat(tree)
      while (queen.length) {
        let first = queen.shift()
        if (first.childNodes) {
          queen = queen.concat(first.childNodes)
        }
        out.push(first)
      }
      return out
    },
    // 选中的select选项改变的回调
    changeSelectedNodes(selectedData) {
      // 多选,清空select输入框时，清除树勾选
      if (this.multiple && selectedData.length <= 0) {
        this.clearSelectedNodes()
      }
      this.updateValue(this.selectedData)
    },
    updateValue(val) {
      const { checkedKeys } = this
      let flag = false
      if(checkedKeys instanceof Array && val instanceof Array) {
        flag = _.isEqual(checkedKeys, val)
      }
      else {
        flag = val === this.checkedKeys
      }
      // 相同值就不再更新, 避免重复更新
      if(!flag) {
        this.$emit('change', val)
      }
    },
    setCheckedKeys(keys) {
      this.$refs[`tree${this.treeRef}`].setCheckedKeys(keys)
    },
    // 当前节点是否禁用
    isDisabled(node) {
      // 父子不关联, 各玩各的
      if(this.checkStrictly) {
        return node.disabled
      }
      // 父子关联, 自己节点不符合,再递归判断父节点
      if(node.disabled) {
        return true
      }
      return node.parent ? this.isDisabled(node.parent) : node.disabled
    }
  },
  computed: {
    style() {
      return {
        width: this.width,
        height: this.height,
      }
    },
    selectStyle() {
      return { width: this.width }
    },
    computedClass() {
      return (node, data) => {
        return {
          'node-disabled': this.isDisabled(node) && !this.multiple,
          active: data && data.value === this.active,
        }
      }
    },
    active() {
      return this.multiple ? '' : this.selectedData
    }
  },
  watch: {
    keyword(v) {
      this.$refs[`tree${this.treeRef}`].filter(v)
    },
    isShowSelect(val) {
      // 隐藏select自带的下拉框
      this.$refs[`select${this.treeRef}`].blur()
      if (!this.isInited) {
        this.$nextTick(() => this.initCheckedData())
        this.isInited = true
      }
    },
    checkedKeys(val) {
      if (!val) return
      this.checkedKeys = val
      this.initCheckedData()
    },
  },
}
</script>
<style lang="less" scoped>
.tree-select::v-deep {
  .el-select .el-select__tags-text {
    max-width: none !important;
  }
  .select-focus .el-input .el-select__caret {
    transform: rotateZ(0deg);
  }
}
.mask {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
  z-index: 11;
}
.common-tree {
  overflow: auto;
}
.tree-select {
  z-index: 20;
}
</style>
<style lang="less">
.tree-popper {
  max-height: 500px;
  overflow-y: hidden;
  display: flex;
  flex-direction: column;
  .el-tree {
    flex: 1;
    overflow-y: auto;
    margin-top: 10px;
    .tree-node {
      width: 100%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      &.node-disabled {
        cursor: not-allowed;
        color: #aaaaaa;
      }
      &.active {
        color: #3f92fe;
      }
    }

    .el-tree-node__content .el-tree-node__expand-icon {
      margin-right: 3px;
    }
    & > .el-tree-node > .el-tree-node__content {
      background-color: inherit;
    }
    .el-tree-node__expand-icon.expanded {
      color: inherit;
    }
  }
}
</style>
