<template>
  <n-form-item
    class="rich-text-editor-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    @click.native="handleClickFormItem"
  >
    <div class="input-container" v-show="state != 'hidden'">
      <tinymce v-model="value" @input='handleChange' :height="inputHeight" :width='inputWidth' />
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
  import Tinymce from '@/components/Tinymce'
  import { CHANGE } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import form from '@/template/mixins/form'

  export default {
    components: {
      Tinymce
    },
    mixins: [common, form],

    variables: {},
    methods: {
      handleChange(value) {
        this._emitEvent(CHANGE)
      },
    },
    mounted() {
      if (!this.valueInSlot && this.value === undefined) {
        this.value = this.defaultValue
      }
    },
  }
</script>

<style lang="less" scoped>
  .rich-text-editor-widget::v-deep {
    &.is-edit-mode {
      .input-container {
        pointer-events: none;
      }
    }
    .input-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .bottom-tips {
      color: #aaa;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
    }
  }
</style>
