<template>
  <vxe-colgroup :field="group.field" :title="group.label" :align="group.align">
    <template v-for="col in group.children">
      <ColumnGroup
        v-if="!col.isLeaf"
        :key="col.filed"
        :group="col"
        :isEditMode="isEditMode"
        :getSlotByName="getSlotByName"
        :getDataZone="getDataZone"
        :filterSlotProps="filterSlotProps"
        :captureRowClick="captureRowClick"
        :getColByField="getColByField"
      ></ColumnGroup>
      <template v-else>
        <vxe-table-column
          v-if="getColByField(col.field).isUseSlot"
          :key="getColByField(col.field).key"
          :field="getColByField(col.field).key"
          :align="getColByField(col.field).align"
          :width="getColByField(col.field).width"
          :min-width="getColByField(col.field).minWidth"
          :sortable="getColByField(col.field).sortable"
          :title="col.label"
          :visible='!getColByField(col.field).isHidden'
          :fixed='isEditMode ? undefined : getColByField(col.field).fixed'
        >
          <template #default="{ row }">
            <component
              :showLayoutType="false"
              class="control-button-container"
              :is='getSlotByName(getColByField(col.field).key).slotElement'
              :ref="getSlotByName(getColByField(col.field).key).elementId"
              :data-zone="getDataZone(getColByField(col.field).key)"
              v-bind="{ ...getSlotByName(getColByField(col.field).key), slotProps: filterSlotProps(row) }"
              :id="getSlotByName(getColByField(col.field).key).elementId"
              @click.native.capture="captureRowClick(row)"
            ></component>
          </template>
        </vxe-table-column>
        <vxe-table-column
          v-else
          :key="getColByField(col.field).key"
          :field="getColByField(col.field).field"
          :type='getColByField(col.field).formatter && getColByField(col.field).formatter.renderType === "html" ? "html" : undefined'
          :align="getColByField(col.field).align"
          :width="getColByField(col.field).width"
          :min-width="getColByField(col.field).minWidth"
          :sortable="getColByField(col.field).sortable"
          :title="col.label"
          :visible='!getColByField(col.field).isHidden'
          :fixed='isEditMode ? undefined : getColByField(col.field).fixed'
        >
          <template v-if='isEditMode' #default>
            <span>{{`动态数据${getColByField(col.field).key}`}}</span>
          </template>
        </vxe-table-column>
      </template>
    </template>
  </vxe-colgroup>
</template>

<script>
export default {
  name: 'ColumnGroup',
  props: {
    group: {
      type: Array,
    },
    isEditMode: {
      type: Boolean,
    },
    getSlotByName: {
      type: Function,
    },
    getDataZone: {
      type: Function,
    },
    filterSlotProps: {
      type: Function,
    },
    captureRowClick: {
      type: Function,
    },
    getColByField: {
      type: Function,
    },
  }
}
</script>