<template>
  <a-progress
    v-if="isShowProgress"
    class="progress-widget"
    :class='commonClassName'
    :style='componentStyle'
    :percent="percent"
    :successPercent="successPercent"
    :type="type"
    :size="size"
    :showInfo="showInfo"
    :status="status"
    :strokeLinecap="strokeLinecap"
    v-bind="config[type]"
    :format="formatter && !isEditMode ? formatFunction : undefined"
  >
  </a-progress>
</template>

<script>
  import { Progress } from 'ant-design-vue'
  import common from '@/template/mixins/common'
  import _ from 'lodash'
  import { FUNCTION_FLAG } from '@/config'

  export default {
    components: {
      AProgress: Progress
    },
    mixins: [common],
    variables: {},
    data() {
      return {
        isShowProgress: true
      }
    },
    methods: {
      formatFunction(percent, successPercent) {
        let formatValue
        const functionId = this.formatter.id.split(FUNCTION_FLAG)[1]
        const functionsData =
          this.getStore().functionsData
        const currentFunction = functionsData.find(
          (item) => item.id === functionId
        )
        if (currentFunction) {
          const { paramsData, code } = currentFunction
          const data = paramsData.filter((d) => d.key)
          const params = Object.fromEntries(
            data.map((d) => {
              if (d.key === 'percent') {
                return [d.key, percent]
              }
              if (d.key === 'successPercent') {
                return [d.key, successPercent]
              }
              return [d.key, this.getObjectVariables(d.value)]
            })
          )
          const args = Object.keys(params)
          const values = Object.values(params)
          try {
            // todo: js 沙箱机制
            const func = new Function(...args, code)
            formatValue = func(...values)
          } catch (e) {
            console.log(e)
          }
          return formatValue
        }
      }
    },
    mounted() {
      if (this.isEditMode) {
        this.$watch('config', () => {
          this.isShowProgress = false
          this.$nextTick(()=> {
            this.isShowProgress = true
          })
        })
      }
    }
  }
</script>

<style lang="less" scoped>
  .progress-widget {
  }
</style>
