import _ from 'lodash'
import { getImageUrl, getSharePageParams, isValidateVariable, addCssByStyle, isEditMode, getTrueAppVariables, animateCSS, getAppVariables } from '@/utils'
import trigger from '@/renderer/tools/trigger'
import API from '@/api/application'
import {
  encryptByDES
} from '@/utils/encrypt'
import {
  EDIT,
  FUNCTION_FLAG,
  UTILS_FUNCTION
} from '@/config'

import {
  globalOff,
  globalOn
} from '@/renderer/tools/eventBus'
import {
  TRIGGER,
  PROPS,
  STYLES,
  EXECUTE_VALUE,
  EXECUTE_FN,
  RECEIVE_VAR,
  RECEIVE_PROPS,
  RECEIVE_STYLES
} from '@/config/eventKey'
import {
  getComponent,
  getConfigByKey,
  getEmitParamsRegx,
  getPropsValues
} from '@/renderer/tools/config'

import {
  DESTROY,
  MOUNT,
  CHANGE_STYLES,
  PLAY_ANIMATION,
} from '@/config/eventName'
import triggerTarget from '@/renderer/tools/trigger'
import {
  compile,
  parse
} from 'path-to-regexp'
import URI from 'urijs'
import qs from 'querystring'

import { getPathList } from '@/utils/format'
import editMode from './editMode'
import { utils } from '@/components/FunctionSelect/utils'
import { pageInject } from '@/renderer/tools/pageStore'
import styleToCss from 'style-object-to-css-string'
import { getParentNodeByElementId, treeUtil } from '@/utils/tree-utils'
export default {
  inject: [...pageInject],
  mixins: [editMode],
  props: {
    children: {
      type: Array,
      default: () => [],
    },
    elementId: {
      type: String,
      require: true
    },
    elementType: {
      type: String
    },
    elementVersion: {
      type: String
    },
    pageDesignId: {
      type: [String, Number]
    },
    slotProps: {
      type: Object
    }
  },
  data() {
    return {
      // 配置添加的数据响应字段及初始化
      ...getPropsValues.call(this, {
        elementType: this.elementType,
        elementVersion: this.elementVersion,
        elementId: this.elementId
      }),
      showIds: [],
      componentStyle: {}
    }
  },
  computed: {
    dynamicProps() {
      return this.getStore().dynamicProps
    },
    elementProps() {
      return this.getElementPropsById(this.elementId)
    },
    isEditMode() {
      return isEditMode()
    },
    commonClassName() {
      return {
        ...this.editModeClassName,
        ...this.formClassName,
        'low-code-component': true,
      }
    },
    visibleChildren() {
      return this.children.filter(item => this.showIds.includes(item.elementId))
    }
  },
  methods: {
    // 更新显示的字组件数组
    _updateShowIds() {
      this.showIds = this.children.filter(item => this.getElementPropsById(item.elementId).isVisible !== false).map(item => item.elementId)
    },
    // 初始化组件的属性
    _initProps() {
      const obj = this.elementProps
      Object.keys(obj || {}).forEach(key => {
        // 配置项中过滤掉props传进来的属性
        if(!Object.hasOwnProperty.call(this.$props, key) && !getConfigByKey.call(this, {elementType: this.elementType, elementVersion: this.elementVersion}).props[key]?.protected) {
          this[key] = _.cloneDeep(obj[key])
        }
      })
      this._updateShowIds()
    },
    // 初始化监听
    _bindEvents() {
      // 只有编辑模式才需要监听
      if (this.isEditMode) {
        // 样式修改
        // globalOn(STYLES, this.elementId, (data) => {
        //   Object.keys(data || {}).forEach(key => {
        //     this[key] = _.cloneDeep(data[key])
        //   })
        //   this._receiveData({ type: RECEIVE_STYLES, data })
        // })

        // 属性修改
        globalOn(PROPS, this.elementId, data => {
          Object.keys(data).forEach(key => {
            this[key] = data[key]
          })
          this._receiveData({ type: RECEIVE_PROPS, data })
        })
      } else {
        // 订阅其他组件对自己的操作
        globalOn(TRIGGER, this.elementId, ({
          type,
          data
        }) => {
          // 变量赋值
          if (type === EXECUTE_VALUE) {
            for (let key in data) {
              const value = data[key]
              const pathList = getPathList(key)
              const path = pathList.slice(1)
              // 不是第一层赋值
              if (path.length) {
                _.set(this, pathList, value)
              } else {
                this[key] = _.cloneDeep(value)
                // 兼容旧样式赋值
                if (key === 'styles') {
                  const obj = this[key] && typeof this[key] === 'object' ? this[key] : {}
                  const id = `dynamic-style_${this.elementId}`
                  const styleText = `#${this.elementId}{${styleToCss(obj) || ''}}`
                  addCssByStyle(id, styleText)
                }
                if (key === 'stylesText') {
                  const str = this[key] && typeof this[key] === 'string' ? this[key] : ''
                  const id = `dynamic-style_${this.elementId}`
                  const styleText = `#${this.elementId}{${str}}`
                  addCssByStyle(id, styleText)
                }
              }
              this._onVariableChange({ key, value, path, pathList })
            }
            this._receiveData({ type: RECEIVE_VAR, data })
          } else if (type === EXECUTE_FN) {
            const {
              eventName,
              params
            } = data
            typeof this[eventName] === 'function' && this[eventName](params)
          }
        })
      }
    },
    _unbindEvents() {
      if (this.isEditMode) {
        // globalOff(STYLES, this.elementId)
        globalOff(PROPS, this.elementId)
      } else {
        globalOff(TRIGGER, this.elementId)
      }
    },
    // 触发事件, 往队列插入一条记录
    async _emitEvent(eventName, ...args) {
      if (!this.isEditMode) {
        // 触发事件的时机在: DOM更新循环之后
        await this.$nextTick()
        triggerTarget.triggerComponents({ eventName, elementId: this.elementId, args, vueThis: this })
      }
    },
    // 受到来自其他组件的变量赋值时
    _onVariableChange({ key, value, path, pathList }) {
      const {
        variables
      } = this.$options

      // 有配置了赋值回调时
      if (variables instanceof Object && Object.keys(variables).length) {
        let realKey
        if(path.length) {
          realKey = pathList[0]
        } else {
          realKey = key
        }
        const vKey = `$${realKey}`
        typeof variables[vKey] === 'function' && variables[vKey].call(this, value, path)
      }
    },
    // 当前组件接收到消息的回调, 可在组件里覆盖
    _receiveData({ type, data }) {
      // console.log(type, data)
    },
    getApiRequestParams(req, emitParams) {
      const uri = new URI(req.url)
      const pathString = uri.pathname()
      let dynamicPathArray = []
      try {
        dynamicPathArray = parse(pathString).filter(item => _.isObject(item)).map(item => item.name)
      } catch (e) {
        console.log(e)
        dynamicPathArray = []
      }
      const bodyData = req.bodyData.filter((d) => d.key)
      const paramsData = req.paramsData.filter((d) => d.key)
      const headersData = req.headersData.filter((d) => d.key)
      const method = req.method
      const dynamicPath = this.getObjectVariables(
        Object.fromEntries(
          dynamicPathArray.map(key => {
            const current = req.paramsData.find((d) => d.key === key)
            return [key, `${current?.value}`]
          })
        ),
        emitParams
      )
      const realPathString = compile(pathString)(dynamicPath)
      let url = uri.origin() + realPathString
      // 始终转换为当前域的网关
      const protocol = window.location.protocol
      const host = window.location.host
      if (req.type === 4 && url) {
        const uri = new URI(url)
        const pre = `${uri.origin()}/gateway`
        if (uri.origin()) {
          const current = `${protocol}//${host}/gateway`
          url = url.replace(pre, current)
        }
      }
      if (req.type === 5 && url) {
        const uri = new URI(url)
        const pre = `${uri.origin()}/app-data`
        const current = '/app-data'
        if (uri.origin()) {
          url = url.replace(pre, current)
        }
        const {appId: instanceId} = getSharePageParams()
        const search = window.location.search
        const { appId: queryAppId, id } = qs.parse(search.replace('?', ''))
        const currentAppId = queryAppId || id || instanceId
        const appPath = url.replace(`${current}/`, '').split('/')[0]
        const newAppPath = `app${currentAppId}`
        url = url.replace(appPath, newAppPath)
      }
      if (['script', 'orchestration', 'sncflower', 6].includes(req.type) && url) {
        const uri = new URI(url)
        const pre = uri.origin()
        if (!pre) {
          const current = `${protocol}//${host}`
          url = `${current}${url}`
        }
      }
      const data = this.getObjectVariables(
        Object.fromEntries(bodyData.map((d) => [
          [d.key], d.value
        ])),
        emitParams
      )
      const headers = this.getObjectVariables(
        Object.fromEntries(headersData.map((d) => [
          [d.key], d.value
        ])),
        emitParams
      )
      if (['script', 'orchestration', 'sncflower', 5].includes(req.type)) {
        const loginKey = window.localStorage.getItem('loginKey')
        const sncToken = JSON.parse(loginKey) ?. ['snc-token']
        headers['snc-token'] = sncToken
      }
      if ([6].includes(req.type) && req.useDynamicOrigin) {
        try {
          const pageId = this.getStore().layoutData.elementId
          const uri = new URI(url)
          const appVariables = getAppVariables() || {}
          const appVariablesKey = req.dynamicOrigin.replace(`${pageId}.$appVariables.`, '')
          let dynamicUrl = `${_.get(appVariables, appVariablesKey)}`
          if (dynamicUrl.endsWith('/')) {
            dynamicUrl = dynamicUrl.slice(0, -1)
          }
          const dynamicUri = new URI(dynamicUrl)
          const dynamicOrigin = dynamicUri.origin()
          const dynamicDir = dynamicUri.pathname()
          const preDir = uri.pathname()
          url = `${dynamicOrigin}${dynamicDir}${preDir}`
        } catch (e) {
          console.warn('path路径解析失败，正确格式应为/aaa/bbb')
        }
      }
      const params = this.getObjectVariables(
        Object.fromEntries(paramsData
          .filter(d => !dynamicPathArray.includes(d.key))
          .map((d) => [
            [d.key], d.value
          ])),
        emitParams
      )
      if (headers['Content-Type'] === 'multipart/form-data') {
        const formData = new FormData()
        Object.keys(data).forEach(key => {
          formData.append(key, data[key])
        })
        delete headers['Content-Type']
        return {
          url,
          method,
          data: formData,
          params,
          headers
        }
      }
      return {
        url,
        method,
        data,
        params,
        headers,
      }
    },
    getElement(elementType, elementVersion) {
      return getComponent(elementType, elementVersion)
    },
    getElementPropsById(elementId) {
      const realId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
      return this.dynamicProps[realId] || {}
    },
    getSlotByName(slotName) {
      const slot = this.children.find((child) => this.getElementPropsById(child.elementId).slotName === slotName)
      if (!slot) {
        return {}
      }
      const slotElement = this.getElement(slot.elementType, slot.elementVersion)
      const layoutProps = this.children.find(item => item.elementId === slot.elementId)
      return { ...layoutProps, slotElement }
    },
    // 获取sql请求参数
    getSqlParams(req, emitParams) {
      const {
        sql,
        dataSourceId,
        sqlParamsData,
        sqlType,
        select
      } = req
      const bodyData = [{
        key: 'params',
        value: this.getObjectVariables(
          Object.fromEntries(
            sqlParamsData
            .filter((d) => d.key)
            .map((d) => [
              [d.key], d.value
            ])
          ),
          emitParams
        ),
      }, ]
      const url = sqlType === 1 ? API.pageDataExecuteDynamicSQL : API.pageDataExecuteTemplateSQL
      const data = this.getObjectVariables(
        Object.fromEntries(bodyData.map((d) => [
          [d.key], d.value
        ])),
        emitParams
      )
      return {
        url,
        data: {
          ...data,
          sql: encryptByDES(sql),
          dataSourceId,
          select
        },
      }
    },
    // 获取请求需要的Object数据
    getObjectVariables(data, emitParams) {
      const isFunc = typeof data === 'string' && data.indexOf(FUNCTION_FLAG) === 0
      const isUtilsFunc = typeof data === 'string' && data.indexOf(UTILS_FUNCTION) === 0
      const isVariableString = isValidateVariable(data)
      let isVariableObject = false
      if (data && typeof data === 'object') {
        const objString = JSON.stringify(data) || ''
        isVariableObject = (objString.includes('$') || objString.includes(FUNCTION_FLAG))
      }
      switch (true) {
        case isFunc: {
          const functionId = data.split(FUNCTION_FLAG)[1]
          const functionsData = this.getStore().functionsData
          const currentFunction = functionsData.find(item => item.id === functionId)
          if (currentFunction) {
            const {
              paramsData,
              code
            } = currentFunction
            const data = paramsData.filter((d) => d.key)
            const params = Object.fromEntries(
              data.map((d) => {
                return [d.key, this.getObjectVariables(d.value, emitParams)]
              })
            )
            const args = Object.keys(params)
            const values = Object.values(params)
            try {
              // todo: js 沙箱机制
              const func = new Function(...args, code)
              return func(...values)
            } catch (e) {
              return undefined
            }
          }
          return undefined
        }
        case isVariableString: {
          // 储存在vuex中store的应用变量, 是可写数据,会变, 可读不可写的应用变量,例如$localStorage,是存在页面变量中的,不在此读
          if(getTrueAppVariables(data)) {
            return getTrueAppVariables(data)
          }
          let elementId = data.split('.')[0]
          elementId = elementId === 'app' ? this.getStore().layoutData.elementId : elementId
          const regx = getEmitParamsRegx(elementId)
          // 格式匹配并且参数是正整数
          if(regx.test(data)) {
            const body = data.replace(regx, '$1')
            // 没有任何后缀, 返回所有参数集合
            if(body === data) {
              return emitParams
            }
            return _.get(emitParams, data.replace(body + '.', ''))
          }
          const path = data.substr(
            elementId.indexOf(elementId) + elementId.length + 1
          )
          const variable = triggerTarget.getRef(elementId)
          return _.get(variable, path.replace('$', ''))
        }
        case isVariableObject: {
          const isArray = Array.isArray(data)
          if (isArray) {
            return data.map((d) => this.getObjectVariables(d, emitParams))
          } else {
            return Object.fromEntries(
              Object.entries(data).map(([key, value]) => [
                key,
                this.getObjectVariables(value, emitParams),
              ])
            )
          }
        }
        case isUtilsFunc: {
          const currentUtil = utils.find(item => item.id === data)
          return currentUtil?.code || data
        }
        default:
          return data
      }
    },
    recordBug(err, vm) {
      trigger.recordBug({
        target: vm.elementId ? vm : this,
        message: err,
        error: err
      })
    },
    // 编辑模式每个组件维护自身的样式，方便替换
    updateStaticStyle() {
      const pageId = this.getStore().layoutData.elementId
      const styleText = this.styleConfigList.map(item => {
        const { cssText, ...styleObject  } = JSON.parse(JSON.stringify(item.styles))
        if (!cssText && Object.keys(styleObject).length === 0) {
          return ''
        }
        if (styleObject.backgroundImage) {
          const isPic = new RegExp(/\w(\.gif|\.jpeg|\.png|\.jpg|\.bmp)/i).test(styleObject.backgroundImage || '')
          styleObject.backgroundImage = isPic ? `url(${getImageUrl(styleObject.backgroundImage)})` : styleObject.backgroundImage
        }
        const styleString = `${styleToCss(styleObject) || ''}${cssText || ''}`
        return `#${this.elementId === pageId ? `page_${pageId}` : this.elementId}${item.query}{${styleString}}`
      }).join('')
      const id = `static-style_${this.elementId}`
      addCssByStyle(id, styleText)
    },
    // 预览模式动态生成的组件需要维护自己的样式
    updateChildrenStaticStyle() {
      const pageId = this.getStore().layoutData.elementId
      let styleTextList = []
      treeUtil.forEach(this.children, node => {
        const { styleConfigList = [], styles = {}, stylesText = '' } = this.getElementPropsById(node.elementId)
        const styleText = styleConfigList.map((item, index) => {
          const { cssText, ...styleObject  } = JSON.parse(JSON.stringify(item.styles))
          if (!cssText && Object.keys(styleObject).length === 0) {
            return ''
          }
          if (styleObject.backgroundImage) {
            const isPic = new RegExp(/\w(\.gif|\.jpeg|\.png|\.jpg|\.bmp)/i).test(styleObject.backgroundImage || '')
            styleObject.backgroundImage = isPic ? `url(${getImageUrl(styleObject.backgroundImage)})` : styleObject.backgroundImage
          }
          const styleString = `${styleToCss(styleObject) || ''}${cssText || ''}${index === 0 ? styleToCss(styles) || '' : ''}${index === 0 ? stylesText || '' : ''}`
          return `#${node.elementId === pageId ? `page_${pageId}` : node.elementId}${item.query}{${styleString}}`
        }).join('')
        styleTextList.push(styleText)
      })
      const id = `static-style_${this.elementId}`
      addCssByStyle(id, styleTextList.join(''))
    },
    // 动态更改样式
    [CHANGE_STYLES](params) {
      const pageId = this.getStore().layoutData.elementId
      const { dynamicStyleConfigList } = params
      const realConfigList = dynamicStyleConfigList.filter(item => item.name && item.styles).map(item => ({...item, styles: this.getObjectVariables(item.styles)}))
      const styleText = realConfigList.map(item => {
        const styles = item.styles && typeof item.styles === 'object' ? item.styles : {}
        const { cssText, ...styleObject  } = JSON.parse(JSON.stringify(styles))
        if (!cssText && Object.keys(styleObject).length === 0) {
          return ''
        }
        if (styleObject.backgroundImage) {
          const isPic = new RegExp(/\w(\.gif|\.jpeg|\.png|\.jpg|\.bmp)/i).test(styleObject.backgroundImage || '')
          styleObject.backgroundImage = isPic ? `url(${getImageUrl(styleObject.backgroundImage)})` : styleObject.backgroundImage
        }
        const styleString = `${styleToCss(styleObject) || ''}${cssText || ''}`
        return `#${this.elementId === pageId ? `page_${pageId}` : this.elementId}${item.query}{${styleString}}`
      }).join('')
      const id = `dynamic-style_${this.elementId}`
      addCssByStyle(id, styleText)
    },
    // 绑定动画效果
    bindAnimation() {
      const pageId = this.getStore().layoutData.elementId
      const styleConfigList = this.styleConfigList || []
      styleConfigList.forEach(item => {
        const { animation } = item
        animation.forEach(ani => {
          if (ani.animationName && ani.trigger) {
            const targetElementList = document.querySelectorAll(`#${this.elementId === pageId ? `page_${pageId}` : this.elementId}${item.query}`)
            targetElementList.forEach(tar => {
              switch(ani.trigger) {
                case 'click': {
                  tar.addEventListener('click', function() {
                    animateCSS({ element: tar, ...ani })
                  }, false)
                  break
                }
                case 'hover': {
                  tar.addEventListener('mouseover', function() {
                    animateCSS({ element: tar, ...ani })
                  }, false)
                  break
                }
                case 'show': {
                  animateCSS({ element: tar, ...ani })
                  break
                }
              }
            })
          }
        })
      })
    },
    // 隐藏动画单独触发
    playHideAnimation() {
      const pageId = this.getStore().layoutData.elementId
      const parentNode = getParentNodeByElementId(this.getStore().layoutData, this.elementId)
      const parentElement = parentNode ? document.getElementById(parentNode.elementId === pageId ? `page_${pageId}` : parentNode.elementId) : null
      if (!parentElement) {
        return
      }
      const rootStyleConfig = this.styleConfigList[0]
      const { animation } = rootStyleConfig
      const hideAnimate = animation.find(item => item.trigger === 'hide')
      if (hideAnimate && hideAnimate.animationName) {
        const index = parentNode.children.findIndex(node => node.elementId === this.elementId)
        const nextNode = parentNode.children[index + 1]
        const nextElement = nextNode ? document.getElementById(nextNode.elementId) : null
        const copyEl = this.$el.cloneNode(true)
        if (nextElement) {
          parentElement.insertBefore(copyEl, nextElement)
        } else {
          parentElement.appendChild(copyEl)
        }
        animateCSS({ element: copyEl, ...hideAnimate }).then(() => {
          copyEl.remove()
        })
      }
    },
    // 执行动画
    async [PLAY_ANIMATION](params) {
      const pageId = this.getStore().layoutData.elementId
      const { dynamicAnimationConfigList } = params
      const animateList = []
      dynamicAnimationConfigList.forEach(item => {
        const { animate, query } = item
        if (animate.animationName) {
          const targetElementList = document.querySelectorAll(`#${this.elementId === pageId ? `page_${pageId}` : this.elementId}${query}`)
          targetElementList.forEach(tar => {
            animateList.push(animateCSS({ element: tar, ...animate }))
          })
        }
      })
      await Promise.all(animateList)
    }
  },
  created() {
    this._initProps()
    triggerTarget.initElementInstance(this)
    this._bindEvents()
  },
  mounted() {
    if (this.isEditMode) {
      this.updateStaticStyle()
    } else {
      this.bindAnimation()
    }
    this._emitEvent(MOUNT)
  },
  beforeDestroy() {
    if (!this.isEditMode) {
      this.playHideAnimation()
    }
    this._emitEvent(DESTROY)
    this._unbindEvents()
    triggerTarget.deleteElementInstance(this.elementId)
  },
  // 后代组件错误捕获
  errorCaptured(err, vm) {
    this.recordBug(err, vm)
    return false
  },
}