import _ from 'lodash'

import storeKeys from '@/config/storeKey'
import { EDIT, VIEW } from '@/config'
import { appSourcePrefix } from '@/config/code-var'

const {
  UPDATE_PAGE_DATA,
  UPDATE_APP_VARIABLES,
  UPDATE_HISTORY_INDEX,
  UPDATE_APP_SOURCE_PREFIX
} = storeKeys

export default {
  namespaced: true,
  state: {
    env: EDIT,
    // 应用页面
    pageData: [],
    appVariables: {},
    currentHistoryIndex: 0,
    appSourcePrefix
  },
  mutations: {
    // 更新应用数据
    [UPDATE_PAGE_DATA](state, { pageData }) {
      state.pageData = _.cloneDeep(pageData)
    },
    // 更新应用变量
    [UPDATE_APP_VARIABLES](state, { appVariables }) {
      state.appVariables = _.cloneDeep(appVariables)
    },
    [UPDATE_HISTORY_INDEX](state, value) {
      state.currentHistoryIndex = value
    },
    // 切换静态资源前缀
    [UPDATE_APP_SOURCE_PREFIX](state, { pathPrefix }) {
      state.appSourcePrefix = `/app-design${pathPrefix}`
    }
  },
  actions: {},
}
