<template>
  <a
    class="href-widget"
    :class='commonClassName'
    :style='componentStyle'
    :href='href'
    :target='target'
    @click='click'
    >{{ value }}
  </a>
</template>

<script>
  import common from '@/template/mixins/common'
  import { CLICK } from '@/config/eventName'
  import _ from 'lodash'

  export default {
    mixins: [common],
    variables: {},
    computed: {
      value() {
        const value = this.useSlotProps ? _.get(this.slotProps, this.prop) : this.text
        return this.isEditMode && this.useSlotProps && !value ? `动态数据${this.prop}` : value
      }
    },
    methods: {
      click(e) {
        const eventsData = this.getStore().eventsData
        const events = eventsData[this.elementId]
        const hasClickEvent = events?.CLICK?.filter(item => !item.disabled).length
        if (this.isEditMode || hasClickEvent) {
          e.preventDefault()
        }
        this._emitEvent(CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .href-widget {
    font-size: 12px;
    padding: 3px;
  }
</style>
