<template>
  <div
    class="steps-widget"
    :class='{...commonClassName, vertical: direction === "vertical" && type !== "navigation", stretch}'
    :style='componentStyle'
  >
    <a-steps
      v-if="isTabsVisible"
      :current="current"
      :type="type"
      :size="size"
      :status="status"
      :direction="type !== 'navigation' ? direction : undefined"
      :labelPlacement="type !== 'navigation' ? labelPlacement : undefined"
      :progressDot="type !== 'navigation' && progressDot"
      v-on:[eventName]="handleChange"
    >
      <a-step
       :style="{width: direction === 'vertical' && type !== 'navigation' ? `${stepWidth}px` : undefined}"
        v-for="step in stepsData"
        :key="step.key"
        :title="step.title"
        :subTitle="step.subTitle"
        :status="step.status"
        :disabled="!isEditMode && step.disabled"
        :description="step.description">
        <i v-if="step.icon && !progressDot && !['finish', 'error'].includes(step.status)" slot="icon" class="step-icon" :class="step.icon" />
      </a-step>
    </a-steps>
    <template v-if="contentVisible">
      <template v-if="!lazy">
        <div class='step-content' :class="{active: index === current}" v-for="(step, index) in stepsData" :key="step.key">
          <component
            class="panel-content"
            :is='getSlotByName(step.key).slotElement'
            :ref="getSlotByName(step.key).elementId"
            :data-zone="getDataZone(step.key)"
            v-bind="{ ...getSlotByName(step.key), slotProps: step }"
            :id="getSlotByName(step.key).elementId"
          ></component>
        </div>
      </template>
      <div v-else class='step-content active'>
        <component
          class="panel-content"
          :key="getSlotByName(stepsData[current].key).elementId"
          :is='getSlotByName(stepsData[current].key).slotElement'
          :ref="getSlotByName(stepsData[current].key).elementId"
          :data-zone="getDataZone(stepsData[current].key)"
          v-bind="{ ...getSlotByName(stepsData[current].key), slotProps: stepsData[current] }"
          :id="getSlotByName(stepsData[current].key).elementId"
        ></component>
      </div>
    </template>
  </div>
</template>

<script>
  import { Steps } from 'ant-design-vue'
  import common from '@/template/mixins/common'
  import _ from 'lodash'
  import { CHANGE, CHANGE_STEP, PRE_STEP, NEXT_STEP } from '@/config/eventName'

  export default {
    components: {
      ASteps: Steps,
      AStep: Steps.Step,
    },
    mixins: [common],
    variables: {
      $current(n, o) {
        if (n !== o) {
          this.current = n
          this._emitEvent(CHANGE)
        }
      }
    },
    data() {
      return {
        isTabsVisible: true,
        tabsProvide: {
          visible: false,
        },
      }
    },
    provide() {
      return {
        tabsProvide: this.tabsProvide
      }
    },
    computed: {
      eventName() {
        if (this.isEditMode || this.clickable) {
          return 'change'
        }
        return 'none'
      }
    },
    watch: {
      current(n, o) {
        this.status = undefined
      }
    },
    methods: {
      handleChange(current) {
        this.current = current
        this._emitEvent(CHANGE)
      },
      handleStopPropagation(e) {
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
      getDataZone(key) {
        const slot = this.getSlotByName(key)
        if (!slot) {
          return ''
        }
        return `${slot.elementId}_${slot.elementType}`
      },
      [PRE_STEP]() {
        if (this.current <= 0) {
          return
        }
        this.current--
        this._emitEvent(CHANGE)
      },
      [NEXT_STEP]() {
        if (this.current >= this.stepsData.length - 1) {
          return
        }
        this.current++
        this._emitEvent(CHANGE)
      },
      [CHANGE_STEP](params) {
        const { activeKey, dynamicActiveKey } = params
        if (activeKey === undefined && dynamicActiveKey === undefined) {
          return
        }
        if (dynamicActiveKey !== undefined) {
          const realKey = this.getObjectVariables(dynamicActiveKey)
          this.current = Number(realKey)
        } else {
          this.current = activeKey
        }
        this._emitEvent(CHANGE)
      },
      forceUpdateTabs:_.debounce(function() {
        this.isTabsVisible = false
        this.$nextTick(() => {
          this.isTabsVisible = true
        })
      }, 100),
    },
    mounted() {
      this.current = this.isEditMode ? 0 : this.stepsData.findIndex(item => item.active)
      ;['stepsData'].forEach(watchKey => {
          this.$watch(watchKey, this.forceUpdateTabs)
      })
    },
  }
</script>

<style lang="less" scoped>
  .steps-widget::v-deep {
    &.is-edit-mode {
      .step-content {
        padding: 1px;
      }
    }

    &.vertical {
      display: flex;
      .ant-steps.ant-steps-vertical {
        width: unset;
      }
      &.stretch {
        .ant-steps.ant-steps-vertical {
          display: flex;
          flex-direction: column;
        }
      }

      .step-content {
        flex: 1;
      }
    }

    .step-content {
      display: none;
      &.active {
        display: block;
      }
    }

    .step-icon {
      font-size: unset;
    }
  }
</style>
