<template>
  <div class="business-widget" :class="commonClassName" :style="componentStyle">
    <template>
      <el-tag v-if="!source || !moduleName" type="danger">{{
        !source ? emptyTips[type] : '请选择模块名称'
      }}</el-tag>
      <el-tag v-else-if="isEditMode && isMini" type="success">{{
        componentName
      }}</el-tag>
      <template v-else>
        <div v-if="componentLoading">
          组件加载中 <i class="el-icon-loading"></i>
        </div>
        <el-tag v-else-if="!componentLoading && componentError" type="danger"
          >{{ componentName }}异常</el-tag
        >
        <component
          v-if="businessInstance"
          ref="businessInstance"
          :is="businessInstance"
          v-on="events"
          v-bind="props"
        ></component>
      </template>
    </template>
  </div>
</template>

<script>
import common from '@/template/mixins/common'
import _ from 'lodash'
import { RECEIVE_PROPS } from '@/config/eventKey'
import Vue from 'vue'
import trigger from '@/renderer/tools/trigger'
export default {
  mixins: [common],
  variables: {},
  data() {
    return {
      businessInstance: '',
      componentLoading: false,
      componentError: false,
      emptyTips: {
        inner: '请选择微前端',
        outer: '请输入组件url',
      }
    }
  },
  computed: {
    source() {
      return {
        inner: this.app,
        outer: this.appUrl
      }[this.type]
    },
    componentName() {
      const { source, moduleName } = this
      return `业务组件: ${source} - ${moduleName}`
    },
    events() {
      // 编辑模式,不接收任何事件
      if (this.isEditMode) {
        return {}
      }
      const events = this.eventsData.reduce((obj, item) => {
        obj[item.emitKey] = (...args) => {
          this._emitEvent(item.emitKey, ...args)
        }
        return obj
      }, {})
      return events
    },
  },
  mounted() {},
  methods: {
    renderComponent() {
      const { source, moduleName, componentName, isEditMode, isMini } =
        this
      if (isEditMode && isMini) {
        return
      }
      this.businessInstance = ''
      // 配置不完整报错
      if (!source || !moduleName) {
        this.componentError = true
        trigger.recordBug({
          message: '业务组件配置不完整',
          target: this
        })
      } else {
        this.componentLoading = true
        window.System.import(source)
          .then((res) => {
            const instance = res[moduleName]
            // 配置不正确报错
            if (!instance) {
              this.componentError = true
              trigger.recordBug({
                message: `业务组件 “${componentName}” 组件模块不存在`,
                target: this
              })

            } else {
              // 合法的组件
              if(instance instanceof Function || Object.prototype.toString.call(instance) === '[object Object]') {
                this.businessInstance = instance
              }
              // 不合规的, 转为简单组件
              else {
                this.businessInstance = Vue.extend({
                  render(h) {
                    return h('div', instance)
                  }
                })
              }
            }
            // 让组件渲染一段时间再停止loading
            this.$nextTick(() => {
              this.componentLoading = false
              this.componentError = false
            })
          })
          .catch((err) => {
            this.componentError = true
            this.componentLoading = false
            // 资源加载错误报错
            trigger.recordBug({
              message: `业务组件: “${componentName}” 资源加载异常`,
              error: err,
              target: this
            })
          })
      }
    },
    _receiveData({ data, type }) {
      /* 在保存配置后JSON.stringify,把值为 undefined的key给过滤掉,所以不能配置的 props 整个
        覆盖组件里的props, 只能每个键以覆盖形式追加上去
      */
      for(const key in data.props) {
        this.props[key] = _.cloneDeep(data.props[key])
      }
      // 配置更新,刷新获取组件资源
      if ([RECEIVE_PROPS].includes(type)) {
        this.renderComponent()
      }
    },
    // 覆盖common.js的
    recordBug(err) {
      trigger.recordBug({
        message: `${this.componentName}发生错误: ${err}`,
        error: err,
        target: this,
        panel: 'prop'
      })
    }
  },
  created() {
    this.renderComponent()
  },
}
</script>

<style lang="less" scoped>
@import '~@/styles/index.less';

.business-widget {
}
</style>
