<template>
  <n-form-item
    ref="ElFormItem"
    class="upload-widget"
    :class="commonClassName"
    :style="componentStyle"
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    :rules="rules"
    @click.native="handleClickFormItem"
  >
    <div class="upload">
      <el-button
        :size="size"
        type="primary"
        :disabled="state === 'disabled'"
        @click="chooseFile"
        >{{ btnText }}</el-button
      >
      <input
        class="file"
        ref="fileRef"
        type="file"
        :accept="`${uploadType.all ? '*' : typeSetting[uploadType.type]}`"
        @change="fileChange"
      />
      <ul class="el-upload-list el-upload-list--picture">
        <li
          v-for="(item, index) in filesList"
          :key="index"
          :tabindex="index"
          class="el-upload-list__item is-success"
          :class="{ 'no-image': !showIamge }"
        >
          <img
            v-if="showIamge"
            :src="thumbnail[index]"
            alt=""
            class="el-upload-list__item-thumbnail"
          /><a
            class="el-upload-list__item-name"
            :title="auto ? item : item.name"
            href="javascript:void(0);"
            ><i class="el-icon-document"></i>{{ auto ? item : item.name }}</a
          ><label class="el-upload-list__item-status-label"
            ><i class="el-icon-upload-success el-icon-check"></i></label
          ><i class="el-icon-close" @click="deleteFile(index)"></i>
        </li>
      </ul>
    </div>

    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
import common from '@/template/mixins/common'
import form from '@/template/mixins/form'
import { CHANGE } from '@/config/eventName'
import { fileToBase64String } from '@/utils/file'
import { extOption } from '@/config/uploadExt'
import _ from 'lodash'
import request from '@/request/request'
export default {
  mixins: [common, form],
  variables: {},
  watch: {
    auto: {
      handler(val) {
        this.value = val ? [] : undefined
      },
    },
    async filesList(list) {
      const thumbnail = []
      for (let item of list) {
        thumbnail.push(await this.getIcon(item))
      }
      this.thumbnail = thumbnail
    },
  },
  components: {},
  data() {
    return {
      value: [],
      typeSetting: {
        file: '*',
        image: 'image/*',
        video: 'video/*',
      },
      // 缩略图展示的列表
      thumbnail: [],
      // 提示的时长
      messageSetting: {
        duration: 5000,
        showClose: true,
      },
    }
  },
  computed: {
    auto() {
      return this.uploadMode === 'auto'
    },
    rules() {
      const { required, regExp, label } = this
      const res = []
      if (required) {
        res.push(
          this.getTrueRule({
            required: true,
            message: `请选择${label}`,
            trigger: 'change',
          })
        )
      }
      return regExp?.reduce((prev, cur) => {
        return prev.concat({
          pattern: cur.reg,
          message: cur.errorTips,
        })
      }, res)
    },
    extLimit() {
      const { ext, extra } = this.uploadType
      const extraExt = extra.split(',').filter((item) => item.trim())
      return [...new Set(ext.concat(extraExt))]
    },
    filesList() {
      const { value } = this
      if (value instanceof Array) {
        return value
      } else if (value) {
        return [value]
      } else {
        return []
      }
    },
  },
  methods: {
    receiveValue(value) {
      // 可能是在表单内被初始化或者被重置
      if(value === undefined) {
        // 自动上传的时候, value是一个数组,重置表单是置为空数组而不是undefined
        this.value = this.auto ? [] : undefined
      } else if(value instanceof Array) {
        this.value = value.filter(value => value)
      } else {
        this.value = value
      }
    },
    deleteFile(index) {
      if (this.auto) {
        if(this.value instanceof Array) {
          this.value = this.value.filter((item, i) => i !== index)
        } else {
          this.value = []
        }
      } else {
        this.value = undefined
      }
      this.handleChange(this.value)
    },
    async getIcon(item) {
      const { auto } = this
      const ext = this.getFileExt(auto ? { url: item } : { file: item } )
      const { file, image, video } = extOption
      if (file.includes(ext)) {
        return require(`../img/${ext}.png`)
      } else if (image.includes(ext)) {
        if (auto) {
          return item
        } else {
          const res = await fileToBase64String({
            file: item,
            size: 0,
          })
          return res.base64String
        }
      } else if (video.includes(ext)) {
        return require('../img/video.png')
      } else {
        return require('../img/file.png')
      }
    },
    chooseFile() {
      this.$refs.fileRef.click()
    },
    async fileChange(e) {
      const { target } = e
      const { files } = target
      // 单次仅能上传一个文件
      const [file] = files

      //文件校验
      if (!this.fileJudge(files)) {
        return (target.value = '')
      }

      // 编辑模式不上传
      if (this.isEditMode) {
        return (target.value = '')
      }
      await this.updateFile(file)
      // 自动上传,可多选
      if (this.auto) {
        const url = await this.getUploadUrl()
        this.value = this.value.concat([url].filter(item => item))
        // 自动上传时值是url
        this.value.length && this.handleChange(this.value)
      }
      // 手动上传,值是文件类型
      else {
        this.value = file
        this.handleChange(this.value)
      }

      target.value = ''
    },
    // 上传到服务器
    async getUploadUrl() {
      if (this.apiRequest) {
        const params = this.getApiRequestParams(this.apiRequest)
        if (!params.url) {
          return
        }
        const res = await request({
          method: params.method,
          url: params.url,
          params: params.params,
          data: params.data,
          headers: params.headers,
        })
        const path = this.dataVariables.find(item => item.key === 'url')?.path
        return _.get(res.data, path)
      }
      return ''
    },
    fileJudge(files) {
      // 文件数判断
      if (this.auto) {
        if (files.length + this.value.length > this.limit) {
          this.$message.error({
            message: '最多可上传' + this.limit + '个文件',
            ...this.messageSetting,
          })
          return false
        }
      }

      // 文件类型和大小判断
      for (let item of files) {
        const { name, size } = item
        const ext = this.getFileExt({ file: item })

        if (!this.uploadType.all && !this.extLimit.includes(ext)) {
          const message = this.extLimit.length
            ? `文件“${name}”格式不符合(只能上传“${this.extLimit.join(
                ','
              )}”格式的文件)`
            : '文件允许上传格式未设置'
          this.$message.error({
            message,
            ...this.messageSetting,
          })
          return false
        }
        if (this.fileSize && size > this.fileSize * 1024 * 1024) {
          this.$message.error({
            message: `文件“${name}”格式超出${this.fileSize} MB`,
            ...this.messageSetting,
          })
          return false
        }
      }
      return true
    },
    // 获取文件类型
    getFileExt({ file: { name } = {}, url }) {
      const fileName = url || name
      if(typeof fileName !== 'string') {
        console.error('fileName不是字符串: ', fileName)
        return ''
      }
      return fileName.slice(fileName.lastIndexOf('.') + 1).toLocaleLowerCase()
    },
    async handleChange(value) {
      this._emitEvent(CHANGE)
      await this.$nextTick()
      this.dispatchFormItem(this.$refs.ElFormItem, 'el.form.change', value)
    },
    updateFile(file) {
      return new Promise((resolve) => {
        this.fileName = file ? file.name : ''
        this.file = file || undefined
        resolve()
      })
    }
  },
  mounted() {
    if (!this.valueInSlot && this.value === undefined) {
      this.value = this.auto ? [] : undefined
    }
    this.fileName = ''
    this.file = undefined
  },
}
</script>

<style lang="less" scoped>
.upload-widget::v-deep {
  margin-left: 0;
  &.is-edit-mode {
    .upload {
      .el-button {
        pointer-events: none;
      }
    }
  }
  .bottom-tips {
    color: #aaa;
    font-size: 12px;
    line-height: 1;
    padding-top: 4px;
  }
  .upload {
    .file {
      display: none;
    }
    .el-upload-list {
      width: 400px;
      &.el-upload-list--picture {
        .el-upload-list__item {
          &.no-image {
            padding-left: 0;
          }
          .el-upload-list__item-name {
            margin-right: 0;
          }
        }
      }
    }
  }
}
</style>
