<template>
  <n-form-item
    v-show="state !== 'hidden'"
    ref="ElFormItem"
    class="transfer-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    :rules="rules"
    @click.native="handleClickFormItem"
  >
    <el-transfer
      v-load-sync="fetchLoading"
      v-model="value"
      :filterable="filterable"
      :filter-placeholder="filterPlaceholder"
      :titles="[leftTitle, rightTitle]"
      :format="{
        noChecked: '共${total}项',
        hasChecked: '共${total}项',
      }"
      @change="handleChange"
      :data="realOptions"
      :props="{ key: 'value' }"
    >
    </el-transfer>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
import common from '@/template/mixins/common'
import form from '@/template/mixins/form'
import options from '@/template/mixins/options'
import { CHANGE } from '@/config/eventName'
export default {
  mixins: [common, form, options],
  variables: {
    $dataList(n) {
      if (Array.isArray(n)) {
        this.dataList = n
        this.handleFormatOptions(n)
      } else {
        console.warn('dataList数据格式必须为数组！')
      }
    }
  },
  watch: {},
  data() {
    return {}
  },
  computed: {
    rules() {
      const { required, regExp, label } = this
      const res = []
      if (required) {
        res.push(this.getTrueRule({
          required: true,
          message: `请选择${label}`,
          trigger: 'blur',
          type: 'array'
        }))
      }
      return regExp?.reduce((prev, cur) => {
        return prev.concat({
          pattern: cur.reg,
          message: cur.errorTips,
        })
      }, res)
    }
  },
  methods: {
    async handleChange(value) {
      // 触发formItem的change校验
      await this.$nextTick()
      this.dispatchFormItem(this.$refs.ElFormItem, 'el.form.blur', value)
      this._emitEvent(CHANGE)
    },
    initVal() {
      const tempValue = []
      this.realOptions.some((item) => {
        if (item.isDefault) {
          tempValue.push(item.value)
        }
      })
      this.value = tempValue
    },
    receiveValue(value) {
      if(value === undefined) {
        // 自动上传的时候, value是一个数组,重置表单是置为空数组而不是undefined
        this.value = []
      } else if(value instanceof Array) {
        this.value = value.filter(value => value)
      }
      this.update2form(this.value)
    }
  }
}
</script>

<style lang="less" scoped>
.transfer-widget::v-deep {
  margin-left: 0;
  &.el-form-item.is-error .el-input__inner, .el-form-item.is-error .el-input__inner:focus, .el-form-item.is-error .el-textarea__inner, .el-form-item.is-error .el-textarea__inner:focus {
    border-color: #dcdcdc;
  }
  .el-transfer {
    width: 600px;
  }
  .el-transfer__button:first-child {
    margin-bottom: 0;
  }
  .bottom-tips {
    color: #aaa;
    font-size: 12px;
    line-height: 1;
    padding-top: 4px;
  }
}
</style>
