<template>
  <div
    v-if="isEditMode"
    :id="elementId"
    role="alert"
    class="el-notification"
    :class="{
      ...this.commonClassName,
      left: position.includes('left'),
      right: position.includes('right')
    }"
    :style="{
      top: position.includes('top') ? '16px' : undefined,
      bottom: position.includes('bottom') ? '16px' : undefined,
      zIndex: '2200'
    }"
  >
    <i v-if="type && type !== 'default'" :class="`el-notification__icon el-icon-${type}`"></i>
    <div class="el-notification__group is-with-icon">
      <h2 class="el-notification__title">{{ title }}</h2>
      <div class="el-notification__content" v-html="filterXss(message)">
      </div>
      <div
        v-if="showClose"
        class="el-notification__closeBtn el-icon-close"
      ></div>
    </div>
  </div>
</template>


<script>
import { OPEN, CLOSE, CLICK } from '@/config/eventName'
import common from '@/template/mixins/common'
import { filterXss } from '@/utils'
export default {
  mixins: [common],
  variables: {},
  props: {},
  data() {
    return {
      instance: [],
    }
  },
  computed: {},
  methods: {
    filterXss,
    async [OPEN]() {
      const { title, message, type, duration, showClose, position } = this
      this.instance.push(
        this.$notify({
          title,
          message,
          dangerouslyUseHTMLString: true,
          type,
          duration: duration * 1000,
          showClose,
          position,
          onClose: async () => {
            await this.$nextTick()
            this._emitEvent(CLOSE)
          },
          onClick: () => {
            this._emitEvent(CLICK)
          }
        })
      )
      await this.$nextTick()
      this._emitEvent(OPEN)
    },
    async [CLOSE]() {
      this.instance.forEach((item) => {
        item.close()
      })
      this.instance = []
      await this.$nextTick()
      this._emitEvent(CLOSE)
    },
  },
}
</script>

