import _ from 'lodash'
export default {
  inject: ['update'],
  data() {
    return {
      value: undefined
    }
  },
  computed: {
    formClassName() {
      return {
        'label-left': this.labelPosition === 'left',
        'label-right': this.labelPosition === 'right',
        'label-top': this.labelPosition === 'top',
      }
    },
    // 是否有值从表单的slot传递下来
    valueInSlot() {
      const { isEditMode, useSlotProps, slotProps, prop  } = this
      return !isEditMode && useSlotProps && Object.prototype.toString.call(slotProps) === '[object Object]' && Object.prototype.hasOwnProperty.call(slotProps, prop)
    }
  },
  watch: {
    slotProps: {
      async handler(n) {
        // 等待组件的props属性赋值
        await this.$nextTick()
        const { valueInSlot, prop, elementType } = this
        // 接收到来自父组件的值时对当前组件的条件 : [开启了动态数据] && [有配置字段] && [有值] && [值不为空对象]
        if(valueInSlot) {
          const value = n[prop]
          // 是新值才去更新变量池,避免change的时候再度赋值
          if(!_.isEqual(this.value, value)) {
            // 由于有些表单项为空时接收的值不能为undefined, 所有要区分开来,特殊的表单项组件各自维护自己的接收值的方法
            if(['transfer', 'cascader', 'upload', 'checkbox-group', 'select'].includes(elementType)) {
              this.receiveValue(value)
            } else {
              this.value = value
            }
          }
        }
      },
      deep: true,
      immediate: true
    },
    value: {
      deep: true,
      handler(val, old) {
        if(!_.isEqual(val, old)) {
          this.update2form(val)
        }
      }
    }
  },
  methods: {
    // 特殊表单选接收值的方法
    receiveValue() {
      const { elementName, elementId } = this
      const message = `请在${elementName}(${elementId})中设置接受值的方法receiveValue`
      console.error(message)
      alert(message)
    },
    getTrueRule(obj) {
      return this.isEditMode ? { ...obj, validator: () => {} } : obj
    },
    /**
     * 更新form表单的Item值
     * @param {vue element} formItem 传入formItem的vue实例
     * @param {string} eventName ['el.form.change', 'el.form.blur'] 触发校验规则的类型,触发change还是blur校验
     * @param {any} value 改变的值
     */
    dispatchFormItem(formItem, eventName, value) {
      formItem.$emit.apply(formItem, [eventName].concat(value));
    },
    // 更新到组件的model中
    update2form(value) {
      const { useSlotProps, prop } = this
      // 满足三个条件执行: [1: 开启了动态数据 2: 配置了字段 3 非编辑模式] => [更新到form表单的model中, 以作表单校验]
      if(useSlotProps && prop && !this.isEditMode) {
        this.update?.(prop, value)
      }
    },
    // 阻止点击事件冒泡
    handleClickFormItem(e) {
      if(!this.isEditMode && this.stopPropagation) {
        e.stopPropagation()
      }
    }
  }
}
