<template>
  <div class="component-dialog" :id='elementId' :class='commonClassName' v-if="isEditMode">
    <div class="component-dialog-header">
      <div class="dialog-title">{{ title }}</div>
      <div class="dialog-close-btn">
        <i class="iconfont icon-liuchengtu-guanbi" v-if='showClose'></i>
      </div>
    </div>
    <div class="component-dialog-content">
      <i v-if="icon" class="icon" :class="icon" :style="{ color }"></i>
      <span class="desc">{{ desc }}</span>
    </div>
    <div class="component-dialog-footer">
      <el-button v-if='showCancelButton'>{{ cancelButtonText }}</el-button>
      <el-button type="primary" v-if='showConfirmButton'>{{ confirmButtonText }}</el-button>
    </div>
  </div>
</template>

<script>
  import { CONFIRM, CANCEL, OPEN, CLOSE } from '@/config/eventName'
  import common from '@/template/mixins/common'
  export default {
    mixins: [common],
    variables: {},
    props: {},
    data() {
      return {}
    },
    computed: {
    },
    methods: {
      async [OPEN]() {
        const h = this.$createElement
        this.$msgbox({
          title: this.title,
          message: h('p', null, [
            h('i', {
              class: this.icon,
              style: {
                marginRight: '5px',
                color: this.color
              },
            }),
            h(
              'span',
              {
                style: {
                  whiteSpace: 'pre-wrap',
                  wordBreak: 'break-all',
                },
              },
              this.desc
            ),
          ]),
          showClose: this.showClose,
          showCancelButton: this.showCancelButton,
          showConfirmButton: this.showConfirmButton,
          confirmButtonText: this.confirmButtonText,
          cancelButtonText: this.cancelButtonText,
          closeOnClickModal: false,
          beforeClose: (action, instance, done) => {
            if (action === 'confirm') {
              this._emitEvent(CONFIRM)
              done()
            } else {
              this._emitEvent(CANCEL)
              done()
            }
            this.$nextTick(() => {
              this._emitEvent(CLOSE)
            })
          },
        })
        await this.$nextTick()
        this._emitEvent(OPEN)
      },
      async [CLOSE]() {
      },
    },
  }
</script>

