<template>
  <n-form-item
    class="date-time-range-picker-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :rules="rules"
    :label-width="labelWidth"
    @click.native="handleClickFormItem"
  >
    <div class="input-container">
      <el-date-picker
        :clearable="clearable"
        :editable="false"
        @clear="value = undefined"
        :style="{ width: inputWidth }"
        :align="align"
        :start-placeholder="startPlaceholder"
        :end-placeholder="endPlaceholder"
        :disabled="state === 'disabled'"
        :readonly="state === 'readonly'"
        :format="format"
        :value-format="valueFormat"
        type="datetimerange"
        :size="size"
        :placeholder="placeholder"
        v-model="value"
        @change="handleChange"
      >
      </el-date-picker>
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
  import { CHANGE } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import form from '@/template/mixins/form'

  export default {
    mixins: [common, form],
    variables: {},
    data() {
      return {
      }
    },
    computed: {
      rules() {
        const { required, regExp, label } = this
        const res = []
        if (required) {
          res.push(this.getTrueRule({
            required: true,
            message: `请选择${label}`,
            trigger: 'change',
          }))
        }
        return regExp?.reduce((prev, cur) => {
          return prev.concat({
            pattern: cur.reg,
            message: cur.errorTips,
          })
        }, res)
      },
    },
    mounted() {
      const { start, end } = this.defaultValueRange
      const defaultStartValue = start.type === 0 ? new Date() : start.value
      const defaultEndValue = end.type === 0 ? new Date() : end.value
      if (
        !this.valueInSlot &&
        defaultStartValue &&
        defaultEndValue &&
        new Date(defaultStartValue) <= new Date(defaultEndValue)
      ) {
        this.value = [new Date(defaultStartValue), new Date(defaultEndValue)]
      }
      const [startTime, endTime] = this.value || []
      this.startTime = startTime
      this.endTime = endTime
    },
    methods: {
      handleChange(value) {
        const [startTime, endTime] = value || []
        this.startTime = startTime
        this.endTime = endTime
        this._emitEvent(CHANGE)
      },
    },
  }
</script>

<style lang="less" scoped>
  .date-time-range-picker-widget::v-deep {
    .input-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .bottom-tips {
      color: #aaa;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
    }
  }
</style>
