import Vue from 'vue'
// import VueCompositionAPI from '@vue/composition-api'
import '@/set-public-path.js'
import {
  run,
  store
} from '@project/frame'
import { dynamicMenu, staticMenu } from '@/router'
import '@/lib/n-element-ui'
import '@/lib/vxe-table'
import '@/lib/global'
import '@/styles/index.less'
import '@/styles/changeTheme.scss'
import '@/styles/animate.min.css'
import _ from 'lodash'
import codeStore from '@/store'
import '@/directive'
import { getAppSourcePrefix, getImgPrefix } from '@/utils'
// 这个文件必须在mixin开始前引入, 否则因为某些不知名的原因导致某些文件获取到export 为 undefined
import '@/renderer/tools/config'

// Vue.use(VueCompositionAPI)

// catch vue本身的错误
Vue.config.errorHandler = function (err, vm) {
  // const str = 'Cannot read property \'call\' of undefined'
  // const s = err.toString()
  // if (!s.includes(str)) {
  //   console.error(err)
  // }
  console.error(err)

}

Vue.prototype.$getImgPrefix = getImgPrefix

const isProd = process.env.NODE_ENV === 'production'

if (!isProd) {
  Vue.config.performance = true
}
export function installApp() {
  console.log('web_code install')
  store.registerModule('CODE', _.cloneDeep(codeStore))
  getAppSourcePrefix()
  return run(isProd, '/front', dynamicMenu, {
    routerCreated: (routes, router) => {
      router.addRoutes(staticMenu)
    },
  })
}
