<template>
  <el-dropdown
    class="dropdown-widget"
    :class='commonClassName'
    :style='componentStyle'
    :splitButton="splitButton"
    :trigger="trigger"
    :hideOnClick="hideOnClick"
    :size="size"
    :type="type"
    @command="handleChange"
    @click.native="handleStopPropagation"
  >
    <el-button v-if="!splitButton" :type="type" :size="size">
      {{ title }}<i class="el-icon-arrow-down el-icon--right"></i>
    </el-button>
    <template v-else>{{title}}</template>
    <el-dropdown-menu slot="dropdown">
      <el-dropdown-item
       v-for="item in realOptions"
       :key="item.value"
       :command="item.value"
       :disabled="item.disabled"
       >{{item.label}}</el-dropdown-item>
    </el-dropdown-menu>
  </el-dropdown>
</template>

<script>
  import common from '@/template/mixins/common'
  import options from '@/template/mixins/options'
  import {
  CHANGE,
  RESET
} from '@/config/eventName'

  export default {
    variables: {
      $dataList(n) {
        if (Array.isArray(n)) {
          this.dataList = n
          this.handleFormatOptions(n)
        } else {
          console.warn('dataList数据格式必须为数组！')
        }
      }
    },
    mixins: [common, options],
    data() {
      return {
        value: undefined
      }
    },
    methods: {
      handleChange(value) {
        this.value = value
        this._emitEvent(CHANGE)
      },
      handleStopPropagation(e) {
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      }
    },
  }
</script>

<style lang="less" scoped>
  .dropdown-widget::v-deep {
    .el-button--mini {
      height: 28px;
    }
    .el-button--small {
      height: 32px;
    }
  }
</style>
