<template>
  <n-tabs
    v-if="isTabsVisible"
    class='tabs-widget'
    :class='commonClassName'
    :style='componentStyle'
    v-model="activeTabName"
    :type="type"
    :stretch='stretch'
    :tab-position='tabPosition'
    @tab-remove="remove"
    @tab-click="click"
    @tab-add="add"
    @click.native="handleStopPropagation"
  >
    <n-tab-pane
      class='tab-pane-content'
      v-for="tab in dataList"
      :key="tab.key"
      :label="tab.showBadge ? undefined : tab.label"
      :name="tab.key"
      :disabled="tab.disabled"
      :closable="tab.closable"
      :lazy="tab.lazy"
    >
      <n-badge
        class="tab-badge"
        v-if="tab.showBadge"
        slot="label"
        :value="getBadgeValue(tab.badgeMessage)"
        :max="tab.max"
        :is-dot="tab.isDot"
        :type="tab.badgeType"
      >
        <span>{{tab.label}}</span>
      </n-badge>
      <component
        class="tab-content"
        :is='getSlotByName(tab.key).slotElement'
        :ref="getSlotByName(tab.key).elementId"
        :data-zone="getDataZone(tab.key)"
        v-bind="{ ...getSlotByName(tab.key), slotProps: tab }"
        :id="getSlotByName(tab.key).elementId"
      ></component>
    </n-tab-pane>
  </n-tabs>
</template>

<script>
  import common from '@/template/mixins/common'
  import {
    UPDATE_DYNAMIC_PROPS,
    DELETE_LAYOUT_DATA,
    UPDATE_LAYOUT_DATA,
    UPDATE_EVENTS_DATA,
    UPDATE_FUNCTIONS_DATA,
    UPDATE_JS_DATA,
  } from '@/config/storeKey'
  import { CLICK, ADD_TAB, CHANGE_TAB, RESET, ADD_TAB_SUCCESS, CLOSE_TAB_SUCCESS, CHANGE_BADGE } from '@/config/eventName'
  import { getNodeByElementId, getRandomHexadecimal, treeUtil } from '@/utils/tree-utils'
  import { globalEmit } from '@/renderer/tools/eventBus'
  import { EXECUTE_VALUE, TRIGGER } from '@/config/eventKey'
  import _ from 'lodash'
  import { GLOBAL_INCREMENT, FUNCTION_FLAG, JS_FLAG } from '@/config'
  import { isNumString } from '@/utils/validateUtils'
import { isValidateVariable } from '@/utils'

  export default {
    mixins: [common],
    variables: {},
    data() {
      return {
        tabsProvide: {
          visible: false,
        },
        isTabsVisible: true
      }
    },
    provide() {
      return {
        tabsProvide: this.tabsProvide
      }
    },
    computed: {
      dataList() {
        const showedList = this.tabsData.filter(tab => {
          return this.isEditMode || (!this.isEditMode && tab.hidden === false)
        })
        this.showedList = showedList
        return this.showedList
      }
    },
    methods: {
      [RESET]() {
        // todo
      },
      getDataZone(key) {
        const slot = this.getSlotByName(key)
        if (!slot) {
          return ''
        }
        return `${slot.elementId}_${slot.elementType}`
      },
      click() {
        this.tabsProvide.visible = true
        this.$nextTick(() => {
          this.tabsProvide.visible = false
        })
        this._emitEvent(CLICK)
      },
      remove(tabName) {
        const deleteNode = this.children.find(item => this.getElementPropsById(item.elementId).slotName === tabName)
        this._storeDispatch(DELETE_LAYOUT_DATA, { elementId: deleteNode.elementId })
        this.tabsData = this.tabsData.map(item => {
          if (item.key === tabName) {
            return {...item, hidden: true}
          } else {
            return item
          }
        })
        this.activeTabName = this.activeTabName === tabName ? _.last(this.dataList)?.key : this.activeTabName
        this._emitEvent(CLOSE_TAB_SUCCESS)
      },
      add() {

      },
      handleStopPropagation(e) {
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
      forceUpdateTabs:_.debounce(function() {
        this.isTabsVisible = false
        this.$nextTick(() => {
          this.isTabsVisible = true
        })
      }, 100),
      [ADD_TAB](params) {
        const addTabs = [params]
        const addTabsDataList = []
        const eventsData = this.getStore().eventsData
        const functionsData = this.getStore().functionsData
        const jsData = this.getStore().jsData
        const dynamicPropsList = []
        const eventsDataList = []
        const newNodeList = []
        const newFunctionsData = []
        const newJsData = []
        // if (!window[GLOBAL_INCREMENT]) {
        //   window[GLOBAL_INCREMENT] = 0
        // }
        addTabs.forEach((tab, index) => {
          const { tabName, label: labelKey } = tab
          const label = this.getObjectVariables(labelKey)
          const currentTab = this.tabsData.find(item => item.key === tabName)
          const sourceNode = this.children.find(item => this.getElementPropsById(item.elementId).slotName === tabName)
          const newKey = getRandomHexadecimal(6)
          const newEventsList = []
          const newFunctionList = []
          const newJsList = []
          const diffIds = []
          const diffFuncIds = []
          const diffJsIds = []
          const currentNode = getNodeByElementId(this.getStore().layoutData, sourceNode.elementId)
          const newNode = _.cloneDeep(currentNode)
          treeUtil.forEach([newNode], (child) => {
            const newProps = {}
            const newEvents = {}
            const oldId = child.elementId
            // const subId = ++window[GLOBAL_INCREMENT]
            // const newId = `${oldId}-${subId}`
            const newId = `${child.elementType}-${getRandomHexadecimal(6)}`
            const {children, ...childProps} = this.getElementPropsById(oldId)
            child.elementId = newId
            newProps[newId] = _.cloneDeep({
              ...childProps,
              elementId: newId,
              layout: childProps.layout ? {...childProps.layout, i: newId} : undefined
            })
            newEvents[newId] = _.cloneDeep(eventsData[oldId])
            dynamicPropsList.push(newProps)
            newEventsList.push(newEvents)
            diffIds.push({newId, oldId})

            // 处理函数和js脚本和高级变量
            const eventsDataString = JSON.stringify(eventsData[oldId] || {})
            const dynamicPropsString = JSON.stringify(childProps || {})
            const functionExp = new RegExp(`${FUNCTION_FLAG}.{12}`, 'g')
            const jsExp = new RegExp(`${JS_FLAG}.{12}`, 'g')
            const usedFunctionIds =
              eventsDataString.concat(dynamicPropsString).match(functionExp) || []
            const usedFunctionList = functionsData.filter((item) =>
              usedFunctionIds.includes(`${FUNCTION_FLAG}${item.id}`)
            )
            const functionsDataString = JSON.stringify(usedFunctionList)
            const usedJsIds = functionsDataString.match(jsExp) || []
            const usedJsList = jsData.filter((item) =>
              usedJsIds.includes(`${JS_FLAG}${item.id}`)
            )

            usedFunctionList.forEach(func => {
              const currentFunction = functionsData.find(item => item.id === func.id)
              const oid = func.id
              const nid = getRandomHexadecimal(12)
              const oldFuncId = `${FUNCTION_FLAG}${func.id}`
              const newFuncId = `${FUNCTION_FLAG}${nid}`
              newFunctionList.push({..._.cloneDeep(currentFunction), id: nid})
              diffFuncIds.push({newFuncId, oldFuncId, nid, oid})
            })

            usedJsList.forEach(js => {
              const currentJs = jsData.find(item => item.id === js.id)
              const nid = getRandomHexadecimal(12)
              const oldJsId = `${JS_FLAG}${js.id}`
              const newJsId = `${JS_FLAG}${nid}`
              newJsList.push({...currentJs, id: nid})
              diffJsIds.push({newJsId, oldJsId})
            })
          })
          newEventsList.forEach(events => {
            Object.keys(events).forEach(eId => {
              let preEventString = JSON.stringify(events[eId])
              if (preEventString) {
                diffIds.forEach(({newId, oldId}) => {
                  preEventString = preEventString.replaceAll(oldId, newId)
                })
                diffFuncIds.forEach(({newFuncId, oldFuncId, nid, oid}) => {
                  preEventString = preEventString.replaceAll(oldFuncId, newFuncId)
                  preEventString = preEventString.replaceAll(oid, nid)
                })
                events[eId] = JSON.parse(preEventString)
              }
            })
          })

          dynamicPropsList.forEach(props => {
            Object.keys(props).forEach(pId => {
              let prePropString = JSON.stringify(props[pId])
              if (prePropString) {
                diffIds.forEach(({newId, oldId}) => {
                  prePropString = prePropString.replaceAll(oldId, newId)
                })
                diffFuncIds.forEach(({newFuncId, oldFuncId}) => {
                  prePropString = prePropString.replaceAll(oldFuncId, newFuncId)
                })
                props[pId] = JSON.parse(prePropString)
              }
            })
          })

          newFunctionList.forEach(func => {
            let paramsDataString = JSON.stringify(func.paramsData)
            if (paramsDataString) {
              diffIds.forEach(({newId, oldId}) => {
                paramsDataString = paramsDataString.replaceAll(oldId, newId)
              })
              diffJsIds.forEach(({newJsId, oldJsId}) => {
                paramsDataString = paramsDataString.replaceAll(oldJsId, newJsId)
              })
            }
            func.paramsData = JSON.parse(paramsDataString)
            newFunctionsData.push(func)
          })

          newJsList.forEach(js => {
            let jsString = JSON.stringify(js)
            if (jsString) {
              diffIds.forEach(({newId, oldId}) => {
                jsString = jsString.replaceAll(oldId, newId)
              })
            }
            newJsData.push(JSON.parse(jsString))
          })

          const diffEvents = newEventsList.filter(eventData => Object.keys(eventData).length)
          eventsDataList.push(...diffEvents)
          newNodeList.push(newNode)


          const currentSlotProps = {}
          currentSlotProps[newNode.elementId] = {
            elementId: newNode.elementId,
            slotName: newKey
          }
          dynamicPropsList.push(currentSlotProps)

          addTabsDataList.push({
            ...currentTab,
            key: newKey,
            label,
            hidden: false
          })
        })

        this._storeCommit(UPDATE_DYNAMIC_PROPS, { dynamicPropsList })
        this._storeCommit(UPDATE_EVENTS_DATA, { eventsDataList })
        this._storeCommit(UPDATE_FUNCTIONS_DATA, { functionsData: [...functionsData, ...newFunctionsData] })
        this._storeCommit(UPDATE_JS_DATA, { jsData: [...jsData, ...newJsData] })
        this.children = [...this.children, ...newNodeList]
        this._storeCommit(UPDATE_LAYOUT_DATA, {elementId: this.elementId, layout: {children: this.children}})
        this.showIds = this.children.map(item => item.elementId)
        this.tabsData.push(...addTabsDataList)
        this.activeTabName = _.last(addTabsDataList).key
        if (!this.isEditMode) {
          this.updateChildrenStaticStyle()
        }
        this._emitEvent(ADD_TAB_SUCCESS)
      },
      [CHANGE_TAB](params) {
        const { tabName, label } = params
        if (!tabName && !label) {
          return
        }
        if (tabName) {
          this.activeTabName = tabName
        } else if (label) {
          const realLabel = this.getObjectVariables(label)
          const currentTabName = this.dataList.find(item => item.label === realLabel)?.key
          if (currentTabName) {
            this.activeTabName = currentTabName
          } else {
            console.warn(`标签名为[${realLabel}]的标签不存在！`)
          }
        }
      },
      [CHANGE_BADGE](params) {
        const { badgeList } = params
        badgeList.forEach(item => {
          const { tab, badgeMessage } = item
          if (!tab || !badgeMessage) {
            return
          }
          if (isValidateVariable(tab)) {
            const realLabel = this.getObjectVariables(tab)
            const currentTab = this.tabsData.find(item => item.label === realLabel)
            if (currentTab) {
              currentTab.badgeMessage = this.getObjectVariables(badgeMessage)
            } else {
              console.warn(`标签名为[${realLabel}]的标签不存在！`)
            }
          } else {
            const currentTab = this.tabsData.find(item => item.key === tab)
            if (currentTab) {
              currentTab.badgeMessage = this.getObjectVariables(badgeMessage)
            }
          }
        })
      },
      getBadgeValue(message = '') {
        return isNumString(message) ? +message : message
      }
    },
    mounted() {
      this.activeTabName = this.dataList.find(item => item.active)?.key
      if (this.isEditMode) {
        this.activeTabName = this.activeTabName || _.first(this.tabsData).key
        ;['type', 'tabsData'].forEach(watchKey => {
          this.$watch(watchKey, this.forceUpdateTabs)
        })
      }
    },
  }
</script>

<style lang="less" scoped>
@import '~@/styles/index.less';
  .tabs-widget::v-deep {
    &.is-edit-mode {
      .tab-pane-content {
        padding: 1px;
      }
    }
    .tab-badge {
      .el-badge__content {
        top: 7px;
        right: 2px;
        &--primary {
          background-color: @drop-main-color !important;
        }
        &--success {
          background-color: #67C23A !important;
        }
        &--warning {
          background-color: #e6a23c !important;
        }
        &--danger {
          background-color: #f56c6c !important;
        }
        &--info {
          background-color: #909399 !important;
        }
      }
    }
  }
</style>
