<template>
  <div
    v-loading="useFetchLoading && fetchLoading"
    :id="elementId"
    class="chart-content"
    :class="commonClassName"
    :style="componentStyle"
  >
    <el-tag type="danger" v-if="!isFullConfig">图表配置不完全</el-tag>
  </div>
</template>

<script>
import common from '@/template/mixins/common'
import _ from 'lodash'

import { RESET, DATA_LOAD_SUCCESS, UPDATE_DATA } from '@/config/eventName'
import request from '@/request/request'
import { asyncFunc, debounce } from '@/utils'

import { RECEIVE_PROPS, RECEIVE_STYLES } from '@/config/eventKey'
import trigger from '@/renderer/tools/trigger'
import { getConfigByKey } from '@/renderer/tools/config'
import { events } from './watch-props'
export default {
  mixins: [common],
  computed: {
    // 配置是否完全
    isFullConfig() {
      const {
        type,
        chartConfigId,
        staticData,
        apiRequest,
        sqlRequest,
        dataMapping,
      } = this
      return [
        type,
        chartConfigId,
        apiRequest || sqlRequest || staticData,
        dataMapping,
      ].every((item) => {
        if (item instanceof Array) {
          return item.length
        }
        return item
      })
    },
  },
  watch: {},
  data() {
    return {
      chartInstance: null,
      ANTV: null,
      fetchLoading: false,
    }
  },
  mounted() {
    import('@antv/g2plot').then((res) => {
      this.ANTV = res
      this.getChartData()
    })
  },
  created() {
    // 渲染添加防抖,避免出现重复画面, 这里在created写是因为如果在methods里面写,多个相同实例会共用了一个方法,只执行了最后一次
    this.renderChart = debounce(this.renderChart)
  },
  beforeDestroy() {
    this.chartInstance?.destroy()
  },
  methods: {
    _receiveData({ type }) {
      // 配置更新,刷新图表
      if ([RECEIVE_STYLES, RECEIVE_PROPS].includes(type)) {
        this.getChartData()
      }
    },
    /**
     * 渲染方法
     */
    async renderChart(destroy) {
      // 此处 $nextTick是为了能拿到$refs.chart
      await this.$nextTick()
      if (!this.chartInstance || destroy) {
        // 先销毁当前的,避免重复
        this.chartInstance?.destroy()
        this.chartInstance = new this.ANTV[this.type](
          this.elementId,
          this.chartOptions
        )
        // 编辑模式不监听
        if(!this.isEditMode) {
        const { elementId, elementType, elementVersion } = this
        const { events: allEvents } = getConfigByKey.call(this, { elementId, elementType, elementVersion })
        const eventKeys = allEvents.map(item => item.eventName).filter(item => !events.find(event => event.eventName === item))
        eventKeys.forEach(eventName => {
          this.chartInstance.on(eventName, (...args) => {
            this._emitEvent(eventName, ...args)
          })
        })
        }
        this.chartInstance.render()
      } else {
        this.chartInstance.update(this.chartOptions)
      }
    },
    formatRequestData(response, destroy) {
      this.responseData = response
      const options = _.cloneDeep(this.getObjectVariables(this.chartConfigId))
      const { dataMapping } = this

      // 数据映射
      dataMapping.forEach(({ sourcePath, mappingPath, formatCode }) => {
        // 当前请求映射和配置映射都配置的情况下才去映射
        if (sourcePath.length && mappingPath.length) {
          let trueData
          const mappingData = _.get({ response }, sourcePath)
          if (formatCode) {
            trueData = trigger.getFuncValueByParams({
              functionId: formatCode,
              extraParams: [
                {
                  key: 'data',
                  value: mappingData,
                },
              ],
              vueThis: this,
            })
          } else {
            trueData = mappingData
          }
          _.set(options, mappingPath, trueData)
        }
      })
      this.chartOptions = options
      this.renderChart(destroy)
    },
    async [RESET](args) {
      this.getChartData(args)
    },
    async getChartData({ logId = '', destroy = true } = {}) {
      if (!this.isFullConfig) {
        return this.chartInstance?.destroy()
      }
      const time = new Date()
      let executeResult
      // 由其他组件触发的
      if (logId) {
        executeResult = {
          children: [],
          pid: logId,
        }
      }
      // 自身初始化触发的, 不是
      else {
        executeResult = {
          children: [],
          pid: '',
          tag: '数据集',
          id: trigger.getUnionKey(time),
          target: this,
          time,
        }
      }

      const { apiRequest, sqlRequest, isEditMode } = this
      // api 请求
      if (apiRequest && !isEditMode) {
        const params = this.getApiRequestParams(this.apiRequest)
        if (!params.url) {
          return
        }
        const unionId = trigger.getUnionKey()
        const requestResult = {
          children: [],
          pid: unionId,
        }
        trigger.recordLog({
          pid: logId || executeResult.id,
          children: [
            trigger.formatLinkMsg({
              id: unionId,
              type: '调用API',
              tagList: {
                method: params.method,
                url: params.url,
                params: params.params,
                data: params.data,
                headers: params.headers,
              },
            }),
          ],
        })
        this.fetchLoading = true
        const [err, res] = await asyncFunc(request, {
          method: params.method,
          url: params.url,
          params: params.params,
          data: params.data,
          headers: params.headers,
        })
        this.fetchLoading = false

        requestResult.children.push(
          trigger.formatLinkMsg(
            res
              ? { successText: '成功回调', tagList: { response: res } }
              : { errorText: '失败回调', tagList: { response: err } }
          )
        )

        if (res) {
          this.formatRequestData(res.data, destroy)
          this._emitEvent(DATA_LOAD_SUCCESS)
          requestResult.children.push(
            trigger.formatLinkMsg({
              successText: '数据加载完成',
              tagList: { chartOptions: this.chartOptions },
            })
          )
        }
        trigger.recordLog(requestResult)
      }
      // sql 请求
      else if (sqlRequest && !isEditMode) {
        const params = this.getSqlParams(sqlRequest)
        const unionId = trigger.getUnionKey()
        trigger.recordLog({
          pid: logId || executeResult.id,
          children: [
            trigger.formatLinkMsg({
              id: unionId,
              type: '调用SQL',
            }),
          ],
        })
        const requestResult = {
          children: [],
          pid: unionId,
        }

        this.fetchLoading = true
        let res, err
        try {
          res = await this.$http({
            url: params.url,
            data: trigger.formatSQLParams(params.data, requestResult.pid),
          })
        } catch (e) {
          console.log(e)
          err = e
        }
        this.fetchLoading = false

        if (res) {
          this.formatRequestData(res.data, destroy)
          this._emitEvent(DATA_LOAD_SUCCESS)
        }

        // 日志记录开始
        // 记录sql执行结果
        trigger.recordSqlResponse(requestResult.pid)

        requestResult.children.push(
          trigger.formatLinkMsg(
            res
              ? { successText: '成功回调', tagList: { response: res } }
              : { errorText: '失败回调', tagList: { response: err } }
          )
        )

        if (res) {
          requestResult.children.push(
            trigger.formatLinkMsg({
              successText: '数据加载完成',
              tagList: { chartOptions: this.chartOptions },
            })
          )
        }
        trigger.recordLog(requestResult)
      }
      // 使用静态数据
      else {
        this.formatRequestData(this.staticData, destroy)
        this._emitEvent(DATA_LOAD_SUCCESS)
        executeResult.children.push(
          trigger.formatLinkMsg({
            successText: '数据加载完成',
            tagList: { chartOptions: this.chartOptions },
          })
        )
      }
      trigger.recordLog(executeResult)
    },
    [UPDATE_DATA](args) {
      this.getChartData({ destroy: false, ...args })
    },
  },
}
</script>

<style lang="less" scoped>
@import '~@/styles/index.less';

.chart-content {
  width: 100%;
  height: 100%;
}
</style>
