<template>
  <n-form-item
    class="date-time-picker-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    :rules="rules"
    @click.native="handleClickFormItem"
  >
    <div class="input-container">
      <el-date-picker
        :clearable="clearable"
        :editable="editable"
        @clear="value = undefined"
        :style="{ width: inputWidth }"
        :disabled="state === 'disabled'"
        :readonly="state === 'readonly'"
        :format="format"
        :value-format="valueFormat"
        type="datetime"
        :size="size"
        :placeholder="placeholder"
        @change="handleChange"
        v-model="value"
      >
      </el-date-picker>
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
  import { CHANGE } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import form from '@/template/mixins/form'

  export default {
    mixins: [common, form],
    variables: {},
    data() {
      return {
      }
    },
    computed: {
      rules() {
        const { required, regExp, label } = this
        const res = []
        if (required) {
          res.push(this.getTrueRule({
            required: true,
            message: `请选择${label}`,
            trigger: 'change'
          }))
        }
        return regExp?.reduce((prev, cur) => {
          return prev.concat({
            pattern: cur.reg,
            message: cur.errorTips,
          })
        }, res)
      },
    },
    mounted() {
      if (!this.valueInSlot && typeof this.value === 'undefined') {
        const { type, value } = this.defaultValue
        const defaultValue = type === 0 ? new Date() : value
        this.value = defaultValue
      }
    },
    methods: {
      handleChange(value) {
        this.value = value
        this._emitEvent(CHANGE)
      },
    },
  }
</script>

<style lang="less" scoped>
  .date-time-picker-widget::v-deep {
    .input-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .bottom-tips {
      color: #aaa;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
    }
  }
</style>
