const requireModule = require.context('./components', true, /.index.js$/)
const modules = {}
requireModule.keys().forEach(c => {
  const module = requireModule(c)
  const d = module.default || {}
  if (d.name && d.version) {
    modules[d.name] = d
  }
})
export const versionData = getVersionData(modules)

function getVersionData(modules = {}) {
  const versionData = {}
  Object.keys(modules).forEach((elementType) => {
    const module = modules[elementType]
    const name = module.name || elementType
    Object.keys(module.version).forEach((v) => {
      versionData[v] = versionData[v] || {}
      versionData[v][name] = module.version[v]
    })
  })
  return versionData
}
