<template>
  <div
    :id='elementId'
    :class="{
      ...this.commonClassName,
      [`el-message--${type}`]: true,
      'is-center': center
    }"
    v-if="isEditMode"
    role="alert"
    class="el-message is-closable"
    style="top: 20px; z-index: 2000"
  >
    <i class="el-message__icon" :class="`el-icon-${type}`"></i>
    <p class="el-message__content">{{ message }}</p>
    <i class="el-message__closeBtn el-icon-close" v-if="showClose"></i>
  </div>
</template>

<script>
import { OPEN, CLOSE } from '@/config/eventName'
import common from '@/template/mixins/common'
export default {
  mixins: [common],
  variables: {},
  props: {},
  data() {
    return {
      instance: [],
    }
  },
  computed: {},
  methods: {
    async [OPEN]() {
      const { message, type, duration, showClose, center } = this
      this.instance.push(this.$message({
        message: message,
        type: type,
        duration: duration * 1000,
        showClose,
        center,
        onClose: async () => {
          await this.$nextTick()
          this._emitEvent(CLOSE)
        }
      }))
      await this.$nextTick()
      this._emitEvent(OPEN)
    },
    async [CLOSE]() {
      this.instance.forEach(item => {
        item.close()
      })
      this.instance = []
      await this.$nextTick()
      this._emitEvent(CLOSE)
    },
  },
}
</script>

