import versionData from './versionData'
import { treeUtil } from '@/utils/tree-utils'
import { widgetsMap } from './widgets'
import _ from 'lodash'
export const versionList = Object.keys(versionData).sort(
  (v1, v2) => Number(v1.replace('v', '')) - Number(v2.replace('v', ''))
)

const hasVersion = (version, elementType) =>
  !!Object.entries(versionData).find(
    ([v, o]) => v === version && o.includes(elementType)
  )

export const getLastVersion = (elementType) => {
  const versionList = Object.entries(versionData).sort(
    ([v1, o1], [v2, o2]) =>
      Number(v2.replace('v', '')) - Number(v1.replace('v', ''))
  )
  try {
    return versionList.find(([v, o]) => o.includes(elementType))[0]
  } catch (e) {
    console.error(e)
    console.warn('如果是新增组件，请重新编译')
  }
}

export const getWidgetList = (widgetType, version) => {
  const widgetsCopy = _.cloneDeep(widgetsMap[widgetType]) || []
  treeUtil.forEach(widgetsCopy, (widget) => {
    widget.elementVersion = hasVersion(version, widget.elementType) ?
      version :
      getLastVersion(widget.elementType)
  })
  return widgetsCopy
}
