<template>
  <n-form-item
    v-show="state != 'hidden'"
    class="select-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    :rules="rules"
    @click.native="handleClickFormItem"
  >
    <div class="radio-container">
      <el-select
        v-load-sync="fetchLoading"
        v-model="value"
        :filterable="filterable"
        :clearable="clearable"
        :multiple="multiple"
        :size="size"
        :placeholder="placeholder"
        :disabled="state === 'disabled'"
        :readonly="state === 'readonly'"
        @change="handleChange"
        :style="{ width: selectWidth }"
      >
        <el-option
          v-for="item in realOptions"
          :key="item.value"
          :label="item.label"
          :value="item.value"
          :disabled="item.disabled"
        >
        </el-option>
      </el-select>
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
  import common from '@/template/mixins/common'
  import form from '@/template/mixins/form'
  import options from '@/template/mixins/options'
  import { CHANGE } from '@/config/eventName'

  export default {
    mixins: [common, form, options],
    variables: {
      $dataList(n) {
        if (Array.isArray(n)) {
          this.dataList = n
          this.handleFormatOptions(n)
        } else {
          console.warn('dataList数据格式必须为数组！')
        }
      }
    },
    data() {
      return {}
    },
    watch: {
      multiple(v) {
        this.value = v ? [] : undefined
      }
    },
    computed: {
      rules() {
        const { required, regExp, label } = this
        const res = []
        if (required) {
          res.push(this.getTrueRule({
            required: true,
            message: `请选择${label}`,
            trigger: 'change'
          }))
        }
        const rules = regExp?.reduce((prev, cur) => {
          return prev.concat({
            pattern: cur.reg,
            message: cur.errorTips,
          })
        }, res)
        return rules
      },
    },
    methods: {
      receiveValue(value) {
        if(this.multiple && value instanceof Array) {
          this.value = value.filter(value => value)
        } else {
          this.value = value
        }
        this.update2form(this.value)
      },
      handleChange(value) {
        this._emitEvent(CHANGE)
      },
      initVal() {
        if (this.realOptions.length) {
          const defValueArr = this.realOptions.filter((item) => {
            return item.isDefault
          })
          if (this.multiple) {
            this.value = defValueArr.length
              ? defValueArr.reduce((prev, cur) => {
                  return prev.concat(cur.value)
                }, [])
              : []
          } else {
            this.value = defValueArr.length ? defValueArr[0].value : undefined
          }
        }
      }
    },
  }
</script>

<style lang="less" scoped>

  .select-widget::v-deep {
    margin-left: 0;
    &.is-edit-mode {
      .radio-container {
        pointer-events: none;
        .el-input__suffix-inner {
          pointer-events: none;
        }
      }
    }

    .bottom-tips {
      color: #aaa;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
    }
  }
</style>
