<template>
  <n-form-item
    v-show="state != 'hidden'"
    ref="ElFormItem"
    class="radio-group-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    :rules="rules"
    @click.native="handleClickFormItem"
  >
    <div class="radio-container">
      <el-radio-group
        v-load-sync="fetchLoading"
        v-model="value"
        :disabled="state == 'disabled'"
        :size="size"
        :class="{
          'vertical-cont': sortMode == 'vertical',
        }"
        @change="handleChange"
      >
        <template v-if="!isButton">
          <el-radio
            :label="item.value"
            v-for="item in realOptions"
            :key="item.value"
            :disabled="item.disabled"
            :border="border"
            >{{ item.label }}</el-radio
          >
        </template>
        <el-radio-button
          class="radio-button"
          v-else
          :label="item.value"
          v-for="item in realOptions"
          :key="item.value"
          :disabled="item.disabled"
          >
            <i v-if="item.icon && iconPosition === 'left'" :class="item.icon" style="margin-right: 3px;"></i>
            {{ item.label }}
            <i v-if="item.icon && iconPosition === 'right'" :class="item.icon" style="margin-left: 3px;"></i>
          </el-radio-button
        >
      </el-radio-group>
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
  import common from '@/template/mixins/common'
  import form from '@/template/mixins/form'
  import options from '@/template/mixins/options'
  import { CHANGE } from '@/config/eventName'
  export default {
    mixins: [common, form, options],
    variables: {},
    data() {
      return {
      }
    },
    computed: {
      rules() {
        const { required, regExp, label } = this
        const res = []
        if (required) {
          res.push(this.getTrueRule({
            required: true,
            message: `请选择${label}`,
            trigger: 'blur'
          }))
        }
        return regExp?.reduce((prev, cur) => {
          return prev.concat({
            pattern: cur.reg,
            message: cur.errorTips,
          })
        }, res)
      },
    },
    watch: {},
    mounted() {
    },
    methods: {
      async handleChange(value) {
        await this.$nextTick()
        // 触发formItem的change校验
        this.dispatchFormItem(this.$refs.ElFormItem, 'el.form.blur', value)
        this._emitEvent(CHANGE)
      },
      initVal() {
        if (
          this.realOptions.length
        ) {
          this.value = this.staticData.filter((item) => {
            return item.isDefault
          })[0]?.value
        }
      }
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .radio-group-widget::v-deep {
    .radio-container {
      .vertical-cont {
        display: flex;
        flex-direction: column;
        .el-radio {
          margin-bottom: 10px;
          &:last-child {
            margin-bottom: 0;
          }
        }
      }
      .radio-button {
        .el-radio-button__inner [class*=el-icon-] {
          line-height: 0.9;
        }
        .el-radio-button__inner .low_code {
          line-height: 0.9;
        }
        &:nth-last-child(2) {
          .el-radio-button__inner {
            border-radius: 0 4px 4px 0 !important;
          }
        }
      }
    }
    .bottom-tips {
      color: #aaa;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
    }
  }
</style>
