<template>
  <n-form-item
    class="textarea-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    :rules="rules"
    @click.native="handleClickFormItem"
  >
    <div class="input-container" v-show="state != 'hidden'">
      <el-input
        :auto-complete="autoComplete"
        @clear="value = undefined"
        :style="{ width: inputWidth }"
        :disabled="state == 'disabled'"
        :readonly="state == 'readonly'"
        :rows="rows"
        type="textarea"
        :size="size"
        :autosize="autoSize ? {minRows: rows} : false"
        :placeholder="placeholder"
        @input="handleChange"
        v-model="value"
      >
      </el-input>
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
  import { CHANGE } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import form from '@/template/mixins/form'

  export default {
    mixins: [common, form],
    variables: {},
    computed: {
      rules() {
        const { required, maxLength, minLength, regExp, label } = this
        const res = []
        if (required) {
          res.push(this.getTrueRule({
            required: true,
            message: `请输入${label}`,
            trigger: 'change',
          }))
        }
        if (maxLength > 0) {
          res.push({
            min: minLength,
            max: maxLength,
            message: `长度在 ${minLength} 到 ${maxLength} 个字符`,
            trigger: 'change',
          })
        }
        return regExp?.reduce((prev, cur) => {
          return prev.concat({
            pattern: cur.reg,
            message: cur.errorTips,
          })
        }, res)
      },
    },
    methods: {
      handleChange(value) {
        if (this.isTrim && value) {
          this.value = value.trim?.()
        }
        this._emitEvent(CHANGE)
      },
    },
    mounted() {
      if (!this.valueInSlot && this.value === undefined) {
        this.value = this.defaultValue
      }
    },
  }
</script>

<style lang="less" scoped>
  .textarea-widget::v-deep {
    .input-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
    .bottom-tips {
      color: #aaa;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
    }
  }
</style>
