<template>
  <div
    v-loading="!chartInstance && fetchLoading"
    ref="chart"
    class="chart-content"
    :class="commonClassName"
  ></div>
</template>

<script>
import { CLICK, RESET } from '@/config/eventName'
import common from '@/template/mixins/common'
import chart from '@/template/mixins/chart'

import _ from 'lodash'

export default {
  mixins: [common, chart],
  variables: {
    $chartData(newV) {
      this.staticData = newV
      this[RESET](false)
    },
  },
  computed: {},
  watch: {},
  data() {
    return {}
  },
  mounted() {},
  created() {},
  methods: {
    getOption() {
      const option = this.getBaseOption()

      const { dimensions: [nameKey, valueKey], source } = option.dataset
      option.series[0].data = source.map(item => ({ name: item[nameKey], value: item[valueKey] }))
      return option
    },
    // 初始化监听事件
    initEvents() {
      //点击
      this.chartInstance.on('click', (e) => {
        this.dotData = e.data
        this._emitEvent(CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.event.event.stopPropagation()
        }
      })
    },
    getTypeSetting() {
      const {
        type,
        grid,
        startAngle,
        clockwise,
        scale,
        selectedMode,
        ySetting: {
          title,
          labelLine
        }
      } = this
      const extra = {
        ...grid,
        name: title,
        roseType: ['radius', 'area'].includes(type) ? type : undefined,
        startAngle: startAngle && startAngle.value + startAngle.unit,
        clockwise: clockwise,
        emphasis: { scale },
        radius: this.getRadius(),
        selectedMode: selectedMode !== 'none' ? selectedMode : false,
        labelLine: {
          show: labelLine
        }
      }
      return extra
    },
    getRadius() {
      const {
        innerRadius: { value: innerVal, unit: innerUnit } = {
          value: 0,
          unit: '%',
        },
        outerRadius: { value: outerVal, unit: outerUnit } = {
          value: 75,
          unit: '%',
        },
      } = this
      return [innerVal + innerUnit, outerVal + outerUnit]
    },
  },
}
</script>

<style lang="less" scoped>
@import '~@/styles/index.less';

.chart-content {
  width: 100%;
  height: 400px;
}
</style>
