import { customAlphabet } from 'nanoid'
import _ from 'lodash'
import treeTool from 'tree-tool'
import { canDropInsideTypes, defaultKeys, dialogTypes, inlineTypes } from '@/config/widgets'
import { UPDATE_LAYOUT_DATA, INIT_DYNAMIC_PROPS, DELETE_LAYOUT_DATA } from '@/config/storeKey'
import { update } from '@/renderer/tools/history'
import EventBus, { globalEmit } from '@/renderer/tools/eventBus'
import { HIGH_LIGHT, PROPS } from '@/config/eventKey'
import { getGlobalConfig } from '@/renderer/tools/history'

export const getRandomHexadecimal = (size) =>
  customAlphabet('1234567890abcdef', size)() // 随机ID生成器

export const treeUtil = treeTool.createInstance({
  id: 'elementId',
  children: 'children',
  pid: 'parentId',
})

// 递归赋值组件ID
export function getResetIdWidget(widget) {
  const _tree = Array.isArray(widget) ? widget : [widget]
  const deepCopy = _.cloneDeep(_tree)
  const generateNewWidget = (tree) =>
    treeUtil.forEach(tree, (node) => {
      const type = node.elementType
      const id = getRandomHexadecimal(6)
      const oldProps = this.getStore().dynamicProps[node.elementId] || {}
      node.elementId = `${type}-${id}`
      if (oldProps.layout) {
        globalEmit.call(this, PROPS, { elementId: node.elementId, data: { layout: { ...oldProps.layout, i: node.elementId } }, pushHistory: false })
      }
    })
  generateNewWidget(deepCopy)
  return Array.isArray(deepCopy) ? deepCopy[0] : deepCopy
}

// 根据组件ID查找组件节点
export const getNodeByElementId = (tree, elementId) => {
  const _tree = Array.isArray(tree) ? tree : [tree]
  const realId = elementId === 'app' ? tree.elementId : elementId
  return treeUtil.findNode(_tree, (node) => node.elementId === realId)
}

// 根据组件ID查找组件父节点
export const getParentNodeByElementId = (tree, elementId) => {
  const _tree = Array.isArray(tree) ? tree : [tree]
  const parentNodeList =
    treeUtil.findPath(_tree, (node) => node.elementId === elementId) || []
  return parentNodeList[parentNodeList.length - 2]
}

// 获取所有父节点
export const getParentNodeListByElementId = (tree, elementId) => {
  const _tree = Array.isArray(tree) ? tree : [tree]
  return treeUtil.findPath(_tree, (node) => node.elementId === elementId)
}

// 判断tree是否包含target
export const isIncludes = (tree, targetId) => {
  const node = getNodeByElementId(tree, targetId)
  return !!node
}

// 根据组件ID删除组件节点
export function removeTreeNodeByElementId(tree, elementId) {
  const _tree = Array.isArray(tree) ? tree : [tree]
  treeUtil.removeNode(_tree, (node) => node.elementId === elementId)
}

export function insertElement({rootTree, targetId, placement, newNode, oldSelectedId, pushHistory = true}) {
  const oldStore = getGlobalConfig.call(this)
  const newNodeCopy = _.cloneDeep(newNode)
  const targetNode = treeUtil.findNode(
    [rootTree],
    (node) => node.elementId === targetId
  )
  const isMove = isIncludes(rootTree, newNode.elementId)
  if (isMove) {
    treeUtil.removeNode(
      [rootTree],
      (node) => node.elementId === newNode.elementId
    )
  }
  switch (placement) {
    case 'top':
    case 'left':
      treeUtil.insertBefore([rootTree], newNodeCopy, targetNode)
      break
    case 'bottom':
    case 'right':
      treeUtil.insertAfter([rootTree], targetNode, newNodeCopy)
      break
    case 'center':
      targetNode.children.push(newNodeCopy)
      break
  }

  this._storeCommit(UPDATE_LAYOUT_DATA, { layoutData: rootTree })
  if(pushHistory) {
    update({
      oldVal: {
        elementId: oldSelectedId,
      },
      val: {
        elementId: newNode.elementId
      },
      extra: {
        oldVal: oldStore,
        val: getGlobalConfig.call(this)
      },
      callback({ elementId }) {
        EventBus.$emit(HIGH_LIGHT, elementId)
      }
    })
  }
}

export function removeLayoutElement (elementId, layoutId) {
  const config = getGlobalConfig.call(this, 0)
  this._storeDispatch(DELETE_LAYOUT_DATA, { elementId })
  this.dndOptions.selectedId = layoutId
  update({
    oldVal : elementId,
    val: layoutId,
    extra: {
      oldVal: config,
      val: getGlobalConfig.call(this, 0),
      deep: true
    },
    callback: (id) => {
      this.dndOptions.selectedId =id
    }
  })
}

// 判断元素是否为非块级元素
export function isInlineElement(element) {
  if (!element) {
    return false
  }
  const style = getComputedStyle(element, null)
  return !['grid', 'block', 'flex'].includes(style.display)
}

export function isFlexElement(element) {
  if (!element) {
    return false
  }
  const style = getComputedStyle(element, null)
  return ['flex'].includes(style.display)
}

// 判断元素的子元素排布是否是行内排布
export function isRowElement(element) {
  if (!element) {
    return false
  }
  const style = getComputedStyle(element, null)
  return (
    style.display === 'flex' &&
    ['row', 'row-reverse'].includes(style.flexDirection)
  )
}

export function isInCanvas({x, y, currentDialog, hasTree}) {
  const canvas = currentDialog ? document.querySelector(`.component-dialog#${currentDialog.elementId}`) : document.querySelector('.page-canvas')
  const treeCanvas = hasTree ? document.querySelector('.drop-zone.widget-tab-pane') : undefined
  const {left: canvasLeft, top: canvasTop, width: canvasWidth, height: canvasHeight} = canvas.getBoundingClientRect()
  const outOfCanvas = x < canvasLeft || y < canvasTop || x > canvasLeft + canvasWidth || y > canvasTop + canvasHeight
  let outOfTreeCanvas = true
  if (treeCanvas) {
    const {left, top, width, height} = treeCanvas.getBoundingClientRect()
    outOfTreeCanvas = x < left || y < top || x > left + width || y > top + height
  }

  return !(outOfTreeCanvas && outOfCanvas)
}

// 获取落点
// todo 待优化 一开始逻辑很简单，需求改着改着就变复杂了。。。。
export function getDropTargets({
  layoutData, // 根组件树
  draggingWidget, // 当前拖动的组件
  currentDialog, // 当前激活的弹窗类型组件
  hasTree = false, // 是否包含左侧菜单组件树
  currentTimer, // 当前激活的定时器组件
}) {
  const dynamicProps = this.getStore().dynamicProps
  let query = ['.page-canvas .drop-zone']

  if (currentDialog) {
    query = [`.component-dialog#${currentDialog.elementId} .drop-zone`]
  }
  if (hasTree) {
    query.push('.tree-node.drop-zone')
  }

  // 弹窗类
  const isDraggingDialog = dialogTypes.includes(draggingWidget.elementType)
  const isDraggingTimer = draggingWidget.elementType === 'timer'

  if (isDraggingDialog || isDraggingTimer) {
    if (!draggingWidget.elementId) {
      const pageCanvasElement = document.querySelector('.page-canvas')
      const {left, top, width, height} = pageCanvasElement.getBoundingClientRect()
      return [{x: left + width / 2, y: top + height / 2}]
    } else {
      return []
    }
  }

  if (currentTimer && !isDraggingTimer && !isDraggingDialog) {
    return []
  }

  const dropZones = []
  query.forEach(item => {
    const res = Array.prototype.slice.call(document.querySelectorAll(item))
    dropZones.push(...res)
  })
  const draggingElement = document.querySelector(`[data-zone="${draggingWidget.elementId}_${draggingWidget.elementType}"]`)
  const targets = Array.prototype.slice.call(dropZones).map((item) => {
    if (!item.dataset.zone) {
      return []
    }
    if (item.classList.contains('no-drop')) {
      return []
    }
    if (draggingElement && draggingElement.contains(item) && item !== draggingElement) {
      return []
    }
    const parent = item.parentElement
    const {left: rLeft, top: rTop, width, height,} = item.getBoundingClientRect()
    const [id, type] = item.dataset.zone.split('_')
    const currentElement = getNodeByElementId(layoutData, id)
    const currentProps = dynamicProps[id] || {}
    const parentElement = getParentNodeByElementId(layoutData, id)
    const isInSlot = parentElement && ['slot'].includes(parentElement.elementType)
    const isInGridDnD = parentElement && ['grid-dnd'].includes(parentElement.elementType)
    const isInDynamicContainer = parentElement && ['dynamic-container'].includes(parentElement.elementType)
    const leftPosition = { x: rLeft + 1, y: rTop + height / 2 }
    const rightPosition = { x: rLeft + width - 1, y: rTop + height / 2 }
    const topPosition = { x: rLeft + width / 2, y: rTop + 1 }
    const bottomPosition = { x: rLeft + width / 2, y: rTop + height - 1 }
    const centerPosition = { x: rLeft + width / 2, y: rTop + height / 2 }
    const left = isInCanvas({ ...leftPosition, currentDialog, hasTree }) ? leftPosition : undefined
    const right = isInCanvas({ ...rightPosition, currentDialog, hasTree}) ? rightPosition : undefined
    const top = isInCanvas({ ...topPosition, currentDialog, hasTree}) ? topPosition : undefined
    const bottom = isInCanvas({ ...bottomPosition, currentDialog, hasTree}) ? bottomPosition : undefined
    const center = isInCanvas({ ...centerPosition , currentDialog, hasTree}) ? centerPosition : undefined
    const isInline = (isInlineElement(item) && inlineTypes.includes(draggingWidget.elementType)) || isRowElement(parent)

    const isChildOfDraggingElement = isIncludes(draggingWidget.children, id)
    if (isChildOfDraggingElement) {
      return []
    }

    // page v1
    switch (true) {
      case currentProps.canDropInsideTypes && !currentProps.canDropInsideTypes.includes(draggingWidget.elementType) && !['slot'].includes(currentElement.elementType): {
        if (isInGridDnD) {
          return [bottom]
        }
        return isInline ? [left, right] : [top, bottom]
      }
      case currentProps.ignoreDropInsideTypes && currentProps.ignoreDropInsideTypes.includes(draggingWidget.elementType) && !['slot'].includes(currentElement.elementType): {
        if (isInGridDnD) {
          return [bottom]
        }
        return isInline ? [left, right] : [top, bottom]
      }
      case isInSlot && dynamicProps[parentElement.elementId]?.canDropInsideTypes && !dynamicProps[parentElement.elementId]?.canDropInsideTypes.includes(draggingWidget.elementType): {
        return []
      }
      case isInDynamicContainer: {
        if (['layout', 'grid-dnd', 'dynamic-container'].includes(type) && !currentElement.children.filter((c) => dynamicProps[c.elementId]?.isVisible !== false).length) {
          return [center]
        }
        return []
      }
      case canDropInsideTypes.includes(type) && !currentElement.children.filter((c) => dynamicProps[c.elementId]?.isVisible !== false && !['dialog-container', 'timer-container'].includes(c.elementType)).length: {
        if (['layout'].includes(type)) {
          if ((['layout'].includes(draggingWidget.elementType) && ['layout-container', 'form-container'].includes(parentElement.elementType))) {
            return [left, right, center, top, bottom]
          }
          if (!['layout-container', 'form-container'].includes(parentElement.elementType)) {
            if (isInGridDnD) {
              return [center, bottom]
            }
            return isInline ? [left, center, right] : [top, center, bottom]
          }
          return [center]
        } else if (['grid-dnd'].includes(type)) {
          if (currentProps.isIgnoreDrag) {
            return [center]
          }
          if (isInGridDnD) {
            return [bottom, center]
          }
          return isInline ? [left, center, right] : [top, center, bottom]
        } else if (['dynamic-container'].includes(type)) {
          if (['dynamic-container'].includes(draggingWidget.elementType)) {
            if (isInGridDnD) {
              return [bottom]
            }
            return isInline ? [left, right] : [top, bottom]
          } else {
            if (isInGridDnD) {
              return [center, bottom]
            }
            return isInline ? [left, center, right] : [top, center, bottom]
          }
        } else {
          return [center]
        }
      }
      case canDropInsideTypes.includes(type) && currentElement.children.length && currentElement.children.some((c) => dynamicProps[c.elementId]?.isVisible !== false && !['dialog-container', 'timer-container'].includes(c.elementType)): {
        if (['layout'].includes(type)) {
          if ((['layout'].includes(draggingWidget.elementType) && ['layout-container', 'form-container'].includes(parentElement.elementType))) {
            return [left, right, top, bottom]
          }
          if (!['layout-container', 'form-container'].includes(parentElement.elementType)) {
            if (isInGridDnD) {
              return [bottom]
            }
            return isInline ? [left, right] : [top, bottom]
          }
          return []
        } else if (['grid-dnd', 'dynamic-container'].includes(type)) {
          if (type === 'grid-dnd' && currentProps.isIgnoreDrag) {
            return []
          }
          if (isInGridDnD) {
            return [bottom]
          }
          return isInline ? [left, right] : [top, bottom]
        } else if (['layout-container', 'form-container'].includes(type)) {
          return isInGridDnD ? [bottom] : [top, bottom]
        } else {
          return []
        }
      }
    }

    if (isInGridDnD) {
      return [bottom]
    }
    return isInline ? [left, right] : [top, bottom]
  })
  return targets.flat(Infinity).filter(t => t)
}

export function buildNewWidget(widget, targetId, placement) {
  const newWidget = getResetIdWidget.call(this, widget)
  const needInitProps = []
  treeUtil.forEach([newWidget], (source) => {
    const {children, ...node} = source
    const props = { ...node, ...(node.defaultProps || {}) }
    defaultKeys.forEach(key => {
      delete props[key]
    })
    needInitProps.push({ elementId: node.elementId, props: _.cloneDeep(props) })
    defaultKeys.forEach(key => {
      delete node[key]
    })
  })

  this._storeCommit(INIT_DYNAMIC_PROPS, {
    propsList: needInitProps,
  })

  insertElement.call(this, {
    rootTree: this.getStore().layoutData,
    targetId,
    placement,
    newNode: newWidget,
    pushHistory: false
  })
  return newWidget
}