<template>
  <grid-layout
    v-if="renderer"
    class="grid-dnd"
    :class="commonClassName"
    :style="gridStyle"
    :id="elementId"
    :data-zone="`${elementId}_${elementType}`"
    :layout="gridLayout"
    :col-num="colNum"
    :row-height="rowHeight"
    :max-rows="maxRows || undefined"
    :vertical-compact="verticalCompact"
    :prevent-collision="preventCollision"
    :margin="[columnGap, rowGap]"
    :use-css-transforms="false"
    :auto-size="false"
    @click.native="click"
  >
    <layout-placeholder
      class="full-holder"
      :elementId="elementId"
      :elementType="elementType"
      v-if="isEmpty"
    ></layout-placeholder>
    <template v-if="gridLayout.length">
      <grid-item
        class="grid-dnd-item"
        :class="{
          'is-selected': getIsSelected(item.i),
        }"
        v-for="item in gridLayout"
        :id="item.i"
        :x="item.x"
        :y="item.y"
        :w="item.w"
        :h="item.h"
        :i="item.i"
        :key="item.i"
        :is-draggable="isEditMode || draggable"
        :is-resizable="isEditMode || resizable"
        :static="!isEditMode && !draggable && !resizable"
        :dragIgnoreFrom="dragIgnoreFrom"
        :dragAllowFrom="dragAllowFrom"
        :resizeIgnoreFrom="resizeIgnoreFrom"
        @move="handleMove"
        @resize="handleResize"
        @resized="resizedEvent"
        @moved="movedEvent"
      >
        <component
          :key="getProps(item).elementId"
          :is="
            getElement(
              getProps(item).elementType,
              getProps(item).elementVersion
            )
          "
          :ref="getProps(item).elementId"
          :data-zone="
            `${getProps(item).elementId}_${getProps(item).elementType}`
          "
          v-bind="{ ...getLayout(item), slotProps }"
          :id="getProps(item).elementId"
        ></component>
      </grid-item>
    </template>
    <span
      class="ref-line v-line"
      v-for="(item, index) in vLine"
      :key="index"
      v-show="item.display"
      :style="{
        left: item.position,
        top: item.origin,
        height: item.lineLength,
      }"
    />
    <span
      class="ref-line h-line"
      v-for="(item, index) in hLine"
      :key="index"
      v-show="item.display"
      :style="{
        top: item.position,
        left: item.origin,
        width: item.lineLength,
      }"
    />
  </grid-layout>
</template>

<script>
  import VueGridLayout from 'vue-grid-layout'
  import { CLICK } from '@/config/eventName'
  import common from '@/template/mixins/common'
  import _ from 'lodash'
  import { globalEmit } from '@/renderer/tools/eventBus'
  import { PROPS } from '@/config/eventKey'
  import { update } from '@/renderer/tools/history'
  import { pageInject } from '@/renderer/tools/pageStore'

  export default {
    components: {
      GridLayout: VueGridLayout.GridLayout,
      GridItem: VueGridLayout.GridItem
    },
    mixins: [common],
    inject: ['dndOptions', ...pageInject],
    variables: {},
    data() {
      return {
        dragIgnoreFrom: '.drag-handler',
        dragAllowFrom: '',
        resizeIgnoreFrom: '',
        gridLayout: [],
        height: undefined,
        vLine: [],
        hLine: [],
        snapTolerance: 2,
        renderer: false,
      }
    },
    watch: {
      visibleChildren: {
        handler(n, o) {
          this.updateGridLayout()
        },
        immdiate: true,
        deep: false
      }
    },
    computed: {
      isEmpty() {
        return !this.visibleChildren.length && this.isEditMode
      },
      gridStyle() {
        return {
          ...this.componentStyle,
          height: this.height
        }
      }
    },
    mounted() {
      this.renderer = true
      if (!this.isEditMode) {
        this.dragIgnoreFrom = undefined
        this.dragAllowFrom = undefined
        this.resizeIgnoreFrom = undefined
      }
      this.updateGridLayout()
    },
    methods: {
      updateGridLayout() {
        const layouts = this.visibleChildren.map(child => this.getElementPropsById(child.elementId).layout)
        this.gridLayout = _.cloneDeep(layouts)
        this.$nextTick(() => {
          this.updateHeight()
        })
      },
      updateHeight() {
        if (this.componentStyle?.height) {
          this.height = this.componentStyle.height
          return
        }
        const parentElement = document.querySelector(`#${this.elementId}`)
        const {y} = parentElement.getBoundingClientRect()
        const parentY = y
        const childrenElements = document.querySelectorAll(`#${this.elementId} > .grid-dnd-item`)
        const rectList = Array.prototype.slice.call(childrenElements).map(item => item.getBoundingClientRect())
        const h = Math.max(...rectList.map(r => r.y + r.height), 0) - parentY + this.rowGap || 0
        this.height = `${h}px`
      },
      getIsSelected(i) {
        return i === this.dndOptions?.selectedId
      },
      resizedEvent(i, newH, newW, newHPx, newWPx, pushHistory = true) {
        const current = this.getElementPropsById(i)
        globalEmit.call(this, PROPS, { elementId: i, data: {layout: {...current.layout, h: newH, w: newW}}, pushHistory: false })
        if (pushHistory) {
          update({
            oldVal: [i, current.layout.h, current.layout.w],
            val: [i, newH, newW],
            callback: (value) => {
              this.resizedEvent(...value, undefined, undefined, false)
            }
          })
        }
        this.updateGridLayout()
        this.resetLine()
        setTimeout(() => {
          if (this.dndOptions) {
            this.dndOptions.selectedId = i
            this.dndOptions.dndResizing = false
          }
        })
      },
      movedEvent(i, newX, newY, newHPx, newWPx, pushHistory = true) {
        const current = this.getElementPropsById(i)
        globalEmit.call(this, PROPS, { elementId: i, data: {layout: {...current.layout, x: newX, y: newY}}, pushHistory: false })
        if (pushHistory) {
          update({
            oldVal: [i, current.layout.x, current.layout.y],
            val: [i, newX, newY],
            callback: (value) => {
              this.movedEvent(...value, undefined, undefined, false)
            }
          })
        }
        this.updateGridLayout()
        this.resetLine()
        setTimeout(() => {
          if (this.dndOptions) {
            this.dndOptions.selectedId = i
            this.dndOptions.dndDragging = false
          }
        })
      },
      getProps(data) {
        return this.getElementPropsById(data.i)
      },
      getLayout(data) {
        return this.visibleChildren.find(item => item.elementId === data.i)
      },
      click(e) {
        if (this.isEditMode) {
          return
        }
        this._emitEvent(CLICK)
        if(!this.isEditMode && this.stopPropagation) {
          e.stopPropagation()
        }
      },
      handleResize: _.throttle(function (i) {
        if (this.dndOptions) {
          this.dndOptions.dndResizing = true
        }
        const currentDom = document.querySelector(`#${this.elementId} > .vue-grid-placeholder`)
        const { left, top, width, height } = currentDom.style
        this.snapCheck({
          i,
          w: parseInt(width),
          h: parseInt(height),
          x: parseInt(left),
          y: parseInt(top),
        })
      }, 40, {leading: false, trailing: true}),
      handleMove: _.throttle(function (i, newX, newY) {
        if (this.dndOptions) {
          this.dndOptions.dndDragging = true
        }
        const currentDom = document.querySelector(`#${this.elementId} > .vue-grid-placeholder`)
        const { left, top, width, height } = currentDom.style
        this.snapCheck({
          i,
          w: parseInt(width),
          h: parseInt(height),
          x: parseInt(left),
          y: parseInt(top),
        })
      }, 40, {leading: false, trailing: true}),
      snapCheck({ i, w, h, x, y }) {
        let width = w
        let height = h
        if (this.snap) {
          let activeLeft = x
          let activeRight = x + w
          let activeTop = y
          let activeBottom = y + h

          // 初始化辅助线数据
          const temArr = new Array(3).fill({ display: false, position: '', origin: '', lineLength: '' })
          const refLine = { vLine: [], hLine: [] }
          for (let i in refLine) { refLine[i] = JSON.parse(JSON.stringify(temArr)) }

          // 获取当前父节点下所有子节点
          const nodes = document.querySelectorAll(`#${this.elementId} > .grid-dnd-item`)

          let tem = {
            value: { x: [[], [], []], y: [[], [], []] },
            display: [],
            position: []
          }
          for (let item of nodes) {
            if (item.id !== i) {
              const w = parseInt(item.style.width)
              const h = parseInt(item.style.height)
              const l = parseInt(item.style.left)
              const t = parseInt(item.style.top)
              const r = l + w // 对齐目标right
              const b = t + h // 对齐目标的bottom

              const hc = Math.abs((activeTop + height / 2) - (t + h / 2)) <= this.snapTolerance // 水平中线
              const vc = Math.abs((activeLeft + width / 2) - (l + w / 2)) <= this.snapTolerance // 垂直中线

              const ts = Math.abs(t - activeBottom) <= this.snapTolerance // 从上到下
              const TS = Math.abs(b - activeBottom) <= this.snapTolerance // 从上到下
              const bs = Math.abs(t - activeTop) <= this.snapTolerance // 从下到上
              const BS = Math.abs(b - activeTop) <= this.snapTolerance // 从下到上

              const ls = Math.abs(l - activeRight) <= this.snapTolerance // 外左
              const LS = Math.abs(r - activeRight) <= this.snapTolerance // 外左
              const rs = Math.abs(l - activeLeft) <= this.snapTolerance // 外右
              const RS = Math.abs(r - activeLeft) <= this.snapTolerance // 外右

              tem['display'] = [ts, TS, bs, BS, hc, hc, ls, LS, rs, RS, vc, vc]
              tem['position'] = [t, b, t, b, t + h / 2, t + h / 2, l, r, l, r, l + w / 2, l + w / 2]

              if (ts) {
                tem.value.y[0].push(l, r, activeLeft, activeRight)
              }
              if (bs) {
                tem.value.y[0].push(l, r, activeLeft, activeRight)
              }
              if (TS) {
                tem.value.y[1].push(l, r, activeLeft, activeRight)
              }
              if (BS) {
                tem.value.y[1].push(l, r, activeLeft, activeRight)
              }

              if (ls) {
                tem.value.x[0].push(t, b, activeTop, activeBottom)
              }
              if (rs) {
                tem.value.x[0].push(t, b, activeTop, activeBottom)
              }
              if (LS) {
                tem.value.x[1].push(t, b, activeTop, activeBottom)
              }
              if (RS) {
                tem.value.x[1].push(t, b, activeTop, activeBottom)
              }

              if (hc) {
                tem.value.y[2].push(l, r, activeLeft, activeRight)
              }
              if (vc) {
                tem.value.x[2].push(t, b, activeTop, activeBottom)
              }
              // 辅助线坐标与是否显示(display)对应的数组,易于循环遍历
              const arrTem = [0, 1, 0, 1, 2, 2, 0, 1, 0, 1, 2, 2]
              for (let i = 0; i <= arrTem.length; i++) {
                // 前6为Y辅助线,后6为X辅助线
                const xory = i < 6 ? 'y' : 'x'
                const horv = i < 6 ? 'hLine' : 'vLine'
                if (tem.display[i]) {
                  const { origin, length } = this.calcLineValues(tem.value[xory][arrTem[i]])
                  refLine[horv][arrTem[i]].display = tem.display[i]
                  refLine[horv][arrTem[i]].position = tem.position[i] + 'px'
                  refLine[horv][arrTem[i]].origin = origin
                  refLine[horv][arrTem[i]].lineLength = length
                }
              }
            }
          }
          const { vLine, hLine } = refLine
          this.vLine = vLine
          this.hLine = hLine
        }
      },
      calcLineValues (arr) {
        const length = Math.max(...arr) - Math.min(...arr) + 'px'
        const origin = Math.min(...arr) + 'px'
        return { length, origin }
      },
      resetLine() {
        // 初始化辅助线数据
        const temArr = new Array(3).fill({ display: false, position: '', origin: '', lineLength: '' })
        const refLine = { vLine: [], hLine: [] }
        for (let i in refLine) { refLine[i] = JSON.parse(JSON.stringify(temArr)) }
        const { vLine, hLine } = refLine
        this.vLine = vLine
        this.hLine = hLine
      }
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .grid-dnd::v-deep {
    transition: none;
    min-height: 60px;
    position: relative;
    .full-holder {
      position: absolute;
      top: 0;
      left: 0;
      bottom: 0;
      right: 0;
    }
    .grid-dnd-item {
      transition: none;
      touch-action: none;
      & > .layout-placeholder {
        touch-action: none;
      }

      & > .low-code-component {
        width: 100% !important;
        height: 100% !important;
        & > .layout-placeholder {
          touch-action: none;
        }
      }

      & > a {
        display: inline-block;
      }
    }
    &.is-edit-mode {
      // transition: height .2s ease;
      outline: 1px dashed #eeeeee;

      & > .grid-dnd-item {
        // transition: all .2s ease;
        touch-action: none;
        outline: 1px dashed #eeeeee;
        &.is-selected > span.vue-resizable-handle {
            display: block;
          }
        & > span.vue-resizable-handle {
          display: none;
          width: 20px;
          height: 20px;
          background-color: @drop-main-color;
        }
      }
    }
  }
</style>
