<template>
  <n-form-item
    v-show="state != 'hidden'"
    ref="ElFormItem"
    class="checkbox-group-widget"
    :class='commonClassName'
    :style='componentStyle'
    :label="showLabel ? label : ''"
    :prop="prop"
    :label-width="labelWidth"
    :rules="rules"
    @click.native="handleClickFormItem"
  >
    <div class="radio-container">
      <el-checkbox-group
        v-load-sync="fetchLoading"
        v-model="value"
        :disabled="state == 'disabled'"
        :size="size"
        :class="{
          'vertical-cont': sortMode == 'vertical',
        }"
        @change="handleChange"
        v-bind="groupBind"
      >
        <template v-if="!isButton">
          <el-checkbox
            :label="item.value"
            v-for="item in realOptions"
            :key="item.value"
            :disabled="item.disabled"
            :border="border"
            >{{ item.label }}</el-checkbox
          >
        </template>
        <el-checkbox-button
          class="checkbox-button"
          v-else
          :label="item.value"
          v-for="item in realOptions"
          :key="item.value"
          :disabled="item.disabled"
          >
            <i v-if="item.icon && iconPosition === 'left'" :class="item.icon" style="margin-right: 3px;"></i>
            {{ item.label }}
            <i v-if="item.icon && iconPosition === 'right'" :class="item.icon" style="margin-left: 3px;"></i>
          </el-checkbox-button
        >
      </el-checkbox-group>
    </div>
    <div v-if="bottomTips !== ''" class="bottom-tips">
      {{ bottomTips }}
    </div>
  </n-form-item>
</template>

<script>
  // 带m的属性代表是动态可变的
  // 不带m的则代表为静态
  import common from '@/template/mixins/common'
  import form from '@/template/mixins/form'
  import options from '@/template/mixins/options'
  import { CHANGE } from '@/config/eventName'
  import { isValidJsonString } from '@/utils/validateUtils'
  export default {
    mixins: [common, form, options],

    variables: {
      // 重载变量赋值方法
      $value(newV) {
        if (isValidJsonString(newV)) {
          try {
            const res = JSON.parse(newV)
            if (Array.isArray(res)) {
              this.value = res
            } else {
              this.value = []
            }
          } catch (e) {
            console.log(e)
            this.value = []
          }
        }
      }
    },
    computed: {
      rules() {
        const { required, regExp, label } = this
        const res = []
        if (required) {
          res.push(this.getTrueRule({
            required: true,
            message: `请选择${label}`,
            trigger: 'blur',
            type: 'array'
          }))
        }
        return regExp?.reduce((prev, cur) => {
          return prev.concat({
            pattern: cur.reg,
            message: cur.errorTips,
          })
        }, res)
      },
      groupBind() {
        const opt = {}
        if(this.max > 0) {
          opt.max = this.max
        }
        return opt
      }
    },
    watch: {},
    methods: {
      receiveValue(value) {
        // 初始化值是undefined的情况
        if(value === undefined) {
          this.value = []
        } else if(value instanceof Array) {
          this.value = value.filter(value => value)
        }
        this.update2form(this.value)
      },
      async handleChange(value) {
        await this.$nextTick()
        // 触发formItem的change校验
        this.dispatchFormItem(this.$refs.ElFormItem, 'el.form.blur', value)
        this._emitEvent(CHANGE)
      },
      initVal() {
        const tempValue = []
        this.realOptions.some((item) => {
          if (item.isDefault) {
            tempValue.push(item.value)
          }
        })
        this.value = tempValue
      }
    },
    mounted() {
      this.initVal()
    },
  }
</script>

<style lang="less" scoped>
  @import '~@/styles/index.less';

  .checkbox-group-widget::v-deep {
    .vertical-cont {
      display: flex;
      flex-direction: column;
      .el-radio {
        margin-bottom: 10px;
        &:last-child {
          margin-bottom: 0;
        }
      }
      .el-checkbox + .el-checkbox {
        margin-left: 0;
      }
    }
    .checkbox-button {
      .el-checkbox-button__inner [class*=el-icon-] {
        line-height: 0.9;
      }
      .el-checkbox-button__inner .low_code {
        line-height: 0.9;
      }
      &:nth-last-child(2) {
        .el-checkbox-button__inner {
          border-radius: 0 4px 4px 0 !important;
        }
      }
    }
    .bottom-tips {
      color: #aaa;
      font-size: 12px;
      line-height: 1;
      padding-top: 4px;
    }
  }
</style>
