/**
 * 获取文件base64编码
 *
 * @param {File} file
 * @param {string} format  指定文件格式
 * @param {number} size  指定文件大小(字节)
 * @param {string} formatMsg 格式错误提示
 * @param {string} sizeMsg   大小超出限制提示
 * @returns {Promise<any>}
 */
export function fileToBase64String({
  file,
  format = ['jpg', 'jpeg', 'png', 'gif', 'bmp'],
  size = 20 * 1024 * 1024,
  formatMsg = '文件格式不正确',
  sizeMsg = '文件大小超出限制'
}) {
  return new Promise((resolve, reject) => {
    // 格式过滤
    let suffix = file.type.split('/')[1].toLowerCase()
    let inFormat = false
    for (let i = 0; i < format.length; i++) {
      if (suffix === format[i]) {
        inFormat = true
        break
      }
    }
    if (!inFormat) {
      reject(formatMsg)
    }
    // 大小过滤
    if (size && file.size > size) {
      reject(sizeMsg)
    }
    // 转base64字符串
    let fileReader = new FileReader()
    fileReader.readAsDataURL(file)
    fileReader.onload = () => {
      let res = fileReader.result
      resolve({
        base64String: res,
        suffix: suffix,
      })
      reject('异常文件，请重新选择')
    }
  })
}

/**
 * base64转File
 *
 * @param {base64} base64
 * @param {string} filename 转换后的文件名
 * @returns {File}
 */
export const base64ToFile = (base64, filename) => {
  let arr = base64.split(',')
  let mime = arr[0].match(/:(.*?);/)[1]
  let suffix = mime.split('/')[1] // 图片后缀
  let bstr = atob(arr[1])
  let n = bstr.length
  let u8arr = new Uint8Array(n)
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n)
  }
  return new File([u8arr], `${filename}.${suffix}`, {
    type: mime,
  })
}

/**
 * base64转Blob
 *
 * @param {base64} base64
 * @returns {Blob}
 */
export const base64ToBlob = (base64) => {
  let arr = base64.split(','),
    mime = arr[0].match(/:(.*?);/)[1],
    bstr = atob(arr[1]),
    n = bstr.length,
    u8arr = new Uint8Array(n)
  while (n--) {
    u8arr[n] = bstr.charCodeAt(n)
  }
  return new Blob([u8arr], {
    type: mime,
  })
}

/**
 * 将图片转换为base64
 *
 * @param {string} url 图片链接
 * @return {promise} 返回图片base64
 */
export function imageToBase64(url) {
  if (!url) {
    return Promise.reject(`传入url不合法 : ${url}`)
  }
  return new Promise((resolve, reject) => {
    const img = document.createElement('img')
    let dataURL = ''
    img.crossOrigin = ''
    img.src = url
    img.onload = () => {
      var canvas = document.createElement('canvas')
      canvas.width = img.width
      canvas.height = img.height
      var ctx = canvas.getContext('2d')

      // 将canvas的透明背景设置成白色
      const imageData = ctx.getImageData(0, 0, canvas.width, canvas.height)
      for (var i = 0; i < imageData.data.length; i += 4) {
        // 当该像素是透明的，则设置成白色
        if (imageData.data[i + 3] == 0) {
          imageData.data[i] = 255
          imageData.data[i + 1] = 255
          imageData.data[i + 2] = 255
          imageData.data[i + 3] = 255
        }
      }
      ctx.putImageData(imageData, 0, 0)

      ctx.drawImage(img, 0, 0, img.width, img.height)
      try {
        dataURL = canvas.toDataURL('image/jpeg')
        resolve(dataURL)
      } catch (err) {
        console.error(err)
        reject()
      }
    }
    img.onerror = () => {
      reject(`图片加载失败！！[${url}]`)
    }
  })
}
